<template>
  <div class="float" id="box">
    <transition name="fade">
      <div>
        <el-container>
          <el-header class="bl">
            <h1>项目现状交通量</h1>
            <div class="next"></div>
          </el-header>
          <el-main class="bl">
            <div>
              <el-scrollbar v-show="showContent" class="pmain">
                <p>
                  根据2021年高速公路联网收费数据，本项目全线平均汽车交通量为78890
                  pcu/d.
                </p>
                <p>各路段交通量详情如下图所示：</p>
                <div id="chart2-1-1"></div>
                <p>
                  根据2021年高速公路联网收费数据，本项目主线客车交通量占71.2%，货车交通量占19.8%。
                </p>
                <div id="chart2-1-2"></div>
                <div id="chart2-1-3"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data: function () {
    return {
      chart1: null,
      chart2: null,
      chart3: null,
      showContent: true,
    };
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart2-1-1"));
      var option = {
        title: {
          text: "G35济广高速交通量分布情况",
          left: "center",
          textStyle:{
            color:"#fff",
            fontSize:16
          }
        },
        yAxis: {
          type: "category",
          inverse: true,
          name: "路段",
          nameTextStyle:{
            color:"#fff",
            fontSize:12
          },
          data: [
            "零点立交～济南北立交",
            "济南北立交～天桥立交",
            "天桥立交～槐荫枢纽",
            "全线平均",
          ],
          axisTick: {
            show: false,
          },
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 13.5,
            },
            showMaxLabel: true,
            interval: 0,
          },
          nameLocation: "start",
        },
        grid: {
          x: 150,
          y: 55,
          x2: 50,
          y2: 40,
        },
        xAxis: {
          type: "value",
          nameLocation: "middle",
          name: "2021年交通量（Pcu/d）",
          nameTextStyle: {
            color: "#fff",
            padding: [10, 0],
          },
          axisTick: {
            show: true,
          },
          axisLine: {
            show: true,
          },
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 12,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        series: [
          {
            type: "bar",
            label: {
              show: true,
              position: "inside",
              color: "azure",
              formatter: function (params) {
                return params.value + " pcu/d";
              },
            },
            itemStyle: {
              color: "#3397FF",
            },
            barWidth: "40%",
            data: [98193, 77453, 68417, 78890],
          },
        ],
      };
      this.chart1.setOption(option);
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart2-1-2"));
      var option = {
        title: {
          text: "项目主线车型占比",
          textStyle:{
            color:"#fff",
            fontSize:16
          },
          left: "center",
        },

        tooltip: {
          trigger: "item",
          formatter: function (params) {
            return (
              params.marker +
              "  " +
              params.name +
              "  " +
              (params.value * 100).toFixed(1) +
              "%"
            );
          },
        },
        legend: {
          top: "bottom",
          textStyle: {
            color: "#fff",
          },
        },
        series: [
          {
            name: "车型占比",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: true,
              textStyle: {
                color:"#fff",
                fontSize: 14,
              },
              formatter: function (params) {
                return (
                  params.name + " " + (params.value * 100).toFixed(1) + "%"
                );
              },
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "18",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: true,
            },
            data: [
              { value: 0.698, name: "小客车" },
              { value: 0.014, name: "大客车" },
              { value: 0.107, name: "小货车" },
              { value: 0.05, name: "中货车" },
              { value: 0.042, name: "大货车" },
              { value: 0.09, name: "汽车列车" },
            ],
          },
        ],
      };
      this.chart2.setOption(option);
    },
    initChart3() {
      this.chart3 = echarts.init(document.getElementById("chart2-1-3"));
      var option = {
        tooltip: {
          trigger: "item",
          show: true,
        },
        grid: {
          x: 80,
          x2: 80,
        },
        title: {
          text: "G35济南北观测站历年交通量",
          textStyle:{
            color:"#fff",
            fontSize:16
          },
          left: "center",
        },
        xAxis: {
          type: "category",
          data: [
            "2014",
            "2015",
            "2016",
            "2017",
            "2018",
            "2019",
            "2020",
            "2021",
          ],
          axisLabel:{
            textStyle:{
              color:"#fff",
              fontSize:12
            }
          },
          axisLine: {
            show: true,
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          name: "交通量 pcu/d",
          nameTextStyle:{
            color:"#fff",
            fontSize:12
          },
          axisLine: {
            show: true,
          },
          axisTick: {
            show: true,
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: "#fff",
              fontSize: 12,
            },
            interval: 0,
            showMaxLabel: true,
          },
        },
        series: {
          type: "bar",
          data: [66673, 72990, 83050, 80708, 0, 0, 0, 98193],
          barWidth: "50%",
          label: {
            show: true,
            position: "top",
            color: "azure",
            formatter: function (params) {
              if (params.value == 0) {
                return "";
              }
              return params.value;
            },
          },
        },
      };
      this.chart3.setOption(option);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style["max-height"] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style["max-height"] = "90px";
    },
  },
  mounted() {
    this.initChart1();
    this.initChart2();
    this.initChart3();
  },
  beforeUnmount() {
    if (!this.chart1 && !this.chart2 && !this.chart3) {
      return;
    }
    window.removeEventListener("resize", this.__resizeHandler);
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
    this.chart3.dispose();
    this.chart3 = null;
  },
};
</script>

<style scoped>
#chart2-1-1 {
  width: calc(100%-20px);
  height: 320px;
}

#chart2-1-2 {
  width: calc(100%-20px);
  height: 320px;
}
#chart2-1-3 {
  width: calc(100%-20px);
  height: 320px;
}

#box {
  max-height: 730px;
}
.pmain {
  height: 625px;
}


div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
</style>