<template>
  <div class="float" id="box">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>全省高速公路概况</h1>
            <div class="next">
              <el-button @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent" class="pmain">
              <el-scrollbar>
                <p>
                  截至2022年底山东省高速公路里程在全国省级行政区中排名第7位。
                </p>

                <div id="chart1-1-1"></div>
                <!-- <div id="chart1-1-2"></div> -->

                <!-- <div id="chart12"></div> -->
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { markRaw } from "vue";

export default {
  name: "panel1-1",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,

      chart2: null,
    };
  },
  props: ["showPC"],
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart1-1-1"));
      var highwayTable = require("../assets/data/1-1-1.json");

      var city = [];
      var km = [];
      var rank = [];
      highwayTable.forEach((item, index) => {
        city.push(item["name"]);
        if (item["name"] == "山东") {
          km.push({ value: item["value"], itemStyle: { color: "#24FFC5" } });
        } else {
          km.push(item["value"]);
        }
        rank.push(index + 1);
      });

      var option = {
        tooltip: {
          show: true,
          confine: true,
        },
        grid: {
          left: 75,
          bottom: 50,
        },
        title: {
          text: "全国各省级行政区高速公路里程(公里)",
          subtext: "(不包含港澳台地区)",
          left: "center",
          textStyle: { fontSize: 15, color: "azure" },
          subtextStyle: { fontSize: 12, color: "azure" },
        },
        yAxis: {
          type: "category",
          data: city,
          inverse: true,
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 15,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        xAxis: [
          {
            type: "value",
            name: "高速公路里程(公里)",
            nameTextStyle: {
              fontSize: 15,
              padding : [20,0],
              color: "#fff",
            },
            position: "bottom",
            alignTicks: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 15,
              },
              //不显示最后一个坐标标签
              //   showMaxLabel: true,
              //   interval: 0,
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "grey",
                type: "dashed",
              },
            },
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [5, 0],
            },
            axisTick: {
              show: true,
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "高速公路里程",
            data: km,
            barWidth: "35%",
            itemStyle: {
              color: "#5CF4F8",
            },
            label: {
              show: true,
              position: "right",
              formatter: function (params) {
                return params.value + " km";
              },
              color: "azure",
              fontSize: 15,
            },
            markLine: {
              lineStyle: {
                color: "grey",
              },
              label: {
                position: "start",
                formatter: "",
                textStyle: {
                  color: "grey",
                },
              },
              symbol:['arrow','circle'],
              data: [{ type: "average", name: "平均值" }],
            },
          },
        ],
      };
      this.chart1.setOption(option);
    },
    initChart2() {
      this.chart1 = markRaw(
        echarts.init(document.getElementById("chart1-1-1"))
      );
      var table = require("../assets/data/1-1-1-new2.json");
      var city = [];
      var travel = [];
      var gdp = [];
      var population = [];
      var area = [];

      table.forEach((item) => {
        city.push(item.name);
        travel.push(item.kilo);
        gdp.push(item.gdp);
        population.push(item.pop);
        area.push(item.area)
      });
      var setLayerOption = ""

      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        grid: {
          left: 125,
          bottom: 50,
        },
        legend: {
          icon: "rect",
          show: true,
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        xAxis: {
          type: "value",
          name: "比重",
          nameLocation: "middle",
          nameTextStyle: {
            padding: [10, 0],
            color: "azure",
          },
          // max: 0.2,
          axisLabel: {
            color: "azure",
            formatter: function (value, index) {
              return value * 100 + "%";
            },
          },
        },
        yAxis: {
          type: "category",
          data: city,
          name: "城市",
          nameLocation: "start",
          nameTextStyle: {
            padding: [-10, -20],
            color: "azure",
          },
          inverse: true,
          splitArea:{
            show:true,
            interval:0,
            areaStyle:{
              color:["rgba(0,0,0,0.2)","rgba(0,0,0,0.1)"]
            }
          },
          axisLabel: {
            color: "azure",
          },
        },
        emphasis: {
          focus: "series",
        },
        color:["#5CF4F8","#24FFC5","#F97952","#ffff00"],
        series: [
          {
            name: "高速里程比重",
            type: "bar",
            data: travel,
          },
          {
            name: "GDP比重",
            type: "bar",
            data: gdp,
          },
          {
            name: "人口比重",
            type: "bar",
            data: population,
          },
          {
            name: "面积比重",
            type: "bar",
            data: area,
          },
        ],
      });
      this.chart1.on("legendselectchanged", (params)=>{
        this.chart1.setOption({
          legend: { selected: { [params.name]: true } },
        });
        var name = params.name;
        var optionName = "";
        if (name == "高速里程比重") {
          optionName = "travel";
        } else if (name == "GDP比重") {
          optionName = "gdp";
        } else if (name == "人口比重") {
          optionName = "pop";
        } else if (name == "面积比重") {
          optionName = "area";
        }
        setLayerOption = optionName
        table.sort(function (a, b) {
          return b[optionName] - a[optionName];
        });
        var city = [];
        var travel = [];
        var gdp = [];
        var population = [];
        table.forEach((item) => {
          city.push(item.name);
          travel.push(item.travel);
          gdp.push(item.gdp);
          population.push(item.pop);
        });
        this.chart1.setOption({
          yAxis: {
            data:city,
          },
          series: [
          {
            name: "高速里程比重",
            type: "bar",
            data: travel,
          },
          {
            name: "GDP比重",
            type: "bar",
            data: gdp,
          },
          {
            name: "人口比重",
            type: "bar",
            data: population,
          },
          {
            name: "面积比重",
            type: "bar",
            data: area,
          },
        ],
          
        });
        // this.$emit("changeTravelLayerOption",setLayerOption)
        // do something
      });
    },

    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[max - height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[max - height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "1-4");
        this.$emit("subTitle", "1-1");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "1-1");
        this.$emit("subTitle", "1-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      //   this.initChartgk2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  height: 625px;
}


#chart1-1-1 {
  width: calc(100%-20px);
  height: 750px;
}
#chart1-1-2 {
  width: calc(100%-20px);
  height: 280px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>