<template>
  <div>
    <el-scrollbar>
      <div v-if="!compare" id="echarts3"></div>
      <div v-if="compare" id="comparison-container">
        <div id="before" class="map"></div>
        <div id="after" class="map"></div>
      </div>
      <div class="footerBlue"></div>
      <div class="leftPanel">
        <el-scrollbar>
          <div v-if="subTitle == '1'">
            <leftPanel1
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '2'">
            <leftPanel2
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '3'">
            <leftPanel3
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '4'">
            <leftPanel4
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '6'">
            <left-panel-6
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == 'compare'">
            <compare-panel
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == 'project'">
            <project-panel
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
        </el-scrollbar>
      </div>
    </el-scrollbar>
  </div>
</template>

<script>
import "ol/ol.css";
// import Map from "ol/Map";
import * as echarts from "echarts";
import "echarts-gl";
// import EChartsLayer from "ol-echarts";
// import { Style, Stroke, Fill, Circle } from "ol/style";
// import TileLayer from "ol/layer/Tile";
// import TileWMS from "ol/source/TileWMS";
// import XYZ from "ol/source/XYZ";
import View from "ol/View";
import leftPanel3 from "./weifangPanel/leftPanel3.vue";
import leftPanel1 from "./weifangPanel/leftPanel1.vue";
import leftPanel2 from "./weifangPanel/leftPanel2.vue";
import leftPanel4 from "./weifangPanel/leftPanel4.vue";
import * as mapboxgl from "mapbox-gl";
import Compare from "mapbox-gl-compare";
import turf from "turf";
import LeftPanel6 from "./weifangPanel/leftPanel6.vue";
import ComparePanel from "./weifangPanel/comparePanel.vue";
import ProjectPanel from "./weifangPanel/projectPanel.vue";
window.mapboxgl = mapboxgl;
mapboxgl.Compare = Compare;
window.mapboxgl.Compare = Compare;

export default {
  name: "sdmap",
  props: ["tab"],
  data: function () {
    return {
      tabNum: 1,
      toolcoord: null,
      globalShowPanelContent: true,
      echartslayer: null,
      map: null,
      cacheMap: null,
      compare: false,
      chart: null,
      subTitle: "0",
      tollPopup: new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      }),
      changeLayerTimer: null,
    };
  },
  components: {
    leftPanel2,
    leftPanel1,
    leftPanel4,
    leftPanel3,
    LeftPanel6,
    ComparePanel,
    ProjectPanel,
  },
  methods: {
    changeshowcontent(val) {
      this.globalShowPanelContent = val;
      console.log(this.globalShowPanelContent);
    },
    initChart() {
      window.mapboxgl = mapboxgl;
      mapboxgl.Compare = Compare;
      window.mapboxgl.Compare = Compare;
      window.mapboxgl.accessToken =
        "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw";
      // this.chart = echarts.init(document.getElementById("echarts3"));
      this.map = new mapboxgl.Map({
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [119.152, 36.705],
        container: "echarts3",
        zoom: 13.6,
        bearing: -40.6,
        pitch: 65,
        // style: "mapbox://styles/zpeng2018/ckyth6z1u000814nxo5meg5x9",
        // style: mapStyle, // 本地地图样式
        // 复刻样式
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        // style: "mapbox://styles/zpeng2018/cl244evte003o14moq2obne81",
        // style: "mapbox://styles/zpeng2018/ckz27op9k000y15ousweylykr",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      });
      console.log("loaded?", this.map.loaded());
      console.log("isStyleLoaded()?", this.map.isStyleLoaded());
      let controls = this.map._controls;
      controls.forEach((control) => {
        // 去除logo控件
        if ("_updateLogo" in control) {
          this.map.removeControl(control);
        }

        // 去除attribute控件
        if ("_attribHTML" in control) {
          this.map.removeControl(control);
        }
      });
      this.map.getCanvas().style.cursor = "default";
      this.map.on("mousemove", this.lonLat);
      this.map.on("style.load", () => {
        this.map.addSource("shandong", {
          type: "geojson",
          data: require("../assets/data/shandong.json"),
        });
        this.map.addLayer({
          id: "shandongContour",
          type: "line",
          source: "shandong",
          paint: {
            "line-color": "#a9e1ff",
          },
        });
        this.map.addSource("china", {
          type: "geojson",
          data: require("../assets/data/china.json"),
        });
        this.map.addLayer({
          id: "chinaContour",
          type: "line",
          source: "china",
          paint: {
            "line-color": "#a9e1ff",
            "line-opacity": ["match", ["get", "name"], "山东省", 0, 1],
          },
        });
        this.map.addLayer({
          id: "chinaFill",
          type: "fill",
          source: "china",
          paint: {
            "fill-color": "#a9e1ff",
            "fill-opacity": ["match", ["get", "name"], "山东省", 0, 0],
          },
        });
        var shandong = require("../assets/data/shandong.json");
        this.map.addSource("sdCitiesSource", {
          type: "geojson",
          data: shandong,
        });
        var ytFillOpacity = ["match", ["get", "name"], "潍坊市", 0, 0.2];
        this.map.addLayer({
          id: "sd-cities-fill",
          type: "fill",
          source: "sdCitiesSource",
          paint: {
            "fill-opacity": ytFillOpacity,
            "fill-color": "#a9e1ff",
          },
          layout: {
            visibility: "visible",
          },
        });

        this.map.addSource("wfkakouSource", {
          type: "geojson",
          data: require("../assets/data/weifang/wfkakou.json"),
        });
        this.map.addLayer({
          id: "wfkakou",
          type: "circle",
          source: "wfkakouSource",
          layout: {
            visibility: "visible",
          },
          paint: {
            "circle-radius": 3.5,
            "circle-color": "#5CF4F8",
            "circle-opacity": 0.6,
          },
        });
        this.map.setLayoutProperty(
          "road-label-simple2",
          "visibility",
          "visible"
        );
        this.map.setLayoutProperty("road-simple2", "visibility", "visible");
        this.map.setLayoutProperty(
          "Weifang-buildings",
          "visibility",
          "visible"
        );
        this.map.setLayoutProperty("weifang-heatmap", "visibility", "none");
        this.map.setLayoutProperty("highway-volume", "visibility", "none");
        this.map.addSource("volumeSource", {
          type: "geojson",
          data: require("../assets/data/weifang/wfVolume.json"),
        });
        this.map.addLayer({
          id: "volume-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "#5CF4F8",
            "line-width": 2,
          },
        });
        this.map.setPaintProperty("volume-layer", "line-width", {
          property: "交通量",
          type: "exponential",
          stops: [
            [369, 1.5],
            [6390, 8],
          ],
        });
        this.map.setPaintProperty("volume-layer", "line-opacity", {
          property: "交通量",
          type: "exponential",
          stops: [
            [369, 0.8],
            [6390, 1],
          ],
        });
        this.map.addLayer({
          id: "ratio-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "#4d9b30",
            "line-width": 3.5,
          },
        });
        this.map.setPaintProperty("ratio-layer", "line-color", {
          property: "颜色",
          type: "exponential",
          stops: [
            [1, "#24FFC5"],
            [2, "#5CF4F8"],
            [3, "#F97952"],
          ],
        });

        var st = this.$route.query.tn;
        if (st != null) {
          this.subTitle = st;
        }
        if (this.subTitle == "0") {
          this.$router.push(this.$route.path + "?tn=" + "1");
          this.subTitle = "1";
        }
        if (st == "compare") {
          this.compare = true;
        }
        if (st != "1" || st != undefined) {
          this.map.setLayoutProperty("wfkakou", "visibility", "none");
          this.subTitle = st;
        }
      });
    },
    lonLat(e) {
      this.toolcoord = [e.lngLat.lng, e.lngLat.lat];
    },
    tollLayer() {
      this.map.setLayoutProperty("road-label-simple2", "visibility", "visible");
      this.map.setLayoutProperty("road-simple2", "visibility", "visible");
      // this.map.setLayoutProperty("weifang-heatmap", "visibility", "none");
      this.map.setLayoutProperty("wfkakou", "visibility", "visible");
      // this.map.setLayoutProperty("sd-cities-layer", "visibility", "visible");
      // this.map.setLayoutProperty("wms-test-layer", "visibility", "visible");
    },
    heatmapLayer() {
      this.map.setLayoutProperty("weifang-heatmap", "visibility", "visible");
    },
    volumeLayer() {
      // this.map.setLayoutProperty("weifang-heatmap", "visibility", "none");
      // this.map.setLayoutProperty("road-label-simple2", "visibility", "visible");
      // this.map.setLayoutProperty("road-simple2", "visibility", "none");
      // this.map.setLayoutProperty("wfkakou", "visibility", "none");
      // this.map.setLayoutProperty("sd-cities-layer", "visibility", "none");
      // this.map.setLayoutProperty("wms-test-layer", "visibility", "none");
      this.map.addSource("xiangmuRoad", {
        type: "geojson",
        data: require("../assets/data/weifang/xiangmuroad.json"),
      });
      this.map.addLayer({
        id: "xiangmu-layer",
        type: "line",
        source: "xiangmuRoad",
        layout: {
          "line-join": "round",
          "line-cap": "round",
          visibility: "visible",
        },
        paint: {
          "line-color": "orange",
          "line-width": 3,
        },
      });
      this.map.setPaintProperty("xiangmu-layer", "line-width", {
        property: "交通量",
        type: "exponential",
        stops: [
          [369, 3],
          [6390, 10],
        ],
      });

      this.map.setLayoutProperty("volume-layer", "visibility", "visible");

      this.map.on("mouseenter", "volume-layer", this.changeToPointer);
      this.map.on("mousemove", "volume-layer", this.volumePopup);
      this.map.on("mouseleave", "volume-layer", this.removePopup);
    },
    changeToPointer() {
      this.map.getCanvas().style.cursor = "pointer";
    },
    changeToDefault() {
      this.map.getCanvas().style.cursor = "default";
    },
    volumePopup(e) {
      this.changeToPointer();
      const coordinates = this.toolcoord;
      const volume = e.features[0].properties.交通量;
      const highwayName = e.features[0].properties.高速名称;
      var htmlPopup =
        '<div class="volumePopup"><p><b>' +
        highwayName +
        "</b></p><p><b>交通量:</b>  " +
        volume +
        " Pcu/h</p></div>";
      // Ensure that if the map is zoomed out such that multiple
      // copies of the feature are visible, the popup appears
      // over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }
      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },
    removePopup() {
      this.changeToDefault();
      this.tollPopup.remove();
    },
    removeVolumeLayer() {
      this.map.removeLayer("xiangmu-layer");
      this.map.removeSource("xiangmuRoad");
      this.map.setLayoutProperty("volume-layer", "visibility", "none");
      this.map.off("mouseenter", "volume-layer", this.changeToPointer);
      this.map.off("mousemove", "volume-layer", this.volumePopup);
      this.map.off("mouseleave", "volume-layer", this.removePopup);
    },
    colorLayer() {
      try {
        this.map.setLayoutProperty(
          "road-label-simple2",
          "visibility",
          "visible"
        );
        // this.map.setLayoutProperty("wms-test-layer", "visibility", "none");
        this.map.setLayoutProperty("volume-layer", "visibility", "none");
        this.map.setLayoutProperty("ratio-layer", "visibility", "visible");
        this.map.on("mouseenter", "ratio-layer", this.changeToPointer);
        this.map.on("mousemove", "ratio-layer", this.ratioPopup);
        this.map.on("mouseleave", "ratio-layer", this.removePopup);
      } catch (err) {}
    },
    ratioPopup(e) {
      const coordinates = this.toolcoord;
      const volume = e.features[0].properties.拥挤度.toFixed(2);
      const highwayName = e.features[0].properties.高速名称;
      var htmlPopup =
        '<div class="volumePopup"><p><b>' +
        highwayName +
        "</b></p><p><b>拥挤度:</b>  " +
        volume +
        " </p></div>";
      // Ensure that if the map is zoomed out such that multiple
      // copies of the feature are visible, the popup appears
      // over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }
      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },
    removeColorLayer() {
      this.map.setLayoutProperty("ratio-layer", "visibility", "none");
      this.map.on("mouseenter", "ratio-layer", this.changeToPointer);
      this.map.on("mousemove", "ratio-layer", this.ratioPopup);
      this.map.on("mouseleave", "ratio-layer", this.removePopup);
    },
    chuxingfenbuLayer() {
      try {
        var arcs = require("../assets/data/weifang/arcs.json");
        var nodes = require("../assets/data/weifang/nodes.json");
        var points = require("../assets/data/weifang/points.json");

        this.map.addSource("nodes", {
          type: "geojson",
          data: nodes,
        });

        this.map.addLayer({
          id: "nodes",
          source: "nodes",
          type: "circle",
          paint: {
            "circle-color": "#5CF4F8",
          },
        });

        this.map.setPaintProperty("nodes", "circle-radius", {
          property: "交通量",
          type: "exponential",
          stops: [
            [20000, 5],
            [60000, 20],
          ],
        });
        this.map.addLayer({
          id: "nodesText",
          source: "nodes",
          type: "symbol",
          layout: {
            "text-field": ["get", "地区"],
            "text-anchor": "bottom-left",
          },
          paint: {
            "text-color": "azure",
            "text-halo-width": 2,
            "text-halo-color": "#000",
          },
        });

        this.map.addSource("route", {
          type: "geojson",
          data: arcs,
        });
        this.map.addSource("point", {
          type: "geojson",
          data: points,
        });

        this.map.addLayer({
          id: "route",
          source: "route",
          type: "line",
          paint: {
            "line-width": 10,
            "line-blur": 3,
            "line-opacity": 0.4,

            "line-color": "#5CF4F8",
          },
        });

        this.map.addLayer({
          id: "route2",
          source: "route",
          type: "line",
          paint: {
            "line-width": 8,
            "line-blur": 3,
            "line-opacity": 0.4,

            "line-color": "#42AFF6",
          },
        });

        this.map.addLayer({
          id: "route3",
          source: "route",
          type: "line",
          paint: {
            "line-width": 3,
            "line-blur": 1,
            "line-opacity": 0.8,

            "line-color": "#5CF4F8",
          },
        });
        this.map.addLayer({
          id: "point",
          source: "point",
          type: "symbol",
          layout: {
            "icon-image": "dot-9",
            "icon-rotate": ["get", "bearing"],
            "icon-rotation-alignment": "map",
            "icon-allow-overlap": true,
            "icon-ignore-placement": true,
          },
        });
        this.map.setPaintProperty("route3", "line-width", {
          property: "交通量",
          type: "exponential",
          stops: [
            [63, 2.5],
            [20000, 6],
          ],
        });
        this.map.setPaintProperty("route2", "line-width", {
          property: "交通量",
          type: "exponential",
          stops: [
            [63, 5],
            [20000, 12],
          ],
        });
        this.map.setPaintProperty("route", "line-width", {
          property: "交通量",
          type: "exponential",
          stops: [
            [63, 5],
            [20000, 12],
          ],
        });
        this.map.setPaintProperty("route3", "line-opacity", {
          property: "交通量",
          type: "exponential",
          stops: [
            [63, 0.3],
            [20000, 0.8],
          ],
        });
        this.map.setPaintProperty("route2", "line-opacity", {
          property: "交通量",
          type: "exponential",
          stops: [
            [63, 0.1],
            [20000, 0.4],
          ],
        });
        this.map.setPaintProperty("route", "line-opacity", {
          property: "交通量",
          type: "exponential",
          stops: [
            [63, 0.1],
            [20000, 0.4],
          ],
        });
        console.log("fly");
        const easingFn = function (t) {
          return 1 - Math.pow(1 - t, 5);
        };
        const animationOptions = {
          duration: 2500,
          easing: easingFn,
          offset: [200, 0],
          animate: true,
          essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
          center: [119.1441, 36.72],
          zoom: 11,
          bearing: 0,
          pitch: 0,
        };
        this.map.flyTo(animationOptions);
        this.map.on("mouseenter", "route", this.changeToPointer);
        this.map.on("mouseenter", "nodes", this.changeToPointer);
        this.map.on("mousemove", "route", this.chuxingfenbuPopup);
        this.map.on("mousemove", "nodes", this.nodesPopup);
        this.map.on("mouseleave", "nodes", this.mouseleaveRoute);
        this.map.on("mouseleave", "route", this.mouseleaveRoute);
        this.animate(arcs, points);
      } catch (err) {}
    },
    removeChuxingfenbuLayer() {
      cancelAnimationFrame(this.animation);
      this.map.off("mousemove", "route", this.chuxingfenbuPopup);
      this.map.off("mouseenter", "route", this.changeToPointer);
      this.map.off("mouseleave", "route", this.mouseleaveRoute);
      this.map.off("mousemove", "node", this.nodesPopup);
      this.map.off("mouseenter", "nodes", this.changeToPointer);
      this.map.off("mouseleave", "nodes", this.mouseleaveRoute);
      this.animation = null;
      this.counter = 0;
      this.map.removeLayer("route");
      this.map.removeLayer("nodes");
      this.map.removeLayer("nodesText");
      this.map.removeSource("nodes");
      this.map.removeLayer("route2");
      this.map.removeLayer("route3");
      this.map.removeSource("route");
      this.map.removeLayer("point");
      this.map.removeSource("point");
      console.log("removed");
    },
    chuxingfenbuPopup(e) {
      this.map.getCanvas().style.cursor = "pointer";
      var coordinates = this.toolcoord;
      const fromName = e.features[0].properties.From;
      const toName = e.features[0].properties.To;

      const tollVolume = e.features[0].properties.交通量.toFixed(0);
      var htmlPopup =
        '<div class="volumePopup"><p><b>' +
        fromName +
        " - " +
        toName +
        "</b></p><p>交通量： " +
        tollVolume +
        " Pcu/h </p></div>";
      // Ensure that if the map is zoomed out such that multiple
      // copies of the feature are visible, the popup appears
      // over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }
      // console.log(tollPopup)
      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },
    nodesPopup(e) {
      // this.map.getCanvas().style.cursor = "pointer";
      var coordinates = this.toolcoord;
      const fromName = e.features[0].properties.地区;
      // const toName = e.features[0].properties.To;

      const tollVolume = e.features[0].properties.交通量.toFixed(0);
      var htmlPopup =
        '<div class="volumePopup"><p><b>' +
        fromName +
        "</b></p><p>交通量： " +
        tollVolume +
        " Pcu/h </p></div>";
      // Ensure that if the map is zoomed out such that multiple
      // copies of the feature are visible, the popup appears
      // over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }
      // console.log(tollPopup)
      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },
    mouseleaveRoute(e) {
      this.changeToDefault();
      this.tollPopup.remove();
    },

    xiangmuLayer() {
      console.log("fly");
      this.map.addSource("xiangmuRoad", {
        type: "geojson",
        data: require("../assets/data/weifang/xiangmuroad.json"),
      });
      this.map.addLayer({
        id: "xiangmu-layer",
        type: "line",
        source: "xiangmuRoad",
        layout: {
          "line-join": "round",
          "line-cap": "round",
          visibility: "visible",
        },
        paint: {
          "line-color": "#5CF4F8",
          "line-width": 3,
        },
      });
      this.map.setPaintProperty("xiangmu-layer", "line-width", {
        property: "车道数",
        type: "exponential",
        stops: [
          [1, 1],
          [10, 10],
        ],
      });
      console.log("fly");
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [200, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [119.1441, 36.72],
        zoom: 11,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    removeXiangmuLayer() {
      this.map.removeLayer("xiangmu-layer");
      this.map.removeSource("xiangmuRoad");
    },

    compareMap() {
      // this.cacheMap = this.map;
      var afterMap = new mapboxgl.Map({
        container: "after",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [119.1441, 36.72],
        zoom: 12,
        bearing: 0,
        pitch: 0,
        // style: "mapbox://styles/zpeng2018/ckz27op9k000y15ousweylykr",
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        // style: "mapbox://styles/zpeng2018/cl244evte003o14moq2obne81",
        // style: "mapbox://styles/zpeng2018/ckznsgj3p004p14qt7innzu0u",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      });
      var beforeMap = new mapboxgl.Map({
        container: "before",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [119.1441, 36.72],
        zoom: 12,
        bearing: 0,
        pitch: 0,
        // style: "mapbox://styles/zpeng2018/cl244evte003o14moq2obne81",
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        // style: "mapbox://styles/zpeng2018/ckz27op9k000y15ousweylykr",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      });

      mapboxgl.Compare = Compare;
      window.mapboxgl.Compare = Compare;

      const container = "#comparison-container";

      const map = new mapboxgl.Compare(beforeMap, afterMap, container, {
        // Set this to enable comparing two maps by mouse movement:
        // mousemove: true
      });
      afterMap.on("style.load", () => {
        afterMap.addSource("volumeSource", {
          type: "geojson",
          data: require("../assets/data/weifang/weifangroad.json"),
        });
        afterMap.addLayer({
          id: "volume-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "visible",
          },
          paint: {
            "line-color": "#5CF4F8",
            "line-width": 2,
          },
        });
        afterMap.setPaintProperty("volume-layer", "line-width", {
          property: "有项目交通量",
          type: "exponential",
          stops: [
            [369, 1.5],
            [10000, 8],
          ],
        });
        afterMap.setPaintProperty("volume-layer", "line-opacity", {
          property: "有项目交通量",
          type: "exponential",
          stops: [
            [369, 0.8],
            [10000, 1],
          ],
        });
        afterMap.setPaintProperty("volume-layer", "line-color", {
          property: "有项目颜色",
          type: "exponential",
          stops: [
          [1, "#24FFC5"],
            [2, "#5CF4F8"],
            [3, "#F97952"],
          ],
        });
      });
      beforeMap.on("style.load", () => {
        beforeMap.addSource("volumeSource", {
          type: "geojson",
          data: require("../assets/data/weifang/weifangroad.json"),
        });
        beforeMap.addLayer({
          id: "volume-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "visible",
          },
          paint: {
            "line-color": "#4d9b30",
            "line-width": 3.5,
          },
        });
        beforeMap.setPaintProperty("volume-layer", "line-width", {
          property: "无项目交通量",
          type: "exponential",
          stops: [
            [369, 1.5],
            [10000, 8],
          ],
        });
        beforeMap.setPaintProperty("volume-layer", "line-opacity", {
          property: "无项目交通量",
          type: "exponential",
          stops: [
            [369, 0.8],
            [10000, 1],
          ],
        });
        beforeMap.setPaintProperty("volume-layer", "line-color", {
          property: "无项目颜色",
          type: "exponential",
