      }

      if (oldV == "6") {
        this.removeChuxingfenbuLayer();

        // this.subTitle = 6;
      }

      if (val == "6") {
        this.chuxingfenbuLayer();

        // this.subTitle = 6;
      }
      if (val == "project") {
        this.xiangmuLayer();
      }
      if (val == "4") {
        this.colorLayer();

        // this.subTitle = 4;
      }
      if (val == "3") {
        this.volumeLayer();
        // this.subTitle = 3;
      }
      if (val == "2") {
        this.map.setLayoutProperty("weifang-heatmap", "visibility", "visible");
        // this.subTitle = 2;
      }
      if (val == "1") {
        this.map.setLayoutProperty("wfkakou", "visibility", "visible");
        // this.subTitle = 1;
      }
      if (val == "compare") {
        this.compare = true;
        // this.map.remove();
        setTimeout(() => {
          this.compareMap();
        }, 1000);
        // this.subTitle = "compare";
      }
    },
  },
  watch: {
    tab(val) {
      if (val == "compare") {
        this.compare = true;
      } else {
        this.compare = false;
      }
      this.subTitle = val;
    },
    async subTitle(val, oldV) {
      console.log("oldV", oldV, "newV", val);
      if (oldV == "compare") {
        await new Promise(async (resolve) => {
          {
            await this.reinit();
          }
          resolve("done");
        });
        let that = this;
        const changeSubtitle = function (oldV, val) {
          console.log("changeSubtitle", "old", oldV, "val", val);
          if (oldV == "1") {
            that.map.setLayoutProperty("wfkakou", "visibility", "none");
          }
          if (oldV == "2") {
            that.map.setLayoutProperty("weifang-heatmap", "visibility", "none");
          }
          if (oldV == "3") {
            that.removeVolumeLayer();
          }
          if (oldV == "4") {
            that.removeColorLayer();
          }

          if (oldV == "6") {
            that.removeChuxingfenbuLayer();

            // that.subTitle = 6;
          }
          if (oldV == "project") {
            that.removeXiangmuLayer();
            // this.map.setLayoutProperty("ratio-layer", "visibility", "none");
          }

          if (val == "project") {
            that.xiangmuLayer();
            // this.map.setLayoutProperty("ratio-layer", "visibility", "none");
          }

          if (val == "6") {
            that.chuxingfenbuLayer();

            // that.subTitle = 6;
          }
          if (val == "4") {
            that.colorLayer();

            // that.subTitle = 4;
          }
          if (val == "3") {
            that.volumeLayer();
            // that.subTitle = 3;
          }
          if (val == "2") {
            that.map.setLayoutProperty(
              "weifang-heatmap",
              "visibility",
              "visible"
            );
            // that.subTitle = 2;
          }
          if (val == "1") {
            that.map.setLayoutProperty("wfkakou", "visibility", "visible");
            // that.subTitle = 1;
          }
          if (val == "compare") {
            that.compare = true;
            // that.map.remove();
            setTimeout(() => {
              that.compareMap();
            }, 500);
            // that.subTitle = "compare";
          }
        };
        that.changeLayerTimer = setInterval(function () {
          if (that.map.isStyleLoaded() == true) {
            try {
              changeSubtitle(oldV, val);
              clearInterval(that.changeLayerTimer);
            } catch (err) {
              console.log(err);
            }
          }
        }, 50);
      } else {
        this.changeSubtitle(oldV, val);
      }
      this.$emit("st", this.subTitle);
    },
  },
  mounted() {
    // this.initMap(0);
    this.initChart();
    // this.tollLayer();
  },
  beforeUnmount() {
    this.map.remove();
    // console.log("beforeUnmount");
    if (!this.chart) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart.dispose();
    this.chart = null;
  },
};
</script>

