<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>山东省高速公路拥挤度分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button disabled=true @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  为详细分析山东省高速公路网路段拥挤情况，路段饱和度在0.5以下为<b
                    class="green"
                    >畅通状态</b
                  >饱和度在0.5和0.75之间为<b class="yellow">缓行状态</b
                  >，饱和度在0.75以上为<b class="red">拥挤状态</b>。
                </p>
                <p>
                  2021年山东省高速公路网平均拥挤度为0.39，其中，<b class="green"
                    >畅通路段</b
                  >占<b class="green">79%</b>；<b class="yellow">缓行路段</b
                  >占<b class="yellow">12%</b>；<b class="red">拥挤路段</b>占<b
                    class="red"
                    >9%</b
                  >。
                </p>
                <div id="chart41"></div>

                <div id="chart42"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["沈海高速", "日照至鲁苏界", 0.9],
        ["济广高速", "孔村枢纽至东平湖枢纽", 0.83],
        ["长深高速", "于家庄枢纽至临朐立交", 0.82],
        ["青银高速", "青岛东收费站至夏庄枢纽", 0.81],
        ["长深高速", "无棣至鲁冀界", 0.8],
        ["青银高速", "崔寨枢纽至机场枢纽", 0.79],
        ["济广高速", "定陶枢纽至鲁豫界", 0.77],
        ["德上高速", "聊城西枢纽至李集枢纽", 0.75],
      ],
    };
  },
  props: ["showPC"],

  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart41"));
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value * 100 +
              "%"
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        series: [
          {
            name: "路段占比",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 5,
              borderColor: "#fff",
              borderWidth: 1,
            },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              {
                value: 0.79,
                name: "畅通路段",
                itemStyle: { color: "#2b821d" },
              },
              {
                value: 0.12,
                name: "缓行路段",
                itemStyle: { color: "#e6b600" },
              },
              {
                value: 0.09,
                name: "拥挤路段",
                itemStyle: { color: "#c12e34" },
              },
            ],
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart42"));
      this.traffic_rank.reverse();
      var highways = [];
      var sections = [];
      var ratios = [];
      this.traffic_rank.forEach((element) => {
        highways.push(element[0]);
        sections.push(element[1]);
        ratios.push(element[2]);
      });

      this.chart2.setOption({
        title: {
          show: true,
          text: "全省高速公路主要拥堵路段",
          x: "center",
          y: "top",
          textStyle: {
            fontSize: 15,
            color: "azure",
          },
        },
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          show: false,
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 135,
          y: 25,
          x2: 20,
          y2: 25,
        },
        yAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "#C9DD23",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            data: sections,
          },
        ],
        xAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#C9DD23",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "平均拥堵度",
            type: "bar",
            smooth: true,
            barWidth: 10,
            data: ratios,
            itemStyle: {
              normal: {
                color: "#c12e34",
              },
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-4");
        this.$emit("subTitle", "2-4");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-2");
        this.$emit("subTitle", "2-2");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}

.bl b {
  color: #0098d9;
}


#chart41 {
  width: calc(100%-20px);
  height: 200px;
}
#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>