<template>
  <div>
    <el-scrollbar>
      <div id="mapin"></div>
      <div class="footerBlue"></div>
      <div class="leftPanel">
        <el-scrollbar>
          <jinan-panel-1 v-if="subTitle == 1" />
          <jinan-panel-2-1
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            v-if="subTitle == '2-1'"
          />
          <jinan-panel-2-2
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            v-if="subTitle == '2-2'"
          />
          <jinan-panel-2-3
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            v-if="subTitle == '2-3'"
          />
          <jinan-panel-2-4
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            v-if="subTitle == '2-4'"
          />
          <jinan-panel-3-1
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            v-if="subTitle == '3-1'"
          />
          <jinan-panel-3-2
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            :toll="tollName"
            @ratiolabel="changeVolumeByPanelRadio"
            v-if="subTitle == '3-2'"
          />
          <jinan-panel-4-1
            :showPC="globalShowPanelContent"
            @showContent="changeshowcontent"
            :toll="tollName"
            @ratiolabel="changeVolumeByPanelRadio"
            v-if="subTitle == '4-1'"
          />
          <jinan-panel-5-1 v-if="subTitle == '5-1'" />
        </el-scrollbar>
      </div>
    </el-scrollbar>
  </div>
</template>

<script>
import "ol/ol.css";
import "echarts-gl";
import * as mapboxgl from "mapbox-gl"; //引入组件
// import { ArrowDown } from "@element-plus/icons-vue";
import JinanPanel1 from "./jinanPanel/jinanPanel1.vue";
import JinanPanel21 from "./jinanPanel/jinanPanel2-1.vue";
import JinanPanel51 from "./jinanPanel/jinanPanel5-1.vue";
import JinanPanel32 from "./jinanPanel/jinanPanel3-2.vue";
import turf from "turf";
import JinanPanel22 from "./jinanPanel/jinanPanel2-2.vue";
import JinanPanel23 from "./jinanPanel/jinanPanel2-3.vue";
import JinanPanel24 from "./jinanPanel/jinanPanel2-4.vue";
import JinanPanel31 from "./jinanPanel/jinanPanel3-1.vue";
import JinanPanel41 from "./jinanPanel/jinanPanel4-1.vue";
import FileSaver from "file-saver";

export default {
  name: "testMap",
  props: ["tab"],
  data: function () {
    return {
      map: null,
      chart: null,
      globalShowPanelContent: true,
      subTitle: 1,
      tollPopup: new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      }),
      toolcoord: null,
      tollName: "济南",
    };
  },
  components: {
    JinanPanel1,
    JinanPanel21,
    JinanPanel32,
    JinanPanel51,
    JinanPanel22,
    JinanPanel23,
    JinanPanel24,
    JinanPanel31,
    JinanPanel41,
  },
  watch: {
    tab(val) {
      this.subTitle = val;
      // console.log("tab", val);
    },
    subTitle(val, oldV) {
      // console.log(val);
      switch (oldV) {
        case "1":
          this.removeGeolocation();
          break;
        case "2-1":
          this.removeVolumeLayer();
          break;
        case "2-2":
          this.removeVolumeLayer();
          break;
        case "2-3":
          this.removeVolumeLayer();
          break;
        case "2-4":
          this.removeVolumeLayer();
          break;
        case "3-1":
          this.removeProjectOrigin();
          break;
        case "3-2":
          this.removeSfzLayer();
          break;
        case "4-1":
          this.removeVolumeLayer();
          break;
        case "5-1":
          this.removeVolumeLayer();
          break;
        case "5":
          this.removeJinanRoadLayer();
          break;
      }
      switch (val) {
        case "1":
          this.geolocation();
          break;
        case "2-1":
          this.volumeLayer();
          this.flyToProject();
          break;
        case "2-2":
          this.volumeLayer();
          this.overpass1();
          break;
        case "2-3":
          this.volumeLayer();
          this.overpass2();
          break;
        case "2-4":
          this.volumeLayer();
          this.overpass3();
          break;
        case "3-1":
          this.projectOrigin();
          break;
        case "3-2":
          this.sfzLayer();
          break;
        case "4-1":
          this.volumeLayer();
          this.flyToProject();
          break;
        case "5-1":
          this.volumeLayer();
          this.flyToProject();
          break;
        case "5":
          this.jinanRoadLayer();
          break;
      }
      // this.subTitle = val;
      this.$emit("st", this.subTitle);
    },
  },
  methods: {
    changeshowcontent(val) {
      this.globalShowPanelContent = val;
    },
    initChart() {
      window.mapboxgl = mapboxgl;
      window.mapboxgl.accessToken =
        "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw";
      this.map = new mapboxgl.Map({
        container: "mapin",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [116.91, 36.7251],
        zoom: 12,
        // style: "mapbox://styles/zpeng2018/ckyth6z1u000814nxo5meg5x9",
        // style: "mapbox://styles/zpeng2018/ckznsgj3p004p14qt7innzu0u",
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        // style: "mapbox://styles/zpeng2018/cl1my9b1w001114t6e4igz3w8",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      });

      let controls = this.map._controls;
      controls.forEach((control) => {
        // 去除logo控件
        if ("_updateLogo" in control) {
          this.map.removeControl(control);
        }

        // 去除attribute控件
        if ("_attribHTML" in control) {
          this.map.removeControl(control);
        }
      });
      // this.map = this.chart.getModel().getComponent("mapbox3D").getMapbox();
      this.map.getCanvas().style.cursor = "default";
      this.map.on("load", () => {
        this.map.addSource("sfzSource", {
          type: "geojson",
          data: require("../assets/data/jinan/sfz.json"),
        });
        this.map.addSource("roadSource", {
          type: "geojson",
          data: require("../assets/data/jinan/jinanRoad.json"),
        });
        this.map.on("mousemove", this.lonLat);
        this.map.setLayoutProperty("road-icon-label", "visibility", "visible");
        // this.removeMotorWay();
        this.addJinanAllRoad();
        this.addKakou();
        // this.addJiyangKakou();
        // this.addLaiwuKakou();
        this.addJinanContour();
        this.addShandongContour();
        var st = this.$route.query.tn;
        console.log(st);
        if (st == undefined) {
          this.$router.push(this.$route.path + "?tn=" + "1");
        }
        this.subTitle = st;
      });
    },
    changeContent(id, name, volume) {
      var idobj = document.getElementById("id");
      var nameobj = document.getElementById("name");
      var volumeobj = document.getElementById("volume");
      console.log(idobj);
      idobj.innerHTML = id;
      nameobj.innerHTML = name;
      volumeobj.innerHTML = volume;
    },
    lonLat(e) {
      this.toolcoord = [e.lngLat.lng, e.lngLat.lat];
    },
    animate(arcs, points) {
      var routes = arcs.features;
      var ps = points.features;
      var steps = routes[0].geometry.coordinates.length - 1;

      routes.forEach((element, index) => {
        // console.log(element,index)
        const start =
          element.geometry.coordinates[
            this.counter >= steps ? this.counter - 1 : this.counter
          ];
        const end =
          element.geometry.coordinates[
            this.counter >= steps ? this.counter : this.counter + 1
          ];
        // console.log(start,end)
        if (!start || !end) return;
        ps[index].geometry.coordinates =
          routes[index].geometry.coordinates[this.counter];

        // Calculate the bearing to ensure the icon is rotated to match the route arc
        // The bearing is calculated between the current point and the next point, except
        // at the end of the arc, which uses the previous point and the current point
        ps[index].properties.bearing = turf.bearing(
          turf.point(start),
          turf.point(end)
        );
      });
      points.features = ps;

      this.map.getSource("point").setData(points);
      this.counter = this.counter + 1;
      // Request the next frame of animation as long as the end has not been reached
      if (this.counter < steps) {
        this.animation = requestAnimationFrame(() => {
          this.animate(arcs, points);
        });
      } else {
        this.counter = 0;
        this.animation = requestAnimationFrame(() => {
          this.animate(arcs, points);
        });
      }
    },
    overpass1() {
      var coordinates = [117.0363, 36.7275];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 1500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        offset: [200, 0],
        zoom: 14.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    overpass2() {
      var coordinates = [117.0153, 36.7167];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 1500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        offset: [200, 0],
        zoom: 14.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    overpass3() {
      var coordinates = [116.9252, 36.7204];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 1500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        offset: [200, 0],
        zoom: 14.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    overpass4() {
      var coordinates = [116.8612, 36.715];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        offset: [200, 0],
        zoom: 14.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    geolocation() {
      this.map.addLayer({
        id: "road-layer",
        type: "line",
        source: "roadSource",
        layout: {
          "line-join": "round",
          "line-cap": "round",
          visibility: "visible",
        },
        paint: {
          "line-color": "#5CF4F8",
          "line-width": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            0.75,
            18,
            28,
          ],
        },
      });
      var coordinates = [116.91, 36.7251];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 1500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        zoom: 12,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    removeGeolocation() {
      this.map.removeLayer("road-layer");
    },
    volumeLayer() {
      this.map.addLayer({
        id: "volume-layer",
        source: "roadSource",
        type: "line",
        layout: {
          "line-join": "round",
          "line-cap": "round",
          visibility: "visible",
        },
        paint: {
          "line-color": "#5CF4F8",
          "line-width": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            0.75,
            16,
            [
              "interpolate",
              ["linear"],
              ["get", "volume"],
              0,
              4,
              50000,
              16,
              100000,
              32,
            ],
          ],
        },
      });
      this.map.addLayer({
        id: "volume-label-layer",
        source: "roadSource",
        type: "symbol",
        layout: {
          "symbol-placement": "line-center",
          "text-size": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            8,
            6,
            12,
            18,
            14.5,
            22,
          ],
          "text-field": ["get", "volume2"],
        },
        paint: {
          "text-color": "#fff",
          "text-halo-width": 1,
          "text-halo-color": "black",
        },
      });

      this.map.on("mouseenter", "volume-layer", this.changeCursorToPointer);
      this.map.on("mousemove", "volume-layer", this.volumePopup);
      this.map.on("mouseleave", "volume-layer", this.leaveLayer);
    },
    flyToProject() {
      var coordinates = [116.91, 36.7251];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        zoom: 12,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    projectOrigin() {
      this.map.setLayoutProperty(
        "settlement-major-label copy",
        "visibility",
        "none"
      );
      this.map.setLayoutProperty(
        "settlement-minor-label copy",
        "visibility",
        "none"
      );
      this.map.addSource("projectOriginSource", {
        type: "geojson",
        data: require("../assets/data/jinan/roaddataJinanbei.json"),
      });
      this.map.addLayer({
        id: "prj-origin-layer",
        source: "projectOriginSource",
        type: "line",
        layout: {
          "line-join": "round",
          "line-cap": "round",
          visibility: "visible",
        },
        paint: {
          "line-color": "#5CF4F8",
          "line-width": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            [
              "interpolate",
              // ["exponential",1.5],
              ["linear"],
              ["get", "济南北溯源"],
              0,
              2,
              5000,
              6,
              78000,
              12,
            ],
            16,
            [
              "interpolate",
              // ["exponential",1.5],
              ["linear"],
              ["get", "济南北溯源"],
              0,
              4,
              50000,
              16,
              78000,
              32,
            ],
          ],
        },
      });
      this.map.addSource("originCircleSource", {
        type: "geojson",
        data: require("../assets/data/jinan/jinanbeisuyuan.json"),
      });
      this.map.addLayer({
        id: "origin-circle",
        source: "originCircleSource",
        type: "circle",
        paint: {
          "circle-color": "#F97952",
          "circle-radius": [
            "interpolate",
            ["exponential", 1],
            ["get", "总量"],
            330,
            8,
            60000,
            25,
          ],
        },
      });
      this.map.addLayer({
        id: "origin-circle-label",
        source: "originCircleSource",
        type: "symbol",
        layout: {
          "text-field": ["get", "地区"],
          "text-anchor": "bottom-left",
        },
        paint: {
          "text-color": "#000",
          "text-halo-width": 1.5,
          "text-halo-color": "#fff",
        },
      });
      this.map.on("mouseenter", "prj-origin-layer", this.changeCursorToPointer);
      // this.map.on("mousemove", "prj-origin-layer", this.originRoadPopup);
      this.map.on("mouseleave", "prj-origin-layer", this.leaveLayer);
      this.map.on("mouseenter", "origin-circle", this.changeCursorToPointer);
      this.map.on("mousemove", "origin-circle", this.originCirclePopup);
      this.map.on("mouseleave", "origin-circle", this.leaveLayer);

      var coordinates = [118.043, 36.377];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 3500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: coordinates,
        offset: [200, 0],
        zoom: 6.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },

    originRoadPopup(e) {
      this.map.getCanvas().style.cursor = "pointer";
      var coordinates = this.toolcoord;

      var volume = e.features[0].properties.济南北溯源 + " Pcu/d";
      // var ratio =
      //   e.features[0].properties.高速密度.toFixed(2) + " 公里/百平方公里";
      // console.log(e.features[0].properties)
      // const roadId = e.features[0].properties.序号;
      const roadName = e.features[0].properties.高速名称;
      var htmlPopup =
        '<div class="volumePopup"><p><b>' +
        roadName +
        "</b></p>" +
        "<p>来源交通量：<b>" +
        volume +
        "</b></p>";

      // Ensure that if the map is zoomed out such that multiple
      //   copies of the feature are visible, the popup appears
      //   over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }

      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },

    originCirclePopup(e) {
      this.map.getCanvas().style.cursor = "pointer";
      var coordinates = this.toolcoord;

      var volume = e.features[0].properties.总量 + " Pcu/d";
      // var ratio =
      //   e.features[0].properties.高速密度.toFixed(2) + " 公里/百平方公里";
      // console.log(e.features[0].properties)
      // const roadId = e.features[0].properties.序号;
      const roadName = e.features[0].properties.地区;
      var htmlPopup =
        '<div class="volumePopup"><p><b>' +
        roadName +
        "</b></p>" +
        "<p>来源交通量：<b>" +
        volume +
        "</b></p>";

      // Ensure that if the map is zoomed out such that multiple
      //   copies of the feature are visible, the popup appears
      //   over the copy being pointed to.
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }

      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },

    removeProjectOrigin() {
      console.log("removeP");
      this.map.setLayoutProperty(
        "settlement-major-label copy",
        "visibility",
        "visible"
      );
      this.map.setLayoutProperty(
        "settlement-minor-label copy",
        "visibility",
        "visible"
      );

      this.map.off(
        "mouseenter",
        "prj-origin-layer",
        this.changeCursorToPointer
      );
      this.map.off("mouseleave", "prj-origin-layer", this.leaveLayer);
      this.map.off("mouseenter", "origin-circle", this.changeCursorToPointer);
      this.map.off("mousemove", "origin-circle", this.originCirclePopup);
      this.map.off("mouseleave", "origin-circle", this.leaveLayer);
      this.map.removeLayer("prj-origin-layer");
      this.map.removeSource("projectOriginSource");
      this.map.removeLayer("origin-circle");
      this.map.removeLayer("origin-circle-label");
      this.map.removeSource("originCircleSource");
      console.log("removePCom");
    },

    // 加载济南全路网
    addJinanAllRoad() {
      this.map.addSource("jinanAllRoadSource", {
        type: "geojson",
        data: require("../assets/data/jinan/jinanAllRoad.json"),
      });
      this.map.addLayer({
        id: "jinanAllRoad",
        source: "jinanAllRoadSource",
        type: "line",
        layout: {
          "line-join": "round",
          "line-cap": "round",
        },
        paint: {
          "line-color": "#5CF4F8",
          "line-width": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            0.75,
            16,
            6,
          ],
        },
      });
    },

    addKakou() {
      this.map.addSource("kakouSource", {
        type: "geojson",
        data: require("../assets/data/jinan/jinankakou.json"),
      });
      this.map.addLayer({
        id: "jinankakou",
        source: "kakouSource",
        type: "circle",
        paint: {
          "circle-radius": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            1.5,
            16,
            8,
          ],
          "circle-color": "#24FFC5",
        },
      });
      this.map.on("mouseenter", "jinankakou", this.changeCursorToPointer);
      this.map.on("mousemove", "jinankakou", this.kakouPopup);
      this.map.on("mouseleave", "jinankakou", this.leaveLayer);
    },

    // 卡口弹窗
    kakouPopup(e) {
      this.map.getCanvas().style.cursor = "pointer";
      var coordinates = this.toolcoord;
      const kkId = e.features[0].properties.卡口编号;
      const kkName = e.features[0].properties.卡口名称;
      const adm = e.features[0].properties.辖区;
      var htmlPopup = "";

      htmlPopup =
        '<div class="volumePopup"><p><b>' +
        kkName +
        "</b></p>" +
        "<p>卡口编号：<b>" +
        kkId +
        "</b></p>";
      htmlPopup += "<p>辖区：<b>" + adm + "</b></p>";
      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }

      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },

    // 济阳卡口
    addJiyangKakou() {
      this.map.addSource("jiyangKakouSource", {
        type: "geojson",
        data: require("../assets/data/jinan/jiyangkakou.json"),
      });
      this.map.addLayer({
        id: "jiyangkakou",
        source: "jiyangKakouSource",
        type: "circle",
        paint: {
          "circle-radius": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            1.5,
            16,
            8,
          ],
