    addRoadOriginLayer(e) {
      const pointStart = e.features[0].properties.start;
      const pointEnd = e.features[0].properties.end;
      const originJson = require("../assets/data/checkOrigin.json");
      const trace = originJson["trace"];
      var kakouJson = require("../assets/data/keyPoint.json");
      var features = kakouJson["features"];
      var keyIds = [];
      var numsO = [];
      var numsD = [];
      features.forEach((item) => {
        keyIds.push(item["properties"]["序号"]);
        item["properties"]["origin"] = 0;
        item["properties"]["destination"] = 0;
        numsO.push(0);
        numsD.push(0);
      });
      trace.forEach((item) => {
        var path = item["path"];
        var oId = parseInt(path[0]);
        var dId = parseInt(path[1]);
        var pathIds = [];
        var isValid = false;
        for (var i = 0; i < path.length - 1; i++) {
          var idInt = parseInt(path[i]);
          var nxtIdInt = parseInt(path[i + 1]);
          if (
            (idInt == pointStart && nxtIdInt == pointEnd) ||
            (idInt == pointEnd && nxtIdInt == pointStart)
          ) {
            isValid = true;
          } else {
            continue;
          }
        }
        if (isValid == true) {
          const value = item["value"];
          var oIndex = keyIds.indexOf(oId);
          var dIndex = keyIds.indexOf(dId);
          numsO[oIndex] = numsO[oIndex] + value;
          numsD[dIndex] = numsD[dIndex] + value;
        }
      });
      for (var i = 0; i < numsO.length; i++) {
        features[i]["properties"]["origin"] = numsO[i];
        features[i]["properties"]["destination"] = numsD[i];
      }
      var maxO = 0;
      var maxD = 0;
      for (var j = 0; j < numsD.length; j++) {
        if (numsO[j] > maxO) {
          maxO = numsO[j];
        }
        if (numsD[j] > maxD) {
          maxD = numsD[j];
        }
      }
      var maxAll = 0;
      if (maxO > maxD) {
        maxAll = maxO;
      } else {
        maxAll = maxD;
      }
      var zoom5expression = [
        "interpolate",
        ["linear"],
        ["get", "origin"],
        0,
        1.5,
        maxAll,
        8,
      ];
      var zoom16expression = [
        "interpolate",
        ["linear"],
        ["get", "origin"],
        0,
        12,
        maxAll,
        60,
      ];
      var zoom5expressionD = [
        "interpolate",
        ["linear"],
        ["get", "destination"],
        0,
        1.5,
        maxAll,
        8,
      ];
      var zoom16expressionD = [
        "interpolate",
        ["linear"],
        ["get", "destination"],
        0,
        12,
        maxAll,
        60,
      ];
      if (maxAll == 0) {
        zoom5expression = 1.5;
        zoom16expression = 12;
        zoom5expressionD = 1.5;
        zoom16expressionD = 12;
      }
      this.map.addSource("kakouOriginSource", {
        type: "geojson",
        data: kakouJson,
      });
      this.map.addLayer({
        id: "kakouOrigin",
        source: "kakouOriginSource",
        type: "circle",
        paint: {
          "circle-radius": [
            "interpolate",
            ["exponential", 1.5],
            ["zoom"],
            5,
            zoom5expression,
            16,
            zoom16expression,
          ],
          "circle-opacity": ["match", ["get", "origin"], 0, 0, 1],
          "circle-color": ["match", ["get", "origin"], 0, "#F97952", "#ffff00"],
        },
