      // this.map.on("mousemove", "kakouOrigin", this.kakouPopup2);
      this.map.off("click", "originRoad", this.addRoadOriginLayer);
      this.map.on("click", "originRoad", this.updateRoadOriginLayer);

      var coordinates = [117.0652, 36.6508];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 1500,
        easing: easingFn,
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
