    },
    updateRoadOriginLayer(e) {
      const pointStart = e.features[0].properties.start;
      const pointEnd = e.features[0].properties.end;
      const originJson = require("../assets/data/checkOrigin.json");
      const trace = originJson["trace"];
      var originRoadSourceJson = require("../assets/data/originRoad.json");
      var roadSourceFeatures = originRoadSourceJson["features"];
      roadSourceFeatures.forEach((feature) => {
        var p = feature["properties"];
        p["volume"] = 0;
      });
      // roadSourceFeatures.forEach((feature) => {
      //   var p = feature["properties"];
      //   console.log(p["start"],p["end"],p["volume"]);
      // });
      var volumes = {};

      var kakouJson = require("../assets/data/keyPoint.json");
      var features = kakouJson["features"];
      var keyIds = [];
      var numsO = [];
      var numsD = [];
      features.forEach((item) => {
        keyIds.push(item["properties"]["序号"]);
        item["properties"]["origin"] = 0;
        item["properties"]["destination"] = 0;
        numsO.push(0);
        numsD.push(0);
      });
      trace.forEach((item) => {
        var path = item["path"];
        var oId = parseInt(path[0]);
        var dId = parseInt(path[1]);
        var pathIds = [];
        var isValid = false;
        for (var i = 0; i < path.length - 1; i++) {
          var idInt = parseInt(path[i]);
          var nxtIdInt = parseInt(path[i + 1]);
          if (
            (idInt == pointStart && nxtIdInt == pointEnd) ||
            (idInt == pointEnd && nxtIdInt == pointStart)
          ) {
            isValid = true;
          } else {
            continue;
          }
        }
        if (isValid == true) {
          const value = item["value"];
          var oIndex = keyIds.indexOf(oId);
          var dIndex = keyIds.indexOf(dId);
          numsO[oIndex] = numsO[oIndex] + value;
          numsD[dIndex] = numsD[dIndex] + value;
          for (var i = 0; i < path.length - 1; i++) {
            var idInt = parseInt(path[i]);
            var nxtIdInt = parseInt(path[i + 1]);
            var keyStr = idInt + "-" + nxtIdInt;
            if (keyStr in volumes) {
              volumes[keyStr] = volumes[keyStr] + value;
            } else {
              volumes[keyStr] = value;
            }
          }
        }
      });

      for (var i = 0; i < numsO.length; i++) {
        features[i]["properties"]["origin"] = numsO[i];
        features[i]["properties"]["destination"] = numsD[i];
      }
      var maxO = 0;
      var maxD = 0;
