      for (var j = 0; j < numsD.length; j++) {
        if (numsO[j] > maxO) {
          maxO = numsO[j];
        }
        if (numsD[j] > maxD) {
          maxD = numsD[j];
        }
      }
      var zoom5expression = [
        "interpolate",
        ["linear"],
        ["get", "origin"],
        0,
        1.5,
        maxO,
        5,
      ];
      var zoom16expression = [
        "interpolate",
        ["linear"],
        ["get", "origin"],
        0,
        12,
        maxO,
        30,
      ];
      if (maxO == 0) {
        zoom5expression = 1.5;
        zoom16expression = 12;
      }
      this.map.getSource("kakouOriginSource").setData(kakouJson);
      this.map.setPaintProperty("kakouOrigin", "circle-radius", [
        "interpolate",
        ["exponential", 1.5],
        ["zoom"],
        5,
        zoom5expression,
        16,
        zoom16expression,
      ]);
      this.map.on("mousemove", "kakouOrigin", this.kakouPopup2);
      this.map.on("click", "kakouOrigin", this.updateOriginLayer);
      this.map.on("mouseleave", "kakouOrigin", this.leaveLayer);
      const blob = new Blob([JSON.stringify(kakouJson, null, 2)], {
        type: "application/json",
      });
      // FileSaver.saveAs(blob, kkName + "odjson.geojson");
    },
  },
  mounted() {
    // this.initMap(0);
    this.initChart();
    // this.tollLayer();
  },
};
</script>

