<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>烟台市港口吞吐量变化情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  烟台港位于山东半岛东部，隔海与日本、韩国相望，地处东北亚国际经济圈核心地带，是中国沿海25个重要枢纽港口之一，包括芝罘湾港区、西港区、龙口港区、莱州港区等多个港区。
                </p>
                <p>
                  烟台港吞吐量增长迅速，自2010年的2.08亿吨增长至2018年的4.43亿吨，年均增长9.9%。2019年，由于吞吐量统计办法调整，烟台港吞吐量回落至3.85亿吨，占全省总量的24%，位居全省第三。
                </p>

                <div id="chart41"></div>

                <div id="chart42"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      tuntubianhua: [
        {
          年份: 2010,
          "吞吐量（万吨）": "20,852.50",
        },
        {
          年份: 2011,
          "吞吐量（万吨）": "24,345.30",
        },
        {
          年份: 2012,
          "吞吐量（万吨）": "27,029.80",
        },
        {
          年份: 2013,
          "吞吐量（万吨）": "28,680.00",
        },
        {
          年份: 2014,
          "吞吐量（万吨）": "31,970.70",
        },
        {
          年份: 2015,
          "吞吐量（万吨）": "33,027.40",
        },
        {
          年份: 2016,
          "吞吐量（万吨）": "35,407.00",
        },
        {
          年份: 2017,
          "吞吐量（万吨）": "40,058.10",
        },
        {
          年份: 2018,
          "吞吐量（万吨）": "44,308.00",
        },
      ],
      gangquzhanbi: [
        {
          港区名称: "芝罘湾港区",
          比重: 0.234912,
        },
        {
          港区名称: "西港区",
          比重: 0.279995,
        },
        {
          港区名称: "蓬莱东港区",
          比重: 0.029288,
        },
        {
          港区名称: "蓬莱西港区",
          比重: 0.012959,
        },
        {
          港区名称: "栾家口港区",
          比重: 0.017832,
        },
        {
          港区名称: "龙口港区",
          比重: 0.246228,
        },
        {
          港区名称: "莱州港区",
          比重: 0.059406,
        },
        {
          港区名称: "海阳港区",
          比重: 0.021668,
        },
        {
          港区名称: "牟平港区",
          比重: 0.003369,
        },
        {
          港区名称: "其他港区",
          比重: 0.094343,
        },
      ],
    };
  },
  props: ["showPC"],
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart41"));
      var table = this.tuntubianhua;
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        xdata.push(table[i].年份);
        // 将吞吐量转换为数字
        ydata.push(parseFloat(table[i]["吞吐量（万吨）"].replace(/,/g, "")));
      }
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " 万吨"
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
            data: xdata,
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "吞吐量",
            type: "line",
            smooth: true,
            symbol: "circle",
            symbolSize: 6,
            showSymbol: true,
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            lineStyle: {
              width: 2,
              color: "#5CF4F8",
            },
            data: ydata,
          },
        ],
      });
    },
    initChart2() {
      var table = this.gangquzhanbi;
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        ydata.push(table[i].港区名称);
        xdata.push(table[i].比重);
      }
      this.chart2 = echarts.init(document.getElementById("chart42"));
      this.chart2.setOption({
        tooltip: {
          show: true,
          trigger: "item",
          formatter: function(params){
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              (params.value*100).toFixed(1) +
              " %"
            );
          }
        },
        grid: {
          x: 75,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        yAxis: [
          {
            type:"category",
            inverse:true,
            data: ydata,
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
          }],
        xAxis: [
          {
            type: "value",
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "港区占比",
            type: "bar",
            barWidth: "80%",
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: xdata,
            label:{
              show:true,
              position:"right",
              color:"#fff",
              formatter:function(params){
                return (params.value*100).toFixed(1) + "%";
              }
            }
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-2");
        this.$emit("subTitle", "5-2");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "8");
        this.$emit("subTitle", "8");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}

.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}

#chart41 {
  width: calc(100%-20px);
  height: 350px;
}
#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>