<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>基础数据来源</h1>
            <div class="next">
              <el-button :disabled="true" @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
            <p>
              为了详细掌握潍坊市区交通出行分布特征，项目组调用了潍坊市区道路卡口数据（共计454处卡口），同时在月河路、北宫街主要交叉口进行了交通量观测。
            </p>
            
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["沈海高速", "日照至鲁苏界", 0.9],
        ["济广高速", "孔村枢纽至东平湖枢纽", 0.83],
        ["长深高速", "于家庄枢纽至临朐立交", 0.82],
        ["青银高速", "青岛东收费站至夏庄枢纽", 0.81],
        ["长深高速", "无棣至鲁冀界", 0.8],
        ["青银高速", "崔寨枢纽至机场枢纽", 0.79],
        ["济广高速", "定陶枢纽至鲁豫界", 0.77],
        ["德上高速", "聊城西枢纽至李集枢纽", 0.75],
      ],
      toll_rank: [
        ["青岛东主线收费站", "青银高速", 79729],
        ["双埠主线收费站", "青兰高速", 54861],
        ["济南零点收费站", "济广高速", 48447],
        ["济南机场收费站", "济南绕城高速", 43986],
        ["天桥收费站", "济广高速", 42246],
        ["福山收费站", "沈海高速", 40907],
        ["济南北收费站", "济广高速", 40383],
        ["黄岛东主线收费站", "前湾港疏港1号线", 39385],
        ["临沂收费站", "京沪高速", 39022],
        ["泰安东收费站", "青兰高速", 34989],
      ],
    };
  },
  props:["showPC"],
  methods: {
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "project");
        this.$emit("subTitle", 'project');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "1");
        this.$emit("subTitle", 1);
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "90px";
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;

    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>
.pmain {
  max-height: 650px;
}
.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}
.bl b {
  color: #1dbace;
}
b.green {
  color: #4d9b30;
}
b.yellow {
  color: #f6a512;
}
b.red {
  color: #f03958;
}

#chartyt11 {
  width: calc(100%-20px);
  height: 150px;
}
#chartyt12 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>