<template>
  <div>
    <el-scrollbar>
      <div id="mapin"></div>
      <div class="footerBlue"></div>
      <div class="floatenterance">
        <div class="subbutton">
            <div @click="fly2highway" class="highlight">高速公路<br />数据应用平台</div>
          </div>
        <div class="subbutton"><router-link to="/yantai">
            <div class="highlight">国省道数据<br />应用平台</div>
          </router-link></div>
        <div class="subbutton"><router-link to="/cityselect">
            <div class="highlight">城市道路<br />数据应用平台</div>
          </router-link></div>


      </div>

    </el-scrollbar>
  </div>
</template>

<script>
import "ol/ol.css";
import * as echarts from "echarts";
import "echarts-gl";
import View from "ol/View";
import * as mapboxgl from "mapbox-gl"; //引入组件
import IntroPanel from "./introPanel.vue";
import DataPanel from './dataPanel.vue';

export default {
  name: "sdmap",
  props: ["tab"],
  data: function () {
    return {
      tabNum: 1,
      echartslayer: null,
      map: null,
      chart: null,
      subTitle: 1,
      tollPopup: new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      }),
      ccdata: null,
      counter: 0,
    };
  },
  components: {
    IntroPanel,
    DataPanel,
  },
  methods: {
    initChart() {
      window.mapboxgl = mapboxgl;
      window.mapboxgl.accessToken =
        // "pk.eyJ1IjoienBlbmcyMDE4IiwiYSI6ImNreDE4OHB5MTFlOXQydmxhcW52ZGo1NDIifQ.9NllPb9w2D-oiQDftWS5bg";
        "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw";
      var mapStyle = require("../assets/mapstyle/mapstyle2.json");
      this.map = new mapboxgl.Map({
        container: "mapin",
        projection: "globe",
        // accessToken:
        //   "pk.eyJ1IjoienBlbmcyMDE4IiwiYSI6ImNreDE4OHB5MTFlOXQydmxhcW52ZGo1NDIifQ.9NllPb9w2D-oiQDftWS5bg",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [112.38, 36.616],
        zoom: 2.1,
        // style: "mapbox://styles/zpeng2018/cl2zgt9sf000414p5xz810c63",
        // style: mapStyle, // 本地地图样式
        // 复刻样式
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      });
      let controls = this.map._controls;
      controls.forEach((control) => {
        // 去除logo控件
        if ("_updateLogo" in control) {
          this.map.removeControl(control);
        }

        // 去除attribute控件
        if ("_attribHTML" in control) {
          this.map.removeControl(control);
        }
      });

      this.map.getCanvas().style.cursor = "default";
      this.map.on("load", () => {
        this.map.setFog({
          color: 'rgb(186, 210, 235)', // Lower atmosphere
          'high-color': 'rgb(36, 92, 223)', // Upper atmosphere
          'horizon-blend': 0.02, // Atmosphere thickness (default 0.2 at low zooms)
          'space-color': 'rgb(11, 11, 25)', // Background color
          'star-intensity': 0.6 // Background star brightness (default 0.35 at low zoooms )
        });
        this.chinaLabelLayer();
        this.map.addSource("volumeSource", {
          type: "geojson",
          data: require("../assets/data/road20220822_new.json"),
        });
        this.map.addLayer({
          id: "volume-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "visible",
          },
          paint: {
            "line-color": "#5CF4F8",
            "line-width": [
              "interpolate",
              ["exponential", 1],
              ["zoom"],
              2,
              0.5,
              6,
              3,
              16,
              10
            ],
            "line-opacity": [
              "interpolate",
              ["exponential", 1],
              ["zoom"],
              2,
              0,
              6,
              0.5,
              7,
              1
            ]
          },
        });
        // this.map.addLayer({
        //   id: "volume-layer3",
        //   type: "line",
        //   source: "volumeSource",
        //   layout: {
        //     "line-join": "round",
        //     "line-cap": "round",
        //     visibility: "visible",
        //   },
        //   paint: {
        //     "line-color": "orange",
        //     "line-width": 5,
        //     "line-blur": 3,
        //     "line-opacity": 0.4,
        //   },
        // });
        // this.map.addLayer({
        //   id: "volume-layer2",
        //   type: "line",
        //   source: "volumeSource",
        //   layout: {
        //     "line-join": "round",
        //     "line-cap": "round",
        //     visibility: "visible",
        //   },
        //   paint: {
        //     "line-color": "#5CF4F8",
        //     "line-width": 5,
        //     "line-blur": 1,
        //     "line-opacity": 1,
        //   },
        // });

        this.map.addSource("shandong", {
          type: "geojson",
          data: require("../assets/data/shandong.json"),
        });
        this.map.addLayer({
          id: "shandongContour",
          type: "line",
          source: "shandong",
          paint: {
            "line-color": "#a9e1ff",
            "line-width": [
              "interpolate",
              ["exponential", 1],
              ["zoom"],
              2,
              0.5,
              6,
              1,
              16,
              1
            ],
            "line-opacity": [
              "interpolate",
              ["exponential", 1],
              ["zoom"],
              2,
              0,
              6,
              1
            ]
          },
        });
        this.map.addSource("china", {
          type: "geojson",
          data: require("../assets/data/china.json"),
        });
        this.map.addLayer({
          id: "chinaContour",
          type: "line",
          source: "china",
          paint: {
            "line-color": "#a9e1ff",
          },
        });
        this.map.addLayer({
          id: "chinaFill",
          type: "fill",
          source: "china",
          paint: {
            "fill-color": "#a9e1ff",
            "fill-opacity": [
              "interpolate",
              ["exponential", 1],
              ["zoom"],
              2,
              ["match", ["get", "name"], "山东省", 0.15, 0.15],
              6,
              ["match", ["get", "name"], "山东省", 0, 0.15],
            ]
          },
        });

        setTimeout(() => {

        }, 1000)

      });
      console.log("loaded?", this.map.loaded());

    },
    fly2highway(){
      
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [120.87, 36.616],
        zoom: 6.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
      setTimeout(()=>{
        this.$router.push(this.$route.path + "highway");
      },2750)

    },
    chinaLabelLayer() {
      // 添加中国的label
      this.map.addSource("chinaLabelSource", {
        type: "geojson",
        data: require("../assets/data/chinalabel.json"),
      });
      this.map.addLayer({
        id: "chinaLabel-layer",
        source: "chinaLabelSource",
        type: "symbol",
        layout: {
          "text-field": ["get", "name"],
          "text-anchor": "center",
          "text-padding": 4,
          "text-size": 15,
        },
        paint: {
          "text-color": "azure",
          "text-halo-width": 1,
          "text-halo-color": "#000",
        },
      });
    },
    removeChinaLabelLayer() {
      this.map.removeLayer("chinaLabel-layer");
      this.map.removeSource("chinaLabelSource");
    },

  },
  mounted() {
    // this.initMap(0);
    this.initChart();
    // this.tollLayer();
  },
};
</script>

<style scoped>
/* #map {
  width: 100%;
  height: 840px;
} */

#mapin {
  width: 100%;
  height: 900px;
}

div.floatenterance {
  position: absolute;
  /* background-color: aliceblue; */
  height: 600px;
  width: 300px;
  right: 5%;
  top: 15%;
}

div.floatenterance div.subbutton {
  font-size: 24px;
  backdrop-filter: blur(10px);
  border-radius: 25px;
  box-shadow: 0px 0px 50px #5cf3f844 inset;
  margin: 50px 1%;
  color: #5CF4F8;
  display: block;
  text-align: center;
  /* vertical-align: middle; */
  width: 75%;
  height: 100px;
  padding-top: 30px;

}

div.floatenterance div.subbutton:hover {
  box-shadow: 0px 0px 50px #5cf3f884 inset;

}

div.highlight {
  color: #5CF4F8;
}

div.highlight:hover {
  color: #5CF4F8;
  font-weight: bold;
}

div.leftControl {
  position: absolute;
  left: 20px;
  top: 20px;
}

div.leftPanel {
  position: absolute;
  box-shadow: 0 0px 10px rgba(35, 35, 35, 0.48);
  left: 10px;
  top: 120px;
  z-index: 98;
}

div.headerfloat {
  /* background-color: #00000020; */
  background-image: linear-gradient(to bottom,
      #08163db0,
      #08163d6a,
      rgba(255, 0, 0, 0));
  /* backdrop-filter: blur(5px); */
  text-align: center;
  /* box-shadow: 0 10px 10px rgba(36, 36, 36, 0.277); */
  position: absolute;
  top: 0;
  width: 100%;
  height: 150px;
  z-index: 99;
}

div.footerBlue {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  background-image: linear-gradient(to top,
      #08163d,
      #08163d49,
      rgba(255, 0, 0, 0));
}
</style>