<template>
  <div>
    <el-scrollbar>
      <div id="csmap"></div>
      <!-- <div class="leftPanel">
        <el-scrollbar>
          <div v-if="subTitle == 1">
            <intro-panel />
          </div>
        </el-scrollbar>
      </div> -->
      <div class="footerBlue"></div>
      <!-- <div class="floatenterance">
        <div class="subbutton"><router-link to="/highway"><div class="highlight">高速公路<br/>应用平台</div></router-link></div>
        <div class="subbutton"><router-link to="/yantai"><div class="highlight">国省道<br/>应用平台</div></router-link></div>
        <div class="subbutton"><router-link to="/qingdao"><div class="highlight">城市道路<br/>应用平台</div></router-link></div>


      </div> -->
    </el-scrollbar>
  </div>
</template>

<script>
import "ol/ol.css";
import * as echarts from "echarts";
import "echarts-gl";
import View from "ol/View";
import * as mapboxgl from "mapbox-gl"; //引入组件

export default {
  name: "cityselectmap",
  props: ["tab"],
  data: function () {
    return {
      tabNum: 1,
      echartslayer: null,
      map: null,
      chart: null,
      subTitle: 1,
      tollPopup: new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      }),
      ccdata: null,
      counter: 0,
    };
  },
  components: {},
  methods: {
    initChart() {
      window.mapboxgl = mapboxgl;
      window.mapboxgl.accessToken =
        "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw";
      this.map = new mapboxgl.Map({
        container: "csmap",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [118.38, 36.616],
        zoom: 6.5,
        // style: "mapbox://styles/zpeng2018/ckyth6z1u000814nxo5meg5x9",
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      });
      console.log("loaded?", this.map.loaded());
      let controls = this.map._controls;
      controls.forEach((control) => {
        // 去除logo控件
        if ("_updateLogo" in control) {
          this.map.removeControl(control);
        }

        // 去除attribute控件
        if ("_attribHTML" in control) {
          this.map.removeControl(control);
        }
      });
      // this.map = this.chart.getModel().getComponent("mapbox3D").getMapbox();
      this.map.getCanvas().style.cursor = "default";
      this.map.on("load", () => {
        this.map.addSource("volumeSource", {
          type: "geojson",
          data: require("../assets/data/road20220822_new.json"),
        });
        this.map.addLayer({
          id: "volume-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "#5CF4F8",
            "line-width": 5,
            "line-blur": 3,
            "line-opacity": 0.4,
          },
        });
        this.map.addLayer({
          id: "volume-layer3",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "orange",
            "line-width": 5,
            "line-blur": 3,
            "line-opacity": 0.4,
          },
        });
        this.map.addLayer({
          id: "volume-layer2",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "#5CF4F8",
            "line-width": 5,
            "line-blur": 1,
            "line-opacity": 1,
          },
        });

        this.map.addSource("shandong", {
          type: "geojson",
          data: require("../assets/data/shandong.json"),
        });
        this.map.addLayer({
          id: "shandongContour",
          type: "line",
          source: "shandong",
          paint: {
            "line-color": "#a9e1ff",
            "line-width":1,
          },
        });
        this.map.addLayer({
          id: "shandongFill",
          type: "fill",
          source: "shandong",
          paint: {
            "fill-color": "#a9e1ff",
            "fill-opacity": 0,
          },
        });
        this.map.setPaintProperty("shandongFill", "fill-opacity", [
          "match",
          ["get", "name"],
          ["济南市", "青岛市", "潍坊市","威海市"],
          0.3,
          0,
        ]);
        this.map.addSource("china", {
          type: "geojson",
          data: require("../assets/data/china.json"),
        });
        this.map.addLayer({
          id: "chinaContour",
          type: "line",
          source: "china",
          paint: {
            "line-color": "#a9e1ff",
          },
        });
        this.map.addLayer({
          id: "chinaFill",
          type: "fill",
          source: "china",
          paint: {
            "fill-color": "#a9e1ff",
            "fill-opacity": ["match", ["get", "name"], "山东省", 0, 0.15],
          },
        });
      });
      console.log("loaded?", this.map.loaded());
      this.map.on("mousemove", "shandongFill", this.changeColor);
      this.map.on("click", "shandongFill", this.enterPage);
      
    },
    changeColor(e){
      const coordinates = this.toolcoord;
      const cityname = e.features[0].properties.name;
      if(cityname == '济南市' || cityname == '青岛市' || cityname == '潍坊市' || cityname == '威海市'){
        this.changeToPointer();
        this.map.setPaintProperty("shandongFill", "fill-color", [
          "match",
          ["get", "name"],
          [cityname],
          "#24FFC5",
          "#5CF4F8",
        ]);
        this.map.setPaintProperty("shandongContour", "line-width", [
          "match",
          ["get", "name"],
          [cityname],
          3,
          1,
        ]);
      }else{
        this.changeToDefault();
        this.map.setPaintProperty("shandongFill", "fill-color", 
          "#5CF4F8");
        this.map.setPaintProperty("shandongContour", "line-width", 1);
      }
    },
    changeToPointer() {
      this.map.getCanvas().style.cursor = "pointer";
    },
    changeToDefault() {
      this.map.getCanvas().style.cursor = "default";
    },
    enterPage(e){
      const cityname = e.features[0].properties.name;
      if(cityname == '济南市'){
        this.$router.push("/jinan");
      }
      if(cityname == '青岛市'){
        this.$router.push("/qingdao");
      }
      if(cityname == '潍坊市'){
        this.$router.push("/dataDrivenModel");
      }
      if(cityname == '威海市'){
        this.$router.push("/weihai");
      }



    }
  },

  mounted() {
    // this.initMap(0);
    this.initChart();
    // this.tollLayer();
  },
};
</script>

<style scoped>
/* #map {
  width: 100%;
  height: 840px;
} */

#csmap {
  width: 100%;
  height: 900px;
}

