  font-size: 24px;
  backdrop-filter: blur(10px);
  border-radius: 25px;
  box-shadow: 0px 0px 50px #5cf3f844 inset;
  margin: 0 1%;
  color: #5cf4f8;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  width: 25%;
  height: 100px;
  padding-top: 30px;
}

div.floatenterance div.subbutton:hover {
  box-shadow: 0px 0px 50px #5cf3f884 inset;
}

div.highlight {
  color: #5cf4f8;
}
div.highlight:hover {
  color: #5cf4f8;
  font-weight: bold;
}

div.leftControl {
  position: absolute;
  left: 20px;
  top: 20px;
}

div.leftPanel {
  position: absolute;
  box-shadow: 0 0px 10px rgba(35, 35, 35, 0.48);
  left: 10px;
  top: 120px;
  z-index: 98;
}
div.headerfloat {
  /* background-color: #00000020; */
  background-image: linear-gradient(
    to bottom,
    #08163db0,
    #08163d6a,
    rgba(255, 0, 0, 0)
  );
  /* backdrop-filter: blur(5px); */
  text-align: center;
  /* box-shadow: 0 10px 10px rgba(36, 36, 36, 0.277); */
  position: absolute;
  top: 0;
  width: 100%;
  height: 150px;
  z-index: 99;
}

div.footerBlue {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  background-image: linear-gradient(
    to top,
    #08163d,
    #08163d49,
    rgba(255, 0, 0, 0)
  );
}
</style>