<template>
  <div>
    <el-scrollbar>
      <div id="map2"></div>
      <div class="leftPanel">
        <el-scrollbar>
          <div v-if="subTitle == '1'">
            <leftPanel1
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '2'">
            <leftPanel2
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '3'">
            <leftPanelChuxing1
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '4'">
            <leftPanelChuxing2
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '5'">
            <leftPanelChuxing3
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '6'">
            <leftPanelChuxing4
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '7'">
            <leftPanel3
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '8'">
            <leftPanel4
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '5-1'">
            <leftPanel51
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '5-2'">
            <leftPanel52
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '5-3'">
            <leftPanel53
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
          <div v-if="subTitle == '5-4'">
            <leftPanel54
              :showPC="globalShowPanelContent"
              @showContent="changeshowcontent"
              @subTitle="changeTitleByValue"
            />
          </div>
        </el-scrollbar>
      </div>
      <div class="footerBlue"></div>
    </el-scrollbar>
  </div>
</template>

<script>
import "ol/ol.css";
import * as echarts from "echarts";
import "echarts-gl";
import View from "ol/View";
import leftPanel3 from "./yantaiPanel/leftPanel3.vue";
import leftPanel1 from "./yantaiPanel/leftPanel1.vue";
import leftPanel2 from "./yantaiPanel/leftPanel2.vue";
import leftPanel4 from "./yantaiPanel/leftPanel4.vue";
import * as mapboxgl from "mapbox-gl"; //引入组件
import turf from "turf";
import leftPanelChuxing1 from "./yantaiPanel/leftPanelChuxing1.vue";
import leftPanelChuxing2 from "./yantaiPanel/leftPanelChuxing2.vue";
import leftPanelChuxing3 from "./yantaiPanel/leftPanelChuxing3.vue";
import leftPanelChuxing4 from "./yantaiPanel/leftPanelChuxing4.vue";
import leftPanel51 from "./yantaiPanel/leftPanel51.vue";
import leftPanel52 from "./yantaiPanel/leftPanel52.vue";
import leftPanel53 from "./yantaiPanel/leftPanel53.vue";
import leftPanel54 from "./yantaiPanel/leftPanel54.vue";

export default {
  name: "sdmap",
  props: ["tab"],
  data: function () {
    return {
      tabNum: 1,
      map: null,
      chart: null,
      subTitle: "0",
      globalShowPanelContent: true,
      tollPopup: new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      }),
      counter: 0,
      animation: null,
      toolcoord: null,
    };
  },
  components: {
    leftPanel3,
    leftPanel2,
    leftPanel1,
    leftPanel4,
    leftPanelChuxing1,
    leftPanelChuxing2,
    leftPanelChuxing3,
    leftPanelChuxing4,
    leftPanel51,
    leftPanel52,
    leftPanel53,
    leftPanel54,
  },
  watch: {
    tab(val) {
      this.subTitle = val;
    },
    subTitle(val, oldV) {
      if (oldV == "1") {
        this.map.setLayoutProperty("ytkakou", "visibility", "none");
      }
      if (oldV == "2") {
        this.map.setLayoutProperty("yt-districts", "visibility", "none");
      }
      if (oldV == "3") {
        this.removeChuxingfenbuLayer();
      }
      if (oldV == "4") {
        this.removeNeibuchuxingLayer();
      }
      if (oldV == "5") {
        this.removeDuiwaiLayer();
      }
      if (oldV == "6") {
        this.removeGuojingLayer();
      }
      if (oldV == "7") {
        this.map.setLayoutProperty("volume-layer", "visibility", "none");
      }
      if (oldV == "8") {
        this.map.setLayoutProperty("ratio-layer", "visibility", "none");
      }
      if (oldV == "5-1") {
        this.map.setLayoutProperty("yt-districts", "visibility", "none");
        this.removeGangkouLayer();
      }
      if (oldV == "5-2") {
        this.map.setLayoutProperty("yt-districts", "visibility", "none");
        this.removeLaizhougangquLayer();
      }
      if (oldV == "5-3") {
        this.map.setLayoutProperty("yt-districts", "visibility", "none");
        this.removeLongkougangquLayer();
      }
      if (oldV == "5-4") {
        this.map.setLayoutProperty("yt-districts", "visibility", "none");
        this.removeXigangquLayer();
      }
      if (val == "6") {
        this.guojingLayer();
      }
      if (val == "5-1") {
        this.gangkouLayer();
      }
      if (val == "5-2") {
        this.laizhougangquLayer();
      }
      if (val == "5-3") {
        this.longkougangquLayer();
      }
      if (val == "5-4") {
        this.xigangquLayer();
      }

      if (val == "8") {
        this.colorLayer();
      }
      if (val == "7") {
        this.volumeLayer();
      }
      if (val == "5") {
        this.duiwaiLayer();
      }
      if (val == "4") {
        this.neibuchuxingLayer();
      }
      if (val == "3") {
        this.chuxingfenbuLayer();
      }
      if (val == "2") {
        this.map.setLayoutProperty("yt-districts", "visibility", "visible");
        // this.map.setPaintProperty("yt-districts", "fill-opacity", 0.3);
      }
      if (val == "1") {
        this.tollLayer();
      }
      this.$emit("st", this.subTitle);
    },
  },
  methods: {
    initChart() {
      window.mapboxgl = mapboxgl;
      window.mapboxgl.accessToken =
        "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw";
      // var mapStyle = require("../assets/mapstyle/deepblue.json");
      
      var option = {
        container: "map2",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        
        center: [120.1, 37.385],
        zoom: 8.1,
        // style: mapStyle, // 本地地图样式
        // 复刻样式
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      };
      this.map = new mapboxgl.Map(option);
      let controls = this.map._controls;
      controls.forEach((control) => {
        // 去除logo控件
        if ("_updateLogo" in control) {
          this.map.removeControl(control);
        }

        // 去除attribute控件
        if ("_attribHTML" in control) {
          this.map.removeControl(control);
        }
      });
      this.map.getCanvas().style.cursor = "default";
      this.map.on("load", () => {
        this.map.setLayoutProperty("yantai-buildings", "visibility", "visible");

        this.map.addSource("shandong", {
          type: "geojson",
          data: require("../assets/data/shandong.json"),
        });
        this.map.addLayer({
          id: "shandongContour",
          type: "line",
          source: "shandong",
          paint: {
            "line-color": "#a9e1ff",
          },
        });
        this.map.addLayer({
          id: "shandongFill",
          type: "fill",
          source: "shandong",
          paint: {
            "fill-color": "#F1F1F1",
            "fill-opacity": ["match", ["get", "name"], "烟台市", 0, 0.12],
          },
        });
        this.map.addSource("ytkakouSource", {
          type: "geojson",
          data: require("../assets/data/yantai/yantaikakou.json"),
        });
        this.map.addLayer({
          id: "ytkakou",
          type: "circle",
          source: "ytkakouSource",
          layout: {
            visibility: "visible",
          },
          paint: {
            "circle-radius": 2.5,
            "circle-color": "#5CF4F8",
            "circle-opacity": 0.6,
          },
        });
        this.map.addSource("ytDistrictSource", {
          type: "geojson",
          data: require("../assets/data/yantai/yantai.json"),
        });
        this.map.addLayer({
          id: "yt-districts",
          type: "fill",
          source: "ytDistrictSource",
          layout: { visibility: "none" },

          paint: {
            "fill-color": [
              "interpolate",
              ["linear"],
              ["get", "travelnum"],
              0.049,
              "#DEEDF0",
              0.157,
              "#33D3EB",
            ],
            "fill-opacity": 0.8,
          },
        });

        this.map.addLayer({
          id: "yt-districts-border",
          type: "line",
          source: "ytDistrictSource",
          "layout": {"visibility": "visible"},
          "paint": {"line-color": "#a9e1ff"}
        });


        this.map.setLayoutProperty("ytkakou", "visibility", "visible");

        this.map.addSource("volumeSource", {
          type: "geojson",
          data: require("../assets/data/yantai/ytVolume.json"),
        });

        this.map.addLayer({
          id: "volume-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "#5CF4F8",
            "line-width": 2,
          },
        });

        this.map.setPaintProperty("volume-layer", "line-width", {
          property: "交通量",
          type: "exponential",
          stops: [
            [1089, 3],
            [60000, 12],
          ],
        });
        this.map.setPaintProperty("volume-layer", "line-opacity", {
          property: "交通量",
          type: "exponential",
          stops: [
            [1089, 0.8],
            [58125, 1],
          ],
        });
        this.map.addLayer({
          id: "ratio-layer",
          type: "line",
          source: "volumeSource",
          layout: {
            "line-join": "round",
            "line-cap": "round",
            visibility: "none",
          },
          paint: {
            "line-color": "#4d9b30",
            "line-width": 5,
          },
        });

        this.map.setPaintProperty("ratio-layer", "line-color", {
          property: "颜色",
          type: "exponential",
          stops: [
            [1, "#24FFC5"],
            [2, "#5CF4F8"],
            [3, "#F97952"],
          ],
        });
        var st = this.$route.query.tn;
        if (st != null) {
          this.subTitle = st;
        }
        if (this.subTitle == "0") {
          this.$router.push(this.$route.path + "?tn=" + "1");
          this.subTitle = "1";
        }
        if (st != "1") {
          this.removeTollLayer();
          this.subTitle = st;
        }
      });
      this.map.on("mousemove", this.lonLat);
      this.map.on("mousemove", "volume-layer", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        const volume = e.features[0].properties.交通量;
        const highwayName = e.features[0].properties.高速名称;

        var titleHtml = '<div class="popup-title">' + highwayName + "</div>";


        var contentHtml =
          '<div class="popup-content">' +
          "<p>交通量: <b>" +
          volume +
          "</b> 当量数/天</p>"+
          " </div>";
        
        
        var htmlPopup =
          '<div class="volumePopup">' + titleHtml + contentHtml + "</div>";


        // var htmlPopup =
        //   '<div class="volumePopup"><p><b>' +
        //   highwayName +
        //   "</b></p><p><b>交通量:</b>  " +
        //   volume +
        //   " pcu/d </p></div>";
        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        // while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        //   coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        // }

        this.tollPopup
          .setLngLat(this.toolcoord)
          .setHTML(htmlPopup)
          .addTo(this.map);
      });
      this.map.on("mousemove", "yt-districts", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        
        var centroid = e.features[0].properties.centroid;
        centroid = centroid.split(":")[1].split(")")[0];
        // console.log(centroid);
        const coordinates = this.toolcoord;

        const vratio = e.features[0].properties.travelnum * 100;
        const districtName = e.features[0].properties.name;

         var titleHtml = '<div class="popup-title">' + districtName + "</div>";

        var contentHtml =
          '<div class="popup-content">' +
          "<p>出行量占比: <b>" +
          vratio +
          "</b> %</p>"+
          " </div>";
        
        
        var htmlPopup =
          '<div class="volumePopup">' + titleHtml + contentHtml + "</div>";
        
        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
          coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        }

        this.tollPopup
          .setLngLat(coordinates)
          .setHTML(htmlPopup)
          .addTo(this.map);
      });
      this.map.on("mouseleave", "yt-districts", (e) => {
        this.map.getCanvas().style.cursor = "default";
        this.tollPopup.remove();
      });
      this.map.on("mouseleave", "ratio-layer", (e) => {
        this.map.getCanvas().style.cursor = "default";
        this.tollPopup.remove();
      });
      this.map.on("mouseenter", "volume-layer", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("mouseenter", "ratio-layer", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
      });
      this.map.on("mouseleave", "volume-layer", (e) => {
        this.map.getCanvas().style.cursor = "default";
        this.tollPopup.remove();
      });
      this.map.on("mousemove", "ratio-layer", (e) => {
        // Copy coordinates array.

        // const coordinates = e.features[0].geometry.coordinates.slice()[0];
        this.map.getCanvas().style.cursor = "pointer";
        const ratio = e.features[0].properties.拥挤度.toFixed(2);
        const highwayName = e.features[0].properties.高速名称;

        var titleHtml = '<div class="popup-title">' + highwayName + "</div>";


        var contentHtml =
          '<div class="popup-content">' +
          "<p>拥挤度: <b>" +
          ratio +
          "</b></p>"+
          " </div>";
        
        var htmlPopup =
          '<div class="volumePopup">' + titleHtml + contentHtml + "</div>";
        
        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        // while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        //   coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        // }

        this.tollPopup
          .setLngLat(this.toolcoord)
          .setHTML(htmlPopup)
          .addTo(this.map);
      });
    },
    animate(arcs, points) {
      var routes = arcs.features;
      var ps = points.features;
      var steps = routes[0].geometry.coordinates.length - 1;

      routes.forEach((element, index) => {
        // console.log(element,index)
        const start =
          element.geometry.coordinates[
            this.counter >= steps ? this.counter - 1 : this.counter
          ];
        const end =
          element.geometry.coordinates[
            this.counter >= steps ? this.counter : this.counter + 1
          ];
        // console.log(start,end)
        if (!start || !end) return;
        ps[index].geometry.coordinates =
          routes[index].geometry.coordinates[this.counter];

        // Calculate the bearing to ensure the icon is rotated to match the route arc
        // The bearing is calculated between the current point and the next point, except
        // at the end of the arc, which uses the previous point and the current point
        ps[index].properties.bearing = turf.bearing(
          turf.point(start),
          turf.point(end)
        );
      });
      points.features = ps;

      this.map.getSource("point").setData(points);
      this.counter = this.counter + 1;
      // Request the next frame of animation as long as the end has not been reached
      if (this.counter < steps) {
        this.animation = requestAnimationFrame(() => {
          this.animate(arcs, points);
        });
      } else {
        this.counter = 0;
        this.animation = requestAnimationFrame(() => {
          this.animate(arcs, points);
        });
      }
    },

    tollLayer() {
      this.map.setLayoutProperty(
        "yt-districts-border",
        "visibility",
        "visible"
      );
      this.map.setLayoutProperty("ytkakou", "visibility", "visible");
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [120.1, 37.385],
        zoom: 8.1,
        // bearing: 0,
        // pitch: 0,
      };
      this.map.flyTo(animationOptions);

      this.map.setLayoutProperty("yt-districts", "visibility", "none");
      this.map.setLayoutProperty("zhangqiu-buildings", "visibility", "none");
      this.map.setLayoutProperty("Jinan-Building", "visibility", "none");
      this.map.setLayoutProperty("volume-layer", "visibility", "none");
    },
    removeTollLayer(){
      this.map.setLayoutProperty("ytkakou", "visibility", "none");
    },
    chuxingfenbuGenerator() {
      var chuxingfenbu = require("../assets/data/yantai/guojing.json");
      var features = chuxingfenbu;
      var arcs = {
        type: "FeatureCollection",
        features: [],
      };
      var points = {
        type: "FeatureCollection",
        features: [],
      };
      // console.log(features);

      features.forEach((item) => {
        var origin = [item["From(Longitude)"], item["From(Latitude)"]];
        var from = item.From;
        var to = item.To;
        var destination = [item["To(Longitude)"], item["To(Latitude)"]];
        var volume = item.交通量;
        var interPoint = this.computeControlPoint(origin, destination, 0.2);

        var route = {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                From: from,
                To: to,
                交通量: volume,
              },
              geometry: {
                type: "LineString",
                coordinates: [origin, interPoint, destination],
              },
            },
          ],
        };

        var point = {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                From: from,
                To: to,
                交通量: volume,
              },
              geometry: {
                type: "Point",
                coordinates: origin,
              },
            },
          ],
        };
        var bezier = turf.bezier(route.features[0]);
        var lineDistance = turf.lineDistance(bezier, "meters");
        var arc = [];
        var steps = 300;

        // Draw an arc between the `origin` & `destination` of the two points
        for (var i = 0; i < lineDistance; i += lineDistance / steps) {
          var segment = turf.along(bezier, i, "meters");

          arc.push(segment.geometry.coordinates);
        }
        route.features[0].geometry.coordinates = arc;
        arcs.features.push(route.features[0]);
        points.features.push(point.features[0]);
      });
      return [arcs, points];
    },
    chuxingfenbuLayer() {
      var arcs = require("../assets/data/yantai/chuxingfenbu/arcs.json");
      var nodes = require("../assets/data/yantai/chuxingfenbu/nodes.json");
      var points = require("../assets/data/yantai/chuxingfenbu/points.json");
      // var [arcs,points] = this.chuxingfenbuGenerator()
      // var FileSaver = require("file-saver");
      // var content = JSON.stringify(arcs);
      // var blob = new Blob([content], { type: "text/plain;charset=utf-8" });
      // FileSaver.saveAs(blob, "arcs.json");
      // var content = JSON.stringify(points);
      // var blob = new Blob([content], { type: "text/plain;charset=utf-8" });
      // FileSaver.saveAs(blob, "points.json");

      this.map.addSource("nodes", {
        type: "geojson",
        data: nodes,
      });

      this.map.addLayer({
        id: "nodes",
        source: "nodes",
        type: "circle",
        paint: {
          "circle-color": "#5CF4F8",
        },
      });
      this.map.addLayer({
        id: "nodesText",
        source: "nodes",
        type: "symbol",
        layout: {
          "text-field": ["get", "地区"],
          "text-anchor": "bottom-left",
        },
        paint: {
          "text-color": "azure",
          "text-halo-color": "#000",
          "text-halo-width": 2,
        },
      });
      this.map.setPaintProperty("nodes", "circle-radius", {
        property: "交通量",
        type: "exponential",
        stops: [
          [10000, 5],
          [130000, 15],
        ],
      });

      this.map.addSource("route", {
        type: "geojson",
        data: arcs,
      });
      this.map.addSource("point", {
        type: "geojson",
        data: points,
      });

      this.map.addLayer({
        id: "route",
        source: "route",
        type: "line",
        paint: {
          "line-width": 10,
          "line-blur": 3,
          "line-opacity": 0.4,

          "line-color": "#5CF4F8",
        },
      });

      this.map.addLayer({
        id: "route2",
        source: "route",
        type: "line",
        paint: {
          "line-width": 8,
          "line-blur": 3,
          "line-opacity": 0.4,

          "line-color": "red",
        },
      });

      this.map.addLayer({
        id: "route3",
        source: "route",
        type: "line",
        paint: {
          "line-width": 3,
          "line-blur": 1,
          "line-opacity": 0.8,

          "line-color": "#5CF4F8",
        },
      });
      this.map.addLayer({
        id: "point",
        source: "point",
