      };
      this.map.flyTo(animationOptions);
    },
    changeshowcontent(val) {
      this.globalShowPanelContent = val;
      console.log(this.globalShowPanelContent);
    },
    computeControlPoint(ps, pe, arc = 0.2) {
      const deltaX = pe[0] - ps[0];
      const deltaY = pe[1] - ps[1];
      const theta = Math.atan(deltaY / deltaX);
      const len = (Math.sqrt(deltaX * deltaX + deltaY * deltaY) / 2) * arc;
      const newTheta = theta - Math.PI / 2;
      return [
        (ps[0] + pe[0]) / 2 + len * Math.cos(newTheta),
        (ps[1] + pe[1]) / 2 + len * Math.sin(newTheta),
      ];
    },
    changeTitleByValue(val) {
      this.subTitle = val;
    },
  },
  mounted() {
    // this.initMap(0);
    this.initChart();
    // this.tollLayer();
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart.dispose();
    this.chart = null;
  },
};
