<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>高速公路网交通荷载分布情况</h1>
            <div class="next">
              <el-button :disabled="true" @click="previousPage">
                &lt;
              </el-button>
              <el-button :disabled="false" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <div class="pmain">
                <el-scrollbar style="height: 100%">
                  <p>
                    2021年受京台、京沪高速施工影响以及高速公路收费费率影响，山东省高速公路网平均交通荷载（车货总重）为<b>16.9万</b>吨/公里/天。
                    其中，<b>69%</b>的路段荷载在20万吨/天以下，<b>23%</b>的交通荷载在20~40万吨/天，<b>8%</b>的交通荷载在40万吨以上。
                  </p>
                  <div id="chart51"></div>

                  <div id="chart52"></div>
                </el-scrollbar>
              </div>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
    };
  },
  props: ["showPC"],

  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart51"));
      this.chart1.setOption({
        tooltip: {
          show: true,
          formatter: function (params) {
            return (
              params.name + "</br>" + (params.value * 100).toFixed(1) + " %"
            );
          },
        },
        series: [
          {
            type: "treemap",
            label: {
              fontSize: 12,
              color: "#42AFF6",
            },
            breadcrumb: {
              show: false,
            },
            roam: false, //是否开启拖拽漫游（移动和缩放）
            top: 20,
            left: 10,
            right: 10,
            bottom: 20,
            // width: "90%",
            nodeClick: false,

            data: [
              {
                name: "高速公路交通荷载",
                value: 1,
                itemStyle: {
                  color: "#5CF4F8",
                  gapWidth:1,
                  borderWidth: 1,
                  borderColor: "#42AFF6",
                },
                children: [
                  {
                    name: "10万吨以下",
                    itemStyle: {
                      color: "#5CF4F8",
                      borderWidth: 1,
                      borderColor: "#42AFF6",
                    },
                    value: 0.473,
                  },
                  {
                    name: "10~20万吨",
                    itemStyle: {
                      color: "#5CF4F8",
                      borderWidth: 1,
                      borderColor: "#42AFF6",
                    },
                    value: 0.211,
                  },
                  {
                    name: "20~40万吨",
                    itemStyle: {
                      color: "#5CF4F8",
                      borderWidth: 1,
                      borderColor: "#42AFF6",
                    },
                    value: 0.233,
                  },
                  {
                    name: "40~60\n万吨",
                    itemStyle: {
                      color: "#5CF4F8",
                      borderWidth: 1,
                      borderColor: "#42AFF6",
                    },
                    value: 0.036,
                  },
                  {
                    name: "60万吨以上",
                    itemStyle: {
                      color: "#5CF4F8",
                      borderWidth: 1,
                      borderColor: "#42AFF6",
                    },
                    value: 0.046,
                  },
                ],
              },
            ],
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart52"));

      this.chart2.setOption({
        title: {
          show: true,
          text: "分车型平均荷载",
          x: "center",
          y: "top",
          textStyle: {
            fontSize: 15,
            color: "azure",
          },
        },
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            // console.log(params)
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              params.name +
              ": <b>" +
              params.value +
              "</b> 吨"
            );
          },
        },
        legend: {
          show: false,
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 15,
            color: "#fff",
          },
        },
        grid: {
          x: 75,
          y: 25,
          x2: 30,
          y2: 45,
        },
        yAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 15,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            data: [
              "6型货车",
              "5型货车",
              "4型货车",
              "3型货车",
              "2型货车",
              "1型货车",
            ],
          },
        ],
        xAxis: [
          {
            type: "value",
            name: "荷载（吨）",
            nameLocation: "middle",
            nameTextStyle: {
              fontSize: 15,
              color: "azure",
              padding: [10, 0],
            },
            axisTick: {
              show: true,
            },
            axisLine: {
              lineStyle: {
                color: "azure",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 15,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "平均荷载",
            type: "bar",
            smooth: true,
            barWidth: 10,
            data: [39.3, 32.6, 26.9, 17.1, 11.6, 3.9],
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            label: {
              show: true,
              position: "right",
              textStyle: {
                fontSize: 15,
                color: "azure",
              },
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6-2");
        this.$emit("subTitle", "6-2");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$emit("subTitle", 7);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>
.el-scrollbar__wrap {
  overflow-x: hidden;
  overflow-y: hidden;
}
.pmain {
  height: 650px;
}

#chart51 {
  width: calc(100%-10px);
  height: 200px;
}
#chart52 {
  width: calc(100%-20px);
  height: 280px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>