<template>
  <div class="float" id="box">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1><span id="channelTitle"></span></h1>
            <div class="next">
              <el-button :disabled="false" @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent" class="pmain">
              <el-scrollbar>
                <p>
                  <span id="channelTitle2"></span>由青银高速和青兰高速组成，2021年交通量为86413 Pcu/d
                </p>
                <div id="chart-channel-beibu-1"></div>
                <div id="chart-channel-beibu-2"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img @click="hideContentFunction" src="../img/up.png" alt="" style="height: 20px" />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img @click="showContentFunction" src="../img/down.png" alt="" style="height: 20px" />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { markRaw } from "vue";

export default {
  name: "leftPanel",
  data: function () {
    return {
      show: false,
      showContent: true,
      channel: null,
      chart1: null,

      chart2: null,
    };
  },
  props: ["showPC"],
  methods: {
    initText() {
      this.channel = "济青通道";
      var t1 = document.getElementById("channelTitle");
      var t2 = document.getElementById("channelTitle2");
      t1.innerHTML = this.channel;
      t2.innerHTML = this.channel;

    },
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart-channel-beibu-1"));
      var table = require("../assets/data/tongdaojiaotongliang.json");
      var year = [];
      var km = [];
      var ch = this.channel;
      table.forEach((item) => {
        if (item[ch] != "") {
          year.push(item.年份);
          km.push(item[ch]);
        }
      });
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " pcu/d</b>"
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 45,
          y: 25,
          x2: 20,
          y2: 25,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
            },
            axisLabel: {
              rotate: 45,
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 1,
            },
            data: year,
          },
        ],
        yAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "交通量",
            type: "bar",
            smooth: true,
            barwidth: "15",
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: km,
            label: {
              show: true,
              position: "top",
              color: "azure",
              formatter: function (params) {
                var index = params.dataIndex
                if (index % 3 == 0) {
                  return params.value
                } else {
                  return ""
                }
              }
            }
          },
        ],
      });
    },
    initChart2() {
      document.getElementById("chart-channel-beibu-2").style.height = "300px";
      this.chart2 = markRaw(
        echarts.init(document.getElementById("chart-channel-beibu-2"))
      );
      var ch = this.channel;
      var tongdaobili = require("../assets/data/tongdaobili.json")
      var data1 = tongdaobili[ch]

      var datapie = [
        { value: data1.小货, name: "小货" },
        { value: data1.中货, name: "中货" },
        { value: data1.大货, name: "大货" },
        { value: data1.特大货, name: "特大货" },
        { value: data1.集装箱, name: "集装箱" },
        { value: data1.小客, name: "小客" },
        { value: data1.大客, name: "大客" },
      ];

      //
      this.chart2.setOption({
        tooltip: {
          trigger: "item",
          formatter: function (params) {
            return (
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b class='black'>" +
              (params.value * 100).toFixed(2) + "%</b>  "
            );
          },
        },
        legend: {
          top: "10%",
          show:false,
          left: "center",
          itemWidth: 30,
          itemGap: 30,
          textStyle: {
            color: "#fff",
            fontSize: 15,
          },
        },
        title: {
          show: true,
          text: ch + "车型比例",
          x: 'center',
          y: 'top',
          textStyle: {
            fontSize: 16,
            fontStyle: 'normal',
            fontWeight: 'normal',
            color: "azure",
          },
        },
        label: {
          show: true,
          fontSize: 15,
          position: "outside",
          color: "#fff",
          formatter: function (params) {
            return (
              params.name +
              "：" +
              (params.value * 100).toFixed(2) +
              " %"
            );
          },
        },
        labelLine: {
          show: true,
        },
        series: [
          {
            type: "pie",
            radius: ["0%", "50%"],
            center: ["50%", "50%"],
            avoidLabelOverlap: true,
            itemStyle: {
              borderRadius: 20,
              borderColor: "#ffffff00",
              borderWidth: 5,
            },
            color: [
              "#24FFC5",
              "#24FFC5F0",
              "#24FFC5E0",
              "#24FFC5D0",
              "#24FFC5C0",
              "#24FFC5B0",
            ],
            emphasis: {
              label: {
                show: true,
                fontSize: "16",
                fontWeight: "bold",
              },
            },
            data: datapie,
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "channel-lunan");
        this.$emit("subTitle", "channel-lunan");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "channel-beibu");
        this.$emit("subTitle", "channel-beibu");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initText();
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    this.chart1.dispose()
    this.chart2.dispose()
    this.chart1 = null;
    this.chart2 = null;
    // window.removeEventListener('resize', this.__resizeHandler)

  },
};
</script>


<style scoped>
div.float {
  position: relative;
  /* width: 550px; */
  /* max-height: 730px;
  background: rgba(255, 255, 255, 0.1);
  /* box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.5); */
  /* border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 20px;
  backdrop-filter: blur(10px);  */
}

.pmain {
  height: 625px;
}

b.green {
  color: #24FFC5;
}


/* #chart1-3-1 {
  width: calc(100%-20px);
  height: 250px;
}
#chart1-3-2 {
  width: calc(100%-20px);
  height: 280px;
} */

#chart-channel-beibu-1 {
  width: calc(100%-20px);
  height: 250px;
}

#chart-channel-beibu-2 {
  margin-top: 10px;
  width: calc(100%-20px);
  height: 250px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}

.fade-leave-active {
  transition: all 0.3s;
}
</style>