<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>烟台市公路网拥挤度分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  2019年烟台市公路网平均拥挤度为<b class="green">0.37</b
                  >，低于全省平均水平<b class="red">0.76</b>。
                </p>

                <div id="chart41"></div>

                <div id="chart42"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["沈海高速", "日照至鲁苏界", 0.9],
        ["济广高速", "孔村枢纽至东平湖枢纽", 0.83],
        ["长深高速", "于家庄枢纽至临朐立交", 0.82],
        ["青银高速", "青岛东收费站至夏庄枢纽", 0.81],
        ["长深高速", "无棣至鲁冀界", 0.8],
        ["青银高速", "崔寨枢纽至机场枢纽", 0.79],
        ["济广高速", "定陶枢纽至鲁豫界", 0.77],
        ["德上高速", "聊城西枢纽至李集枢纽", 0.75],
      ],
    };
  },
  props:["showPC"],
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart41"));
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter:function(params){
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              (params.value*100).toFixed(1) +
              "%"
            );
          
          }
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        series: [
          {
            name: "路段占比",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 5,
              borderColor: "#fff",
              borderWidth: 1,
            },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              {
                value: 0.818,
                name: "畅通路段",
                itemStyle: { color: "#24FFC5" },
              },
              {
                value: 0.154,
                name: "缓行路段",
                itemStyle: { color: "#5CF4F8" },
              },
              {
                value: 0.028,
                name: "拥挤路段",
                itemStyle: { color: "#F97952" },
              },
            ],
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart42"));
      this.chart2.setOption({
        tooltip: {
          show: true,
          trigger: "item",
        },
        yAxis: {
          type: "value",
          name: "平均拥挤度",
          axisTick: {
            show: true,
          },
        },
        grid: {
          x: 45,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        xAxis: {
          type: "category",
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
          textStyle: {
            fontSize: 6,
          },
          data: ["高速公路", "国道", "省道"],
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        series: [
          {
            name: "烟台市",
            type: "bar",
            itemStyle: {
              color: "#5CF4F8",
            },
            data: [0.25, 0.45, 0.39],
          },
          {
            name: "全省平均值",
            type: "bar",
            itemStyle: {
              color: "#F97952",
            },
            data: [0.64, 0.90, 0.78],
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-1");
        this.$emit("subTitle", '5-1');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "7");
        this.$emit("subTitle", '7');
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  max-height: 650px;
}

.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}



#chart41 {
  width: calc(100%-20px);
  height: 200px;
}
#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>