<template>
  <div class="float" id="box">
    <transition name="fade">
      <div>
        <el-container>
          <el-header class="bl">
            <h1>主线交通分布情况</h1>
            <div class="next"></div>
          </el-header>
          <el-main class="bl">
            <div>
              <el-scrollbar v-show="showContent" class="pmain">
                <p>
                  根据2021年高速公路联网收费数据，本项目主线交通出行分布情况如下表所示，其中不经过济南、济南北、天桥收费站的交通量约占30%。
                </p>
                <div id="chart3-1-2"></div>

                <div id="chart3-1-1"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data: function () {
    return {
      chart1: null,
      chart2: null,
      showContent: true,
    };
  },
  methods: {
    initChart1() {
      var table = require("../../assets/data/jinan/3-1-1.json");
      var origin = [];
      var destination = [];
      table.forEach((item) => {
        origin.push(item.起点);
        destination.push(item.终点);
      });
      this.chart1 = echarts.init(document.getElementById("chart3-1-1"));
      var option = {
        tooltip: {
          trigger: "item",
          axisPointer: {
            lineStyle: {},
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.起点 +
              " - " +
              params.data.终点 +
              "</b></br>" +
              params.marker +
              "交通量：&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/d</br>" +
              params.marker +
              "比重：&nbsp;" +
              (params.data.比重 * 100).toFixed(1) +
              "%"
            );
          },
        },
        grid: {
          x: 220,
          y: 25,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "交通量(Pcu/d)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "#fff",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 12,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
              lineStyle: {
                color: "azure",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            nameTextStyle: {
              color: "#fff",
              padding: [0, 0, 0, -60],
            },
            name: "终点",
            nameGap: 5,
            type: "category",
            data: destination,
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 13.5,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
          },
          {
            type: "category",
            nameGap: 5,
            name: "起点",
            nameTextStyle: {
              color: "#fff",
              padding: [0, 0, 0, -60],
            },
            nameLocation: "start",
            position: "left",
            offset: 110,
            data: origin,
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 13.5,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: {
          type: "bar",
          name: "交通量",
          barWidth:"40%",
          itemStyle: {
            color: function (params) {
              var type = params.data.类型;
              if (type == "内部出行") {
                return "#24FFC5";
              }
              if (type == "对外出行") {
                return "#F97952";
              }
              if (type == "过境出行") {
                return "#5CF4F8";
              }
            },
          },
          label: {
            show: true,
            position: "right",
            textStyle: {
              color:"#fff",
              fontSize:12,
            },
          },
          data: table,
        },
      };
      this.chart1.setOption(option);
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart3-1-2"));
      var option = {
        title: {
          text: "出行类型占比",
          textStyle: {
            fontSize: 16,
            color: "#fff",
          },
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: function (params) {
            return (
              params.marker +
              "  " +
              params.name +
              "  " +
              (params.value * 100).toFixed(1) +
              "%"
            );
          },
        },
        legend: {
          textStyle: {
            color: "#fff",
          },
          top: "bottom",
        },
        series: [
          {
            name: "天桥收费站车型占比",
            type: "pie",
            radius: ["20%", "48%"],
            avoidLabelOverlap: true,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: true,
              // position:"inner",
              textStyle:{
                color:"#fff",
                fontSize:14,
              },
              formatter:function(params){
                return params.name+" "+(params.value*100).toFixed(1)+"%"
              }
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "18",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: true,
            },
            color:[
            "#24FFC5",
            "#F97952",
            "#5CF4F8"
            ],
            data: [
              { value: 0.25, name: "内部出行"},
              { value: 0.472, name: "对外出行"},
              { value: 0.278, name: "过境出行"},
            ],
          },
        ],
      };
      this.chart2.setOption(option);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style["max-height"] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style["max-height"] = "90px";
    },
  },
  mounted() {
    this.initChart1();
    this.initChart2();
  },
  beforeUnmount() {
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>

<style scoped>
#chart3-1-1 {
  width: calc(100%-20px);
  height: 600px;
}

#chart3-1-2 {
  width: calc(100%-20px);
  height: 320px;
}

#box {
  max-height: 730px;
}
.pmain {
  height: 625px;
}
</style>