<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1><span id="sfztitle"></span>收费站交通量溯源</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent&&isClicked">
              <el-scrollbar class="pmain">
                <p>
                  2021年泰新高速全线平均汽车交通量为 34102
                  Pcu/d。其中，<b>长途过境出行</b>占<b>54.1%</b>；泰安市与周边地区之间的<b
                    class="green"
                    >对外出行</b
                  >占<b class="green">22.6%</b>；泰安市区与新泰市之间的<b
                    class="yellow"
                    >内部出行</b
                  >占<b class="yellow">23.3%</b>。
                </p>
                <div id="chart4-2-1"></div>
                <p>
                  通过交通朔源分析，可以看出泰新高速主交通流的来源是河北石家庄、邯郸方向、济南、德州地区与临沂市、江苏省及以远地区之间的交通出行，主交通流是方向是西北与东南。
                </p>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { markRaw } from "vue";

export default {
  name: "Panel41",
  data: function () {
    return {
      showContent: true,
      isClicked:true,
      show: false,
      chart1: null,
      chart2: null,
      sfzName: null,
      sfzId: null,
    };
  },
  props: ["showPC", "clicked"],
  watch: {
    clicked(val){
      this.isClicked = val
    }
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart4-2-1"));
      var table = require("../assets/data/4-2-1.json");
      var region = [];
      var direction = [];
      var dataTable = [];
      table.forEach((item) => {
        region.push(item.地区);
        direction.push(item.方向);
        dataTable.push({
          地区: item.地区,
          方向: item.方向,
          类型: item.类型,
          value: item.交通量,
          比重: item.比重,
        });
      });

      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.地区 +
              " - " +
              params.data.方向 +
              "</b></br>" +
              params.marker +
              "交通量：&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/d</br>" +
              params.marker +
              "比重：&nbsp;" +
              params.data.比重
            );
          },
        },
        title: {
          text: "泰新高速交通量构成情况",
          left: "center",
          textStyle: { fontSize: 15, color: "azure" },
          textStyle: {
            color: "azure",
          },
        },
        grid: {
          x: 160,
          y: 55,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "交通量(Pcu/d)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
              lineStyle: {
                color: "grey",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            name: "方向",
            nameGap: 5,
            type: "category",
            data: direction,
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -40],
            },
          },
          {
            type: "category",
            nameGap: 5,
            name: "地区",
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
            nameLocation: "start",
            position: "left",
            offset: 80,
            data: region,
            splitArea: {
              show: true,
              areaStyle: {
                color: [
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                ],
              },
            },
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: {
          type: "bar",
          name: "交通量",
          itemStyle: {
            color: function (params) {
              var type = params.data.类型;
              if (type == "内部") {
                return "#87D1FC";
              }
              if (type == "对外") {
                return "#32CD31";
              }
              if (type == "过境") {
                return "#ffff00";
              }
            },
          },
          label: {
            show: true,
            position: "right",
            textStyle: {
              color: "azure",
            },
          },
          data: dataTable,
        },
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-2");
        this.$emit("subTitle", "5-2");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "4-1");
        this.$emit("subTitle", "4-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
      this.isClicked = this.clicked;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  height: 650px;
}
#chart4-2-1 {
  width: calc(100%-20px);
  height: 550px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>