<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>过境出行</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button :disabled='false' @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  山东省高速公路过境出行，<b class="red">河北沧州方向</b>与<b
                    class="red"
                    >江苏连云港方向</b
                  >占比最高，通道包括京台高速、京沪高速和长深高速。
                </p>
                <div id="chart241"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      isDisable:  true,
    };
  },
  props: ["showPC"],

  methods: {
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-4");
        this.$emit("subTitle", '5-4');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-2");
        this.$emit("subTitle", '5-2');
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart241"));
      var tabledata = [
        {
          To: "江苏连云港方向",
          From: "河北沧州方向",
          value: 26943,
          比重: "31.4%",
          index: 1,
        },
        {
          To: "江苏新沂方向",
          From: "河北沧州方向",
          value: 4569,
          比重: "5.3%",
          index: 2,
        },
        {
          To: "江苏徐州方向",
          From: "河北沧州方向",
          value: 8868,
          比重: "10.3%",
          index: 3,
        },
        {
          To: "河南濮阳方向",
          From: "河北沧州方向",
          value: 3272,
          比重: "3.8%",
          index: 4,
        },
        {
          To: "江苏连云港方向",
          From: "河北石家庄方向",
          value: 2256,
          比重: "2.6%",
          index: 5,
        },
        {
          To: "江苏新沂方向",
          From: "河北石家庄方向",
          value: 995,
          比重: "1.2%",
          index: 6,
        },
        {
          To: "江苏徐州方向",
          From: "河北石家庄方向",
          value: 7958,
          比重: "9.3%",
          index: 7,
        },
        {
          To: "河南濮阳方向",
          From: "河北石家庄方向",
          value: 2665,
          比重: "3.1%",
          index: 8,
        },
        {
          To: "江苏连云港方向",
          From: "河北邯郸方向",
          value: 1117,
          比重: "1.3%",
          index: 9,
        },
        {
          To: "江苏徐州方向",
          From: "河北邯郸方向",
          value: 3952,
          比重: "4.6%",
          index: 10,
        },
        {
          To: "河南濮阳方向",
          From: "河北邯郸方向",
          value: 3146,
          比重: "3.7%",
          index: 11,
        },
        {
          To: "河南濮阳方向",
          From: "江苏徐州方向",
          value: 1068,
          比重: "1.2%",
          index: 12,
        },
        {
          To: "河南开封方向",
          From: "河南濮阳方向",
          value: 2268,
          比重: "2.6%",
          index: 13,
        },
        {
          To: "河南商丘方向",
          From: "河南濮阳方向",
          value: 16692,
          比重: "19.5%",
          index: 14,
        },
      ];
      var toScatterData = function (data) {
        var scatterData = [];
        data.forEach((item) => {
          scatterData.push([item.value, item.index-1, item]);
        });
        return scatterData;
      };
      var tos = [];
      var froms = [];
      tabledata.forEach((item) => {
        tos.push(item.To);
        froms.push(item.From);
      });

      var scatterData = toScatterData(tabledata);
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.From +
              " - " +
              params.data.To +
              "</b></br>" +
              params.marker +
              "交通量：&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/d</br>" +
              params.marker +
              "比重：&nbsp;" +
              params.data.比重
            );
          },
        },
        grid: {
          x: 160,
          y: 25,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "交通量(Pcu/d)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            max: 35000,
            axisTick: {
              show: false,
              lineStyle: {
                color: "grey",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            name: "终点",
            nameGap: 5,
            type: "category",
            data: tos,
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
          },
          {
            type: "category",
            nameGap: 5,
            name: "起点",
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
            nameLocation: "start",
            position: "left",
            offset: 80,
            data: froms,
            splitArea: {
              show: true,
              areaStyle: {
                color: [
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                ],
              },
            },
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "交通量",
            itemStyle: {
              color: "#F97952",
              opacity: 0.8,
            },
            barWidth: 2.5,
            label: {
              show: true,
              confine:true,
              position: "right",
              textStyle: {
                color: "azure",
                padding: [0, 0, 0, 10],
              },
            },

            data: tabledata,
          },
          {
            type: "scatter",
            symbolSize: function (val) {
              return val[0] / 1000;
            },
            tooltip: {
              formatter: function (params) {
                return (
                  "<b>" +
                  params.data[2].From +
                  " - " +
                  params.data[2].To +
                  "</b></br>" +
                  params.marker +
                  "交通量：&nbsp;" +
                  params.data[2].value +
                  "&nbsp; Pcu/d</br>" +
                  params.marker +
                  "比重：&nbsp;" +
                  params.data[2].比重
                );
              },
            },
            itemStyle: {
              color: "#F97952",
              opacity: 1,
            },
            data: scatterData,
          },
        ],
      });
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>



#chart241 {
  width: calc(100%-20px);
  height: 350px;
}
#chart12 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
.pmain {
  max-height: 650px;
}
</style>