                    >
                      <el-table-column prop="车型" label="车型" width="180">
                        <template #default="scope">
                        <el-form-item :prop="'detail.'+scope.$index+'.车型'" >
                          <span>{{scope.row.车型}}</span>
                        </el-form-item>
                        </template>
                      </el-table-column>
                      <el-table-column prop="originalPrice" label="原价（元/公里）" width="180">
                        <template #default="scope">
                          <el-form-item :prop="'detail.'+scope.$index+'.originalPrice'" :rules="rules.name" >
                            <el-input disabled v-model="scope.row.originalPrice" placeholder="请输入价格">
                            </el-input>
                          </el-form-item>
                        </template>
                      </el-table-column>

                      <el-table-column prop="adjustPrice" label="调整价（元/公里）">
                        <template #default="scope">
                          <el-form-item :prop="'detail.'+scope.$index+'.adjustPrice'" :rules="rules.address" >
                            <el-input v-model="scope.row.adjustPrice" placeholder="请输入价格">
                            </el-input>
                          </el-form-item>
                        </template>

                      </el-table-column>

                    </el-table>
                  </el-form>
                  <div style="text-align:right">
                    <el-button type="lightblue">
                    确定
                  </el-button>

                  </div>
                  
                  </div> -->
                  <div class="select">
                    <template
                      v-for="(item, index) in formselect.detail"
                      :key="index"
                    >
                      <div class="title">
                        <span>{{ item.region1 }}</span
                        ><span>-</span><span> {{ item.region2 }} </span
                        ><span>-</span><span>{{ item.type }}</span>
                      </div>
                      <div class="slider-price">
                        <div class="slider-div">
                          <el-slider
                            @input="updateChart(item, index)"
                            :min="0"
                            :max="0.5"
                            v-model="item.sale"
                            :step="0.01"
                            :format-tooltip="saletooltip"
                          />
                        </div>
                        <div :id="'description-' + index"></div>
                        <!-- <div
                          :id="'income-chart-' + index"
                          class="income-chart"
                        ></div> -->
                        <div
