                          class="income-chart"
                        ></div>
                      </div>
                    </template>
                  </div>
                </el-scrollbar>
              </div>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { ref } from "vue";
import { Check, Close } from "@element-plus/icons-vue";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      radio1: ref("开启"),
      checkAll: ref(false),
      isIndeterminate: ref(true),
      checkedTrunkType: ref(["货6"]),
      trunkTypes: ["货1", "货2", "货3", "货4", "货5", "货6"],
      checkAllCar: ref(false),
      isIndeterminateCar: ref(true),
      checkedCarType: ref([]),
      carTypes: ["客1", "客2", "客3", "客4"],
      form: {
        name: "",
        detail: [
          {
            车型: "货6",
            originalPrice: 2.3,
            adjustPrice: null,
          },
          {
            车型: "客1",
            originalPrice: 0.6,
            adjustPrice: null,
          },
        ],
      },
      rules: {
        name: [{ required: true, message: "请输入名称", trigger: "change" }],
        address: [{ required: true, message: "请输入地址", trigger: "change" }],
      },
      value2: ref(false),
      formselect: {
        name: "",
        detail: [],
      },
      rulesSelect: {
        name: [{ required: true, message: "请输入名称", trigger: "change" }],
        address: [{ required: true, message: "请输入地址", trigger: "change" }],
      },
      currentIndex1: null,
      currentIndex2: null,
      s_ids: null,
      timer: null,
    };
  },
  props: ["showPC", "selectedIds"],
  components: {
    Check,
    Close,
  },
  watch: {
    selectedIds: {
      handler: function (val, oldV) {
        this.s_ids = val;
        this.updateRegionName();
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    saletooltip(val) {
      if (val == 0) {
        return "无折扣";
      }
      if (((1 - val) * 10).toFixed(1).endsWith("0")) {
        return ((1 - val) * 10).toFixed(0) + " 折";
      }
      return ((1 - val) * 10).toFixed(1) + " 折";
    },
    updateChart(item, index) {
      var region1 = item.region1;
      if (region1 == null) {
        return;
      }
      region1 = region1.slice(0, -2);
      var region2 = item.region2;
      if (region2 == null) {
        return;
      }
      region2 = region2.slice(0, -2);
      var cyhincomeJson = require("../assets/data/cyhincome2.json");
      for (var i = 0; i < cyhincomeJson.length; i++) {
        var obj = cyhincomeJson[i];
        var jsonRegion1 = obj["region1"];
        var jsonRegion2 = obj["region2"];
        if (
          (jsonRegion1 == region1 && jsonRegion2 == region2) ||
          (jsonRegion1 == region2 && jsonRegion2 == region1)
        ) {
          var table = obj["table"];

          var data = [];
          var data2 = [];
          var minProfit = null;
          var maxProfit = null;
          var sales = [];
          var profits = [];
          var currentSale = 1 - item.sale;
          var upperSale = 1;
          var lowerSale = 1;
          var validIndex = 0;

          table.forEach((item) => {
            data.push([item["sale"], item["profit"]]);
            if (minProfit == null || item["profit"] < minProfit) {
              minProfit = item["profit"];
            }
            if (maxProfit == null || item["profit"] > maxProfit) {
              maxProfit = item["profit"];
            }
            // data2.push([item["sale"], item["G204"]]);
            sales.push(item["sale"]);
            profits.push(item["profit"]);
          });
          for (var j = 0; j < sales.length - 1; j++) {
            if (currentSale <= sales[j] && currentSale > sales[j + 1]) {
              validIndex = j;
              break;
            } else {
              continue;
            }
          }
          upperSale = sales[validIndex];
          lowerSale = sales[validIndex + 1];
          var upperProfit = data[validIndex][1];
          var lowerProfit = data[validIndex + 1][1];
          var currentProfit =
            upperProfit +
            ((lowerProfit - upperProfit) * (upperSale - currentSale)) /
              (upperSale - lowerSale);
          var descriptionObj = document.getElementById("description-" + index);
          var boldLabel = "";
          if (currentProfit >= 0) {
            boldLabel = "<b class='green'>";
          } else {
            boldLabel = "<b class='red'>";
          }

          descriptionObj.innerHTML =
            "<p>对在<b>" +
            region1 +
            "</b>区域驶入高速，并在<b>" +
            region2 +
            "</b>区域驶出高速的" +
            item.type +
            "实施通行费" +
            (currentSale * 10).toFixed(1) +
            "折优惠，" +
            "货6周期利润差约为" +
            boldLabel +
            currentProfit.toFixed(0) +
            "</b>元。</p>";

