          var colors_transcluent = ["#F9795270", "#5CF4F870"];
          // var option = {
          //   color: colors,
          //   tooltip: {
          //     trigger: "item",
          //     formatter: function (params) {
          //       return (
          //         params.seriesName +
          //         "</br>" +
          //         params.marker +
          //         '<b class="black">' +
          //         params.value[1] +
          //         " 万元</b>"
          //       );
          //     },
          //   },
          //   xAxis: {
