          //       show: false,
          //     },
          //     axisLabel: {
          //       show: true,
          //       textStyle: {
          //         fontSize: 15,
          //         color: "azure",
          //       },
          //     },
          //   },
          //   yAxis: [
          //     {
          //       type: "value",
          //       name: "收费额（万元/月）",
          //       position: "left",
          //       nameTextStyle: {
          //         color: colors[0],
          //         fontSize: 15,
          //         padding: [0, 0, 0, 40],
          //       },
          //       min: minIncome - (maxIncome - minIncome) * 0.15,
          //       axisLine: {
          //         show: true,
          //         color: colors[0],
          //       },
          //       splitLine: {
          //         show: true,
          //       },
          //       axisTick: {
          //         show: false,
          //       },
          //       axisLabel: {
          //         show: true,
          //         textStyle: {
          //           fontSize: 15,
          //           color: colors[0],
          //         },
          //       },
          //     },
          //     {
          //       type: "value",
          //       name: "G204交通量转移比重",
          //       position: "right",
          //       nameTextStyle: {
          //         fontSize: 15,
          //         color: colors[1],
          //         padding: [0, 40, 0, 0],
          //       },

          //       axisLine: {
          //         show: true,
          //         color: colors[1],
          //       },
          //       splitLine: {
          //         show: false,
          //       },
          //       axisTick: {
          //         show: false,
          //       },
          //       show: true,
          //       axisLabel: {
          //         textStyle: {
          //           fontSize: 15,
          //           color: colors[1],
          //         },
          //         formatter: function (value) {
          //           return (value * 100).toFixed(0) + " %";
          //         },
          //       },
          //     },
          //   ],
          //   series: [
          //     {
          //       name: "收费额（万元）",
          //       type: "line",
          //       data: data,
          //       markLine: {
          //         symbol: ["none", "none"],
          //         label: {
          //           show: false,
          //         },
          //         tooltip: {
          //           show: false,
          //         },
          //         data: [
          //           {
          //             name: "原收费额",
          //             yAxis: data[0][1],
          //           },
          //         ],
          //       },
          //       markPoint:{
          //         symbol:"pin",
          //         symbolSize:"25",
          //         label: {
          //           show: false,
          //         },
          //         tooltip: {
          //           show: true,
          //           formatter:function(params){
          //             const show_current_sale = params.data.coord[0];
          //             const show_current_income = params.data.coord[1];
          //             return params.marker+"折扣率 <b class='black'>"+show_current_sale.toFixed(2)+"</b></br>"+params.marker+"收费额 <b class='black'>"+show_current_income.toFixed(1)+"</b> 万元";
          //           }
          //         },
          //         data: [
          //           {
          //             name: "当前收费额",
          //             coord:[currentSale,currentIncome],
          //           },
          //         ],
          //       },
          //     },
          //     {
          //       name: "G204交通量转移比重",
          //       tooltip: {
          //         trigger: "item",
          //         formatter: function (params) {
          //           return (
          //             params.seriesName +
          //             "</br>" +
          //             params.marker +
          //             '<b class="black">' +
          //             (params.value[1] * 100).toFixed(1) +
          //             " %" +
          //             "</b>"
          //           );
          //         },
          //       },
          //       type: "line",
          //       data: data2,
          //       yAxisIndex: 1,
          //     },
          //   ],
          // };
          // this.formselect.detail[index].chart = echarts.init(
          //   document.getElementById("income-chart-" + index)
          // );
          // this.formselect.detail[index].chart.setOption(option);
          sales = sales.splice(0, sales.length - 4);
          profits = profits.splice(0, profits.length - 4);

          var profits_data = [];
          profits.forEach((item, nindex) => {
            if (nindex <= sales.indexOf(currentSale)) {
              profits_data.push({
                value: item,
                itemStyle:{
                  color: colors[1],
                }
              });
            } else {
              profits_data.push({
                value: item,
                itemStyle:{
                  color: colors_transcluent[1],
                }
              });
            }
          });

          var option2 = {
            color: colors,
            tooltip: {
              trigger: "item",
              // formatter:function(params){
              //   return (
              //     params.seriesName +
              //     "</br>" +
              //     params.marker +
              //     '<b class="black">'+
              //     params.value[1] + ' 元</b>'
              //   );
              // },
            },
            xAxis: {
              name: "折扣率",
              nameLocation: "middle",
              nameTextStyle: {
                fontSize: 15,
                color: "azure",
                padding: [5, 0],
              },
              splitLine: {
                show: false,
              },
              type: "category",
              data: sales,
              axisLine: {
                show: true,
                color: "azure",
              },
              axisTick: {
                show: false,
              },
              axisLabel: {
                show: true,
                textStyle: {
                  fontSize: 15,
                  color: "azure",
                },
                showMinLabel: true,
                showMaxLabel: true,
              },
            },
            yAxis: {
              name: "周期利润差(元/月)",
              nameTextStyle: {
                color: colors[1],
                fontSize: 15,
                padding: [0, 0, 0, 40],
              },
              axisLine: {
                show: true,
                color: colors[1],
              },
              splitLine: {
                show: true,
              },
              axisTick: {
                show: false,
              },
              axisLabel: {
                show: true,
                textStyle: {
                  fontSize: 15,
                  color: colors[1],
                },
              },
            },
            series: [
              {
                name: "周期利润差",
                type: "bar",
                data: profits_data,
                // color: colors_transcluent[1],
              },
              {
                name: "周期利润差趋势线",
                tooltip: {
                  show: false,
                },
                type: "line",
                data: profits,
                color: colors_transcluent[0],
                symbol: "none",
                lineStyle: {
                  type: "dot",
                  width: 1,
                },
                markPoint: {
                  symbol: "pin",
                  symbolSize: "25",
                  itemStyle: {
                    color: colors[0],
                  },
                  label: {
                    show: false,
                  },
                  tooltip: {
                    show: true,
                    formatter: function (params) {
                      const show_current_sale = params.data.coord[0];
                      const show_current_income = params.data.coord[1];
                      return (
                        params.marker +
                        "折扣率 <b class='black'>" +
                        sales[show_current_sale] +
                        "</b></br>" +
                        params.marker +
                        "周期利润差 <b class='black'>" +
                        show_current_income.toFixed(0) +
                        "</b> 元"
                      );
                    },
                  },
                  data: [
                    {
                      name: "当前收费额",
                      coord: [sales.indexOf(currentSale), currentProfit],
                    },
                  ],
                },
              },
            ],
          };
          this.formselect.detail[index].chart2 = echarts.init(
            document.getElementById("profit-chart-" + index)
          );
          this.formselect.detail[index].chart2.setOption(option2);
          this.$forceUpdate();
        } else {
          continue;
        }
      }

      this.$forceUpdate();
    },
    handleCheckAllChange(val) {
      this.checkedTrunkType = val ? this.trunkTypes : [];
      this.isIndeterminate = false;
      this.updateFromDetail();
    },
    handlecheckedTrunkTypeChange(value) {
      console.log(value);
      const checkedCount = value.length;
      this.checkAll = checkedCount === this.trunkTypes.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.trunkTypes.length;
      this.updateFromDetail();
    },
    handleCheckAllCarChange(val) {
      this.checkedCarType = val ? this.carTypes : [];
      this.isIndeterminateCar = false;
      this.updateFromDetail();
    },
    handlecheckedCarTypeChange(value) {
      const checkedCount = value.length;
      this.checkAllCar = checkedCount === this.carTypes.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.carTypes.length;
      this.updateFromDetail();
    },
    updateRegionName() {
      if (this.s_ids == null) {
        return;
      }
      var idlist = [];
      this.s_ids.forEach((item, index) => {
        idlist.push(item);
      });

      var sfzjson = require("../assets/data/sfzcyh.json");
      var features = sfzjson["features"];
      var xs = [];
      var ys = [];
      var names = [];
      for (var num = 0; num < features.length; num++) {
        var feature = features[num];
        var properties = feature["properties"];
        if (idlist.indexOf(properties["id"]) != -1) {
          names.push(properties["name"]);
          xs.push(properties["longitude"]);
          ys.push(properties["latitude"]);
        }
      }
      var avgx = 0;
      var avgy = 0;
      xs.forEach((item) => {
        avgx += item;
      });
      ys.forEach((item) => {
        avgy += item;
      });
      avgx = avgx / xs.length;
      avgy = avgy / ys.length;
      var minDistance = null;
      var nearestSfz = null;

      var keynodeJson = require("../assets/data/keynode.json");
      var features = keynodeJson["features"];
      var node_name = [];
      var node_x = [];
      var node_y = [];
      features.forEach((item) => {
        var properties = item["properties"];
        var noname = properties["name"];
        var geometry = item["geometry"];
        var coordinates = geometry["coordinates"];
        var longitude = coordinates[0][0];
        var latitude = coordinates[0][1];
        node_name.push(noname);
        node_x.push(longitude);
        node_y.push(latitude);
      });
      // console.log(node_x);
      // console.log(node_y);

      for (var num = 0; num < node_name.length; num++) {
        var currentD = Math.sqrt(
          Math.pow(avgx - node_x[num], 2) + Math.pow(avgy - node_y[num], 2)
        );
        // console.log(currentD, minDistance);
        if (minDistance == null || currentD < minDistance) {
          minDistance = currentD;
          nearestSfz = node_name[num];
          // console.log(nearestSfz+"区域")
        }
      }

      // console.log(nearestSfz+"区域")
      for (var i = 0; i < this.formselect.detail.length; i++) {
        if (this.formselect.detail[i].isActive1) {
          this.formselect.detail[i].region1 = nearestSfz + "区域";
        } else if (this.formselect.detail[i].isActive2) {
          this.formselect.detail[i].region2 = nearestSfz + "区域";
        } else {
          continue;
        }
      }
    },
    updateFromDetail() {
      const allChecked = this.checkedTrunkType.concat(this.checkedCarType);
      const price = {
        货1: 0.4,
        货2: 0.75,
        货3: 1.2,
        货4: 1.65,
        货5: 1.7,
        货6: 2.2,
        客1: 0.4,
        客2: 0.5,
        客3: 0.6,
        客4: 0.75,
      };
      var newDetail = [];
      allChecked.forEach((item) => {
        newDetail.push({
          车型: item,
          originalPrice: price[item],
          adjustPrice: "",
        });
      });
      this.form.detail = newDetail;
    },
    activateSelect(val) {
      this.$emit("isActivated", val === "开启" ? true : false);
    },
    changeActive1(val) {
      if (val) {
        for (var i = 0; i < this.formselect.detail.length; i++) {
          this.formselect.detail[i].isActive2 = false;
          if (i != this.currentIndex1) {
            this.formselect.detail[i].isActive1 = false;
          }
        }
        this.$emit("isRectActivated", val);
      } else {
        this.$emit("isRectActivated", val);
        return;
      }
    },
    changeActive2(val) {
      if (val) {
        for (var i = 0; i < this.formselect.detail.length; i++) {
          this.formselect.detail[i].isActive1 = false;
          if (i != this.currentIndex2) {
            this.formselect.detail[i].isActive2 = false;
          }
        }
        this.$emit("isRectActivated", val);
      } else {
        this.$emit("isRectActivated", val);
        return;
      }
    },
    deleteRow(index) {
      this.formselect.detail.splice(index, 1);
    },
    onAddItem() {
      this.formselect.detail.push({
        region1: null,
        isActive1: false,
        region2: null,
        isActive2: false,
        originalPrice: 2.2,
        sale: 0,
        adjustPrice: null,
        type: "货6",
        chart: null,
        chart2: null,
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "650px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6-3");
        this.$emit("subTitle", "6-3");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6-1");
        this.$emit("subTitle", "6-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    let that = this;
    setTimeout(() => {
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    if (this.formselect.detail.length) {
      for (var i = 0; i < this.formselect.detail.length; i++) {
        if (this.formselect.detail[i].chart) {
          this.formselect.detail[i].chart.dispose();
          this.formselect.detail[i].chart = null;
        }
        if (this.formselect.detail[i].chart2) {
          this.formselect.detail[i].chart2.dispose();
          this.formselect.detail[i].chart2 = null;
        }
      }
    }
    // window.removeEventListener('resize', this.__resizeHandler)
  },
};
</script>


