<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>基础数据来源</h1>
            <div class="next">
              <el-button :disabled="true" @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent" class="pmain">
              <el-scrollbar>
                <p>
                  烟台市域范围内共计卡口<b class="yellow">3556</b
                  >个，主要分布在城市主干路、高速公路及普通国省道的路口及断面，基本覆盖整个烟台市公路网。
                </p>
                <p>其中，福山区卡口数量最多，芝罘区卡口密度最高。</p>
                <div id="chartyt11"></div>

                <div id="chartyt12"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  props: ["showPC"],
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
    };
  },
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      var toll = "image://" + process.env.BASE_URL + "toll.png";
      var maxData = 3556;
      this.chart1 = echarts.init(document.getElementById("chartyt11"));
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
        },
        xAxis: {
          max: maxData,
          splitLine: { show: false },
          offset: 10,
          axisLine: {
            lineStyle: {
              color: "#999",
            },
          },
          axisLabel: {
            margin: 10,
          },
        },
        yAxis: {
          data: ["城市道路卡口", "高速公路卡口", "普通国省道卡口"],
          inverse: true,
          axisTick: { show: false },
          axisLine: { show: false },
          axisLabel: {
            margin: 10,
            color: "#999",
            fontSize: 15,
          },
        },
        grid: {
          top: "center",
          height: 150,
          left: 120,
          right: 70,
        },
        series: [
          {
            // current data
            type: "pictorialBar",
            symbol: toll,
            symbolRepeat: "fixed",
            symbolMargin: "5%",
            symbolClip: true,
            symbolSize: 20,
            symbolBoundingData: maxData,
            data: [2774, 284, 498],
            markLine: {
              symbol: "none",
              label: {
                formatter: "max: {c}",
                position: "start",
              },
              lineStyle: {
                color: "green",
                type: "dotted",
                opacity: 0.2,
                width: 2,
              },
              data: [
                {
                  type: "max",
                },
              ],
            },
            z: 10,
          },
          {
            // full data
            type: "pictorialBar",
            itemStyle: {
              opacity: 0.2,
            },
            label: {
              show: true,
              formatter: function (params) {
                return ((params.value / maxData) * 100).toFixed(1) + " %";
              },
              position: "right",
              offset: [10, 0],
              color: "white",
              fontSize: 15,
            },
            animationDuration: 0,
            symbolRepeat: "fixed",
            symbolMargin: "5%",
            symbol: toll,
            symbolSize: 20,
            symbolBoundingData: maxData,
            data: [2774, 284, 498],
            z: 5,
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chartyt12"));
      var tolls = [];
      var highways = [];
      var volumes = [];
      var t_hs = [];
      var number = [491, 569, 656, 222, 254, 205, 310, 271, 155, 165, 258];
      var density = [
        2.74302, 1.70359, 0.78282, 0.14673, 0.23783, 0.11843, 0.34406, 0.14056,
        0.07688, 0.08639, 0.18017,
      ];
      var districts = [
        "芝罘区",
        "莱山区",
        "福山区",
        "牟平区",
        "蓬莱区",
        "莱阳市",
        "龙口市",
        "莱州市",
        "栖霞市",
        "海阳市",
        "招远市",
      ];
      this.chart2.setOption({
        title: {
          show: true,
          text: "烟台市各地区卡口数量及密度分布情况",
          textAlign: "left",
          x: "center",
          y: "top",
          offset: 5,
          textStyle: {
            fontFamily: "微软雅黑",
            fontSize: 15,
            color: "azure",
          },
        },
        tooltip: {
          //鼠标指上时的标线
          show: true,
          trigger: "item",
          confine: true,
          axisPointer: {
            type: "shadow",
          },
        },
        label: {
          show: true,
          position: "right",
          textStyle:{
            color:"azure"
          }
        },
        legend: {
          show: true,
          x: "right", //可设定图例在左、右、居中
          y: "center",
          padding: [200, 50, 0, 0],
          orient: "vertical",
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 15,
            color: "#fff",
          },
        },
        grid: {
          x: 80,
          y: 55,
          x2: 110,
          y2: 35,
        },
        yAxis: {
          type: "category",
          boundaryGap: true,
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 15,
            },
          },
          data: districts,
        },
        xAxis: [
          {
            type: "value",
            name: "卡口数量",
            position: "top",
            offset: 5,
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 15,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
          {
            type: "value",
            name: "卡口密度",
            axisTick: {
              show: true,
            },
            position: "bottom",
            offset: 5,
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#F97952",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 15,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "卡口数量",
            type: "bar",
            smooth: true,
            data: number,
            itemStyle: {
              color: "#5CF4F8",
            },
            label:{
              color:"#fff"
            },
            tooltip:{
              formatter:function(params){
                return params.name+"</br>"+params.marker+"卡口数量：<b>"+params.value+"</b> 个"
              }
            },
            
          },
          {
            name: "卡口密度",
            type: "bar",
            smooth: true,
            data: density,
            xAxisIndex: 1,
            itemStyle: {
              color: "#F97952",
            },
            label:{
              color:"#fff",
              formatter:function(params){
                return params.value.toFixed(2)
              }
            },
            tooltip:{
              formatter:function(params){
                return params.name+"</br>"+params.marker+"卡口密度：<b>"+params.value.toFixed(2)+"</b> 个/平方公里"
              }
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2");
        this.$emit("subTitle", 2);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "1");
        this.$emit("subTitle", 1);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}
.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}


b.yellow {
  color: #5CF4F8;
}

#chartyt11 {
  width: calc(100%-20px);
  height: 150px;
}
#chartyt12 {
  width: calc(100%-20px);
  height: 450px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>