<template>
  <div class="float" id="box">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>山东省高速公路通道</h1>
            <div class="next">
              <el-button :disabled="false" @click="previousPage"> &lt; </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent" class="pmain">
              <el-scrollbar>
                <p>
                  本项目主要承担胶东经济圈 (烟台、威海、潍坊、青岛地区)、东营滨州地区与鲁南经济圈.(济宁、菏泽、枣庄)、河南省地区之间的交通出行。
                </p>                
                <p>
                  2021年该部分交通量为21318 pcu/d，其中客车交通量占比38.3%，货车交通量占比61.7%。
                </p>
                <p>
                  <b class="green">东北方向</b>交通出行中，来源于青烟威地区及潍坊市南部的交通量占比48.7%:来源于潍坊、滨州、东营地区的交通量占比51.3%:
                </p>
                <p>
                  <b>西南方向</b>交通出行中，来源于济宁市交通量最多，占比33.8%。
                </p>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img @click="hideContentFunction" src="../img/up.png" alt="" style="height: 20px" />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img @click="showContentFunction" src="../img/down.png" alt="" style="height: 20px" />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,

      chart2: null,
    };
  },
  props: ["showPC"],
  methods: {
    initChartgk2() {
      this.chart2 = echarts.init(document.getElementById("chart1-3-2"));
      var lane = ["四车道", "五车道", "六车道", "八车道"];
      var percent = [
        { value: 0.692, itemStyle: { color: "#24FFC5" } },
        { value: 0.004, itemStyle: { color: "#5CF4F8" } },
        { value: 0.165, itemStyle: { color: "#5CF4F8" } },
        { value: 0.140, itemStyle: { color: "#FE9367" } },
      ];
      var option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "none",
          },
          formatter: function (params) {
            return params[0].name + ": " + params[0].value;
          },
        },
        xAxis: {
          data: lane,
          axisTick: { show: false },
          axisLine: { show: false },
          axisLabel: {
            color: "max-height",
          },
        },
        grid: {
          left: 100,
        },
        yAxis: {
          type: "value",
          offset: 50,
          splitLine: { show: false },
          axisTick: { show: true },
          axisLine: { show: true },
          axisLabel: {
            show: true,
            textStyle: {
              color: "azure",
            },
          },
        },
        color: ["#ffb248", "#f2d643", "#f2d643", "#ebdba4"],
        series: [
          {
            name: "hill",
            type: "pictorialBar",
            barCategoryGap: "-130%",
            // symbol: 'path://M0,10 L10,10 L5,0 L0,10 z',
            symbol:
              "path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z",
            itemStyle: {
              opacity: 0.5,
            },
            emphasis: {
              itemStyle: {
                opacity: 1,
              },
            },
            label: {
              position: "top",
              show: true,
              textStyle: {
                color: "azure",
                opacity: 1,
              },
              formatter: function (params) {
                return (params.value * 100).toFixed(1) + "%";
              },
            },
            data: percent,
            z: 10,
          },
        ],
      };
      this.chart2.setOption(option);
    },
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart1-3-1"));
      var table = require("../assets/data/1-3-1.json");
      var year = [];
      var km = [];
      table.forEach((item) => {
        year.push(item.年份);
        km.push(item.里程);
      });
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " km</b>"
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 45,
          y: 25,
          x2: 20,
          y2: 25,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
            },
            axisLabel: {
              rotate: 45,
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 1,
            },
            data: year,
          },
        ],
        yAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "高速公路里程",
            type: "bar",
            smooth: true,
            barwidth: "15",
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: km,
            label: {
              show: true,
              position: "top",
              color: "azure",
              formatter: function (params) {
                var index = params.dataIndex
                if (index % 3 == 1) {
                  return params.value
                } else {
                  return ""
                }
              }
            }
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "channel-3");
        this.$emit("subTitle", "channel-3");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-3");
        this.$emit("subTitle", "5-3");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)

  },
};
</script>


<style scoped>
div.float {
  position: relative;
  /* width: 550px; */
  /* max-height: 730px;
  background: rgba(255, 255, 255, 0.1);
  /* box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.5); */
  /* border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 20px;
  backdrop-filter: blur(10px);  */
}

.pmain {
  max-height: 625px;
}

b.green {
  color: #24FFC5;
}


#chart1-3-1 {
  width: calc(100%-20px);
  height: 250px;
}

#chart1-3-2 {
  width: calc(100%-20px);
  height: 280px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}

.fade-leave-active {
  transition: all 0.3s;
}
</style>