<template>
  <div>
    <div class="headerfloat">
      <img src="../img/yantai2.png" alt="" style="width: 100%" />
      <!-- <img src="../img/bjtu.png" alt="" style="width: 100%" /> -->
      <div
        id="timeDisplay"
        style="
          color: #6cf8f2;
          text-align: right;
          font-size: 15px;
          position: absolute;
          right: 50px;
          top: 10px;
          text-shadow: 0 0 10px #5cf4f8;
        "
      ></div>
      <yantai-nav-menu />
    </div>

    <div class="abs0" style="height: 950px">
      <el-row>
        <el-col :span="24">
          <ytmap
            :tab="$route.query.tn"
            v-show="$route.query.tn != 'report'"
            @st="changetabnum"
          />
          <yantaireportcontent
            :slideNo="slideNo"
            @currentSlide="resetSlideNo"
            v-if="$route.query.tn == 'report'"
          />
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import ytmap from "./yantaimap.vue";
import Yantaireportcontent from "./yantaireportcontent.vue";
import YantaiNavMenu from "./yantaiNavMenu.vue";

export default {
  name: "homepage",
  data: function () {
    return {
      tabnum: 1,
      date: new Date(),
      timer: null,
      slideNo: 1,
    };
  },
  components: {
    ytmap,
    Yantaireportcontent,
    YantaiNavMenu,
  },
  methods: {
    displayTime() {
      var vWeek, vWeek_s;
      vWeek = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();
      if(seconds<10){
        seconds = "0"+seconds;
      }
      if(minutes<10){
        minutes = "0"+minutes;
      }
      if(hours<10){
        hours = "0"+hours;
      }
      vWeek_s = date.getDay();
      document.getElementById("timeDisplay").innerHTML =
        hours +
        ":" +
        minutes +
        ":" +
        seconds +
        "<br />" +
        year +
        "年" +
        month +
        "月" +
        day +
        "日" +
        vWeek[vWeek_s];
      //   console.log("reView");
    },
    changetabnum(val) {
      this.tabnum = val;
    },
    resetSlideNo(val) {
      this.slideNo = val;
    },
  },
  mounted() {
    this.timer = setInterval(this.displayTime, 1000);
    let that = this;
    this.$nextTick(function () {
      document.onkeydown = function (e) {
        let key = window.event.keyCode;
        if (key == 27) {
          console.log("esc");
          console.log(that.tabnum);
          if (that.tabnum == "report") {
            that.$router.go(-1);
          }
        }
      };
    });
  },
  beforeUnmounted() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === "intropage") {
      console.log("clearCache");
      /* pageA是需要跳转的路由   */
      // console.log('返回管理页面')
      if (this.$vnode && this.$vnode.data.keepAlive) {
        if (
          this.$vnode.parent &&
          this.$vnode.parent.componentInstance &&
          this.$vnode.parent.componentInstance.cache
        ) {
          if (this.$vnode.componentOptions) {
            var key =
              this.$vnode.key == null
                ? this.$vnode.componentOptions.Ctor.cid +
                  (this.$vnode.componentOptions.tag
                    ? `::${this.$vnode.componentOptions.tag}`
                    : "")
                : this.$vnode.key;
            var cache = this.$vnode.parent.componentInstance.cache;
            var keys = this.$vnode.parent.componentInstance.keys;
            if (cache[key]) {
              if (keys.length) {
                var index = keys.indexOf(key);
                if (index > -1) {
                  keys.splice(index, 1);
                }
              }
              delete cache[key];
            }
          }
        }
      }
      // this.$destroy();
    }
    next();
  },
};
</script>




<style scoped lang="less">
#navmenuvertical {
  position: absolute;
  height: 600px;
  width: 100px;
  right: 10px;
  top: 30px;
}
.el-menu-demo {
  width: 100%;
}
span.center {
  text-align: center;
}
.tCenter .el-button {
  text-align: center;
  font-family: "微软雅黑";
  background: None;
  border: None;
  color: azure;
  font-size: 1rem;
}

.tCenter .el-button:hover {
  text-align: center;
  font-family: "微软雅黑";
  background: None;
  border: None;
  color: #1dbace;
  font-size: 1rem;
}

.tCenter .el-button:active {
  text-align: center;
  font-family: "微软雅黑";
  background: None;
  border: None;
  color: #1dbace;
  font-size: 1rem;
  transform: translateY(2px);
}

.el-dropdown-link {
  text-align: center;
  font-family: "微软雅黑";
  cursor: pointer;
  font-size: 1rem;
  color: azure;
}
.el-dropdown-link :hover {
  color: #1dbace;
}
.el-dropdown-link :active {
  color: #1dbace;
}

.el-icon-arrow-down {
  font-size: 12px;
}

.el-dropdown-menu__item:focus,
.el-dropdown-menu__item:not(.is-disabled):hover {
  background-color: rgba(0, 204, 255, 0.075);
  color: #1dbace;
}
</style>