<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>烟台港公路集疏运——西港区</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p><b>西港区</b
                  >西港区是烟台港规划建设发展的核心港区，地处烟台经济技术开发区东北海域，主营非金属矿石、石油、煤炭等大宗散货业务，2019年吞吐量为10803万吨，占烟台港的28.0%，位居全港第一位。
                </p>

                <div id="chart531"></div>

                <div id="chart532"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      huowufenlei: [
        {
          种类: "煤炭",
          吞吐量: 607.85,
          比重: 0.056268,
        },
        {
          种类: "石油",
          吞吐量: 2244.27,
          比重: 0.207749,
        },
        {
          种类: "金属矿石",
          吞吐量: 1626.89,
          比重: 0.150599,
        },
        {
          种类: "钢铁",
          吞吐量: 4.64,
          比重: 0.00043,
        },
        {
          种类: "矿建材料",
          吞吐量: 701.02,
          比重: 0.064892,
        },
        {
          种类: "水泥",
          吞吐量: 192.11,
          比重: 0.017783,
        },
        {
          种类: "非金属矿石",
          吞吐量: 5091.03,
          比重: 0.47127,
        },
        {
          种类: "化肥及农药",
          吞吐量: 20.68,
          比重: 0.001914,
        },
        {
          种类: "粮食",
          吞吐量: 70.58,
          比重: 0.006533,
        },
        {
          种类: "其他",
          吞吐量: 221.79,
          比重: 0.020531,
        },
        {
          种类: "集装箱",
          吞吐量: 21.93,
          比重: 0.00203,
        },
      ],
      gangquzhanbi: [
        {
          地区: "芝罘区",
          品类: "地区出行量",
          吞吐量: 268,
          比重: 0.079596,
        },
        {
          地区: "福山区",
          品类: "地区出行量",
          吞吐量: "2,310",
          比重: 0.686071,
        },
        {
          地区: "牟平区",
          品类: "地区出行量",
          吞吐量: 78,
          比重: 0.023166,
        },
        {
          地区: "莱山区",
          品类: "地区出行量",
          吞吐量: 158,
          比重: 0.046926,
        },
        {
          地区: "龙口市",
          品类: "地区出行量",
          吞吐量: 113,
          比重: 0.033561,
        },
        {
          地区: "莱阳市",
          品类: "地区出行量",
          吞吐量: 80,
          比重: 0.02376,
        },
        {
          地区: "莱州市",
          品类: "地区出行量",
          吞吐量: 77,
          比重: 0.022869,
        },
        {
          地区: "蓬莱区",
          品类: "地区出行量",
          吞吐量: 110,
          比重: 0.03267,
        },
        {
          地区: "招远市",
          品类: "地区出行量",
          吞吐量: 63,
          比重: 0.018711,
        },
        {
          地区: "栖霞市",
          品类: "地区出行量",
          吞吐量: 75,
          比重: 0.022275,
        },
        {
          地区: "海阳市",
          品类: "地区出行量",
          吞吐量: 35,
          比重: 0.010395,
        },
      ],
    };
  },
  props: ["showPC"],
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart531"));
      var table = this.huowufenlei;
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        xdata.push(table[i].种类);
        ydata.push(table[i].吞吐量);
      }
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " 万吨"
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 15,
          itemHeight: 10,
          itemGap: 20,
          top: "0px",
          textStyle: {
            fontSize: 12,
            color: "#fff",
          },
        },
        series: [
          {
            name: "吞吐量",
            type: "pie",
            radius: ["30%", "50%"],
            center: ["50%", "50%"],
            avoidLabelOverlap: true,
            label: {
              normal: {
                show: true,
                textStyle: {
                  fontSize: "15",
                  color: "#fff",
                },
              },
            },
            data: (function (xdata, ydata) {
              var arr = [];
              for (var i = 0; i < xdata.length; i++) {
                arr.push({
                  name: xdata[i],
                  value: ydata[i],
                });
              }
              return arr;
            })(xdata, ydata),
          },
        ],
      });
    },
    initChart2() {
      var table = this.gangquzhanbi;
      // 按比重排序
      table.sort(function (a, b) {
        return b.比重 - a.比重;
      });
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        ydata.push(table[i].地区);
        xdata.push(table[i].比重);
      }
      this.chart2 = echarts.init(document.getElementById("chart532"));
      this.chart2.setOption({
        tooltip: {
          show: true,
          trigger: "item",
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              (params.value * 100).toFixed(1) +
              " %"
            );
          },
        },
        grid: {
          x: 75,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        yAxis: [
          {
            type: "category",
            inverse: true,
            data: ydata,
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
          },
        ],
        xAxis: [
          {
            type: "value",
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "港区腹地占比情况",
            type: "bar",
            barWidth: "80%",
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: xdata,
            label: {
              show: true,
              position: "right",
              color: "#fff",
              formatter: function (params) {
                return (params.value * 100).toFixed(1) + "%";
              },
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-5");
        this.$emit("subTitle", "5-3");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-3");
        this.$emit("subTitle", "5-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}

.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}

#chart531 {
  width: calc(100%-20px);
  height: 350px;
}
#chart532 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>