<template>
  <div>
    <div class="headerfloat">
      <img src="../img/intro2.png" alt="" style="width: 100%" />
      <div
        id="timeDisplay"
        style="
          color: #6cf8f2;
          text-align: right;
          font-size: 15px;
          position: absolute;
          right: 50px;
          top: 10px;
          text-shadow: 0 0 10px #5cf4f8;
        "
      ></div>
      <!-- <navmenu /> -->
      <!-- <intro-nav-menu /> -->
    </div>

    <div class="abs0" style="height: 950px">
      <el-row>
        <el-col :span="24">
          <intromap />
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import intromap from "./intromap.vue";
import IntroNavMenu from "./introNavMenu.vue";
// import navmenu from "./navMenu.vue";

export default {
  name: "homepage",
  data: function () {
    return {
      tabnum: 1,
      date: new Date(),
      timer: null,
    };
  },
  components: {
    intromap,
    IntroNavMenu,
  },
  methods: {
    displayTime() {
      var vWeek, vWeek_s;
      vWeek = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      if (hours < 10) {
        hours = "0" + hours;
      }
      vWeek_s = date.getDay();
      document.getElementById("timeDisplay").innerHTML =
        hours +
        ":" +
        minutes +
        ":" +
        seconds +
        "<br />" +
        year +
        "年" +
        month +
        "月" +
        day +
        "日" +
        vWeek[vWeek_s];
      //   console.log("reView");
    },
  },
  mounted() {
    this.timer = setInterval(this.displayTime, 1000);
  },
  beforeUnmounted() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
};
</script>


