<template>
<el-scrollbar>
  <div>
    <el-scrollbar>
      <div class="headerfloat">
        <img src="../img/biaoti2.png" alt="" style="width: 100%" />
        <!-- <img src="../img/bjtu.png" alt="" style="width: 100%" /> -->
        <div
          id="timeDisplay"
          style="
            color: #6CF8F2;
            text-align: right;
            font-size: 15px;
            position: absolute;
            right: 50px;
            top: 10px;
            text-shadow: 0 0 10px #5CF4F8; 
          "
        ></div>
        <highwayNavMenu />
      </div>
      <div class="abs0" style="height: 1080px">
      <el-scrollbar>
        <el-row>
          <el-col :span="24">
            <sdmap
              :tab="$route.query.tn"
              v-show="
                $route.query.tn != 'report' && $route.query.tn != 'report2022' && $route.query.tn != 'report2018'
              "
              @st="changetabnum"
            />
            <report1
              :slideNo="slideNo"
              @currentSlide="resetSlideNo"
              v-if="$route.query.tn == 'report'"
            />
            <report2018
              :slideNo="slideNo2018"
              @currentSlide="resetSlideNo2018"
              v-if="$route.query.tn == 'report2018'"
            />
            <report2022
              :slideNo="slideNo"
              @currentSlide="resetSlideNo"
              v-if="$route.query.tn == 'report2022'"
            />
          </el-col>
        </el-row>
        </el-scrollbar>
      </div>
    </el-scrollbar>
  </div>
  </el-scrollbar>
</template>

<script>
import sdmap from "./sdmap2.vue";
import highwayNavMenu from "./highwayNavMenu3.vue";
import report1 from "./report1.vue";
import report2022 from "./report2022.vue";
import report2018 from "./report2018.vue";

export default {
  name: "homepage",
  data: function () {
    return {
      tabnum: "1-1",
      date: new Date(),
      timer: null,
      slideNo: 1,
      slideNo2018: 1,
    };
  },
  watch: {
    tn(val) {
      this.tabnum = val;
    },
  },
  components: {
    sdmap,
    highwayNavMenu,
    report1,
    report2022,
    report2018,
  },
  methods: {
    displayTime() {
      var vWeek, vWeek_s;
      vWeek = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();
      if(seconds<10){
        seconds = "0"+seconds;
      }
      if(minutes<10){
        minutes = "0"+minutes;
      }
      if(hours<10){
        hours = "0"+hours;
      }
      vWeek_s = date.getDay();
      document.getElementById("timeDisplay").innerHTML =
        hours +
        ":" +
        minutes +
        ":" +
        seconds +
        "<br />" +
        year +
        "年" +
        month +
        "月" +
        day +
        "日" +
        vWeek[vWeek_s];
      //   console.log("reView");
    },
    changetabnum(val) {
      this.tabnum = val;
    },
    resetSlideNo(val) {
      this.slideNo = val;
    },
    resetSlideNo2018(val) {
      this.slideNo2018 = val;
    },
  },
  mounted() {
    this.timer = setInterval(this.displayTime, 1000);
    let that = this;
    this.$nextTick(function () {
      document.onkeydown = function (e) {
        let key = window.event.keyCode;
        if (key == 27) {
          console.log("esc");
          console.log(that.tabnum);
          if (that.tabnum == "report") {
            that.$router.go(-1);
          }
          if (that.tabnum == "report2018") {
            that.$router.go(-1);
          }
          if (that.tabnum == "report2022") {
            that.$router.go(-1);
          }
        }
      };
    });
  },
  beforeUnmounted() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === "intropage") {
      console.log("clearCache");
      /* pageA是需要跳转的路由   */
      // console.log('返回管理页面')
      if (this.$vnode && this.$vnode.data.keepAlive) {
        if (
          this.$vnode.parent &&
          this.$vnode.parent.componentInstance &&
          this.$vnode.parent.componentInstance.cache
        ) {
          if (this.$vnode.componentOptions) {
            var key =
              this.$vnode.key == null
                ? this.$vnode.componentOptions.Ctor.cid +
                  (this.$vnode.componentOptions.tag
                    ? `::${this.$vnode.componentOptions.tag}`
                    : "")
                : this.$vnode.key;
            var cache = this.$vnode.parent.componentInstance.cache;
            var keys = this.$vnode.parent.componentInstance.keys;
            if (cache[key]) {
              if (keys.length) {
                var index = keys.indexOf(key);
                if (index > -1) {
                  keys.splice(index, 1);
                }
              }
              delete cache[key];
            }
          }
        }
      }
      // this.$destroy();
    }
    next();
  },
};
</script>




