import { createRouter, createWebHistory } from 'vue-router'
import highway from '../components/highway.vue';
import intropage from '../components/intropage.vue';
import yantai from '../components/yantai.vue';
import weifang from '../components/weifang.vue';
import qingdao from '../components/qingdao.vue';
import jinan from '../components/jinanPage.vue';
import weihai from '../components/weihai.vue';
import cityselect from '../components/cityselectpage.vue';

const routes = [
    {
        path: '/',
        name: 'index',
        component: intropage,
    },
    {
        path: '/highway',
        name: 'highway',
        component: highway,
    },
    {
        path: '/yantai',
        name: 'yantai',
        component: yantai,
    },
    {
        path: '/dataDrivenModel',
        name: 'dataDrivenModel',
        component: weifang,
    },
    {
        path: '/qingdao',
        name: 'qingdao',
        component: qingdao,
    },
    {
        path: '/jinan',
        name: 'jinan',
        component: jinan,
    },
    {
        path: '/weihai',
        name: 'weihai',
        component: weihai,
    },
    {
        path: '/cityselect',
        name: 'cityselect',
        component: cityselect,
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})


export default router