<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>潍坊市交通出行分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
            <p>
              潍坊市区交通出行主要集中在 <b class="lightblue">潍城区东部</b>
              、<b class="lightblue">奎文区西部</b>、<b class="lightblue">潍坊市高新区</b>、<b class="lightblue">潍坊市经济开发区</b>几大片区之间。高峰小时出行量为16.8万pcu/h，其中，潍城区东部与奎文区之间的出行量最大，占出行总量的13%。
            </p>
            
            <div id="chart61"></div>

              </el-scrollbar>
            </div>
            
          </el-main>
           <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
    };
  },
  props:["showPC"],
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart61"));
      var tabledata =  require("../../assets/data/weifang/qiwangxian.json")
      
      var toScatterData = function (data) {
        var scatterData = [];
        data.forEach((item,index) => {
          scatterData.push([item.交通量, index, item]);
        });
        return scatterData;
      };
      var destination = []
      var origin = []
      var volume = []
      tabledata.forEach((item)=>{
        origin.push(item.From)
        destination.push(item.To)
        volume.push({From:item.From,To:item.To,value:item.交通量,"比重":item.比重})
      })
      var scatterData = toScatterData(tabledata);
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.From +
              " - " +
              params.data.To +
              "</b></br>" +
              params.marker +
              "交通量：&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/h</br>" +
              params.marker +
              "比重：&nbsp;" +
              params.data.比重
            );
          },
        },
        grid: {
          x: 200,
          y: 25,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "交通量(Pcu/h)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            max: 25000,
            axisTick: {
              show: false,
              lineStyle: {
                color: "grey",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            name: "终点",
            nameGap: 5,
            type: "category",
            data: destination,
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
          },
          {
            type: "category",
            nameGap: 5,
            name: "起点",
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
            nameLocation: "start",
            position: "left",
            offset: 100,
            data: origin,
            splitArea: {
              show: true,
              areaStyle: {
                color: [
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.3)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.3)",
                ],
              },
            },
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "交通量",
            itemStyle: {
              color: "#5CF4F8",
              opacity:0.8,
            },
            barWidth: 2.5,
            label: {
              show: true,
              position: "right",
              textStyle: {
                color: "azure",
                padding: [0,0,0,10],
              },
            },

            data: volume,
          },
          {
            type: "scatter",
            symbolSize: function (val) {
              return val[0] / 1000;
            },
            tooltip: {
              formatter: function (params) {
                return (
                  "<b>" +
                  params.data[2].From +
                  " - " +
                  params.data[2].To +
                  "</b></br>" +
                  params.marker +
                  "交通量：&nbsp;" +
                  params.data[2].交通量 +
                  "&nbsp; Pcu/h</br>" +
                  params.marker +
                  "比重：&nbsp;" +
                  params.data[2].比重
                );
              },
            },
            itemStyle: {
              color: "#5CF4F8",
              opacity: 1,
            },
            data: scatterData,
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "compare");
        this.$emit("subTitle", 'report');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "4");
        this.$emit("subTitle", 4);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  max-height: 650px;
}
.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}


#chart61 {
  width: calc(100%-20px);
  height: 450px;
}
#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}


.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>