<template>
  <div class="float" id="box">
    <transition name="fade">
      <div>
        <el-container>
          <el-header class="bl">
            <h1>区间分布</h1>
            <div class="next"></div>
          </el-header>
          <el-main class="bl">
            <div>
              <el-scrollbar v-show="showContent" class="pmain">
                <!-- <p>收费站ID：<span id="id">未选择</span></p>
                <p>收费站名称：<span id="name">未选择</span></p>
                <p>收费站交通量：<span id="volume">未选择</span></p> -->
                <p>各路段交通量详情如下图所示：</p>
                <div style="text-align:center;margin-top: 20px">
                  <el-radio-group v-model="radio" @change="changeLabel">
                    <el-radio-button label="济南收费站"></el-radio-button>
                    <el-radio-button label="济南北收费站"></el-radio-button>
                    <el-radio-button label="天桥收费站"></el-radio-button>
                  </el-radio-group>
                </div>
                <p> </p>
                <div id="chart3-2-1"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { ref } from "vue";
export default {
  data: function () {
    return {
      chart1: null,
      showContent:true,
      radio: ref("济南收费站"),
    };
  },
  props: ["toll"],
  watch: {
    toll(val) {
      switch (val) {
        case "济南":
          this.setJinan();
          break;
        case "济南北":
          this.setJinanbei();
          break;
        case "天桥":
          this.setTianqiao();
          break;
        default:
          break;
      }
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart3-2-1"));
      var highwayTable = require("../../assets/data/jinan/jinanOrigin.json");

      var city = [];
      var volume = [];
      highwayTable.forEach((item) => {
        city.push(item["名称"]);
        volume.push(item["交通量"]);
      });

      var option = {
        tooltip: {
          show: true,
          confine: true,
        },
        grid: {
          left: 90,
        },
        title: {
          text: "济南收费站车辆来源",
          subtext: "(单位Pcu/d)",
          left: "center",
          textStyle: { fontSize: 15, color: "azure" },
          subtextStyle: { fontSize: 12, color: "azure" },
        },
        yAxis: {
          type: "category",
          data: city,
          inverse: true,
          axisLabel: {
            textStyle: {
              color: "azure",
              fontSize: 13.5,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        xAxis: [
          {
            type: "value",
            name: "车辆来源(Pcu/d)",
            position: "bottom",
            alignTicks: true,
            axisLabel: {
              textStyle: {
                color: "azure",
                fontSize: 13.5,
              },
              //不显示最后一个坐标标签
              //   showMaxLabel: true,
              //   interval: 0,
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "azure",
                type: "dashed",
              },
            },
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [5, 0],
            },
            axisTick: {
              show: true,
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "车辆来源",
            data: volume,
            itemStyle: {
              color: "#5CF4F8",
            },
            label: {
              show: true,
              position: "right",
              color: "azure",
            },
            markLine: {
              lineStyle: {
                color: "#CCE0F2",
              },
              label: {
                position: "start",
                formatter: "平均值",
                textStyle: {
                  color: "grey",
                },
              },
              symbol: ["arrow", "circle"],
              data: [{ type: "average", name: "平均值" }],
            },
          },
        ],
      };
      this.chart1.setOption(option);
    },
    changeLabel(val) {
      switch (val) {
        case "济南收费站":
          this.setJinan();
          this.$emit("ratiolabel", "济南");
          break;
        case "济南北收费站":
          this.setJinanbei();
          this.$emit("ratiolabel", "济南北");
          break;
        case "天桥收费站":
          this.setTianqiao();
          this.$emit("ratiolabel", "天桥");
          break;
        default:
          break;
      }
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    setJinan() {
      var highwayTable = require("../../assets/data/jinan/jinanOrigin.json");
      var city = [];
      var volume = [];
      highwayTable.forEach((item) => {
        city.push(item["名称"]);
        volume.push(item["交通量"]);
      });
      

      var option = {
        title: {
          text: "济南收费站车辆来源",
        },
        yAxis: {
          data: city,
        },
        series: [
          {
            data: volume,
          },
        ],
      };
      this.chart1.setOption(option);
    },
    setJinanbei() {
      var highwayTable = require("../../assets/data/jinan/jinanbeiOrigin.json");

      var city = [];
      var volume = [];
      highwayTable.forEach((item) => {
        city.push(item["名称"]);
        volume.push(item["交通量"]);
      });
      

      var option = {
        title: {
          text: "济南北收费站车辆来源",
        },
        yAxis: {
          data: city,
        },
        series: [
          {
            data: volume,
          },
        ],
      };
      this.chart1.setOption(option);
    },
    setTianqiao() {
      var highwayTable = require("../../assets/data/jinan/tianqiaoOrigin.json");

      var city = [];
      var volume = [];
      highwayTable.forEach((item) => {
        city.push(item["名称"]);
        volume.push(item["交通量"]);
      });
      var option = {
        title: {
          text: "天桥收费站车辆来源",
        },
        yAxis: {
          data: city,
        },
        series: [
          {
            data: volume,
          },
        ],
      };
      this.chart1.setOption(option);
    },
  },
  mounted() {
    this.initChart1();
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    // this.chart2 = null;
  },
};
</script>

<style lang="less" scoped>

/deep/.el-radio-button {
    --el-radio-button-checked-bg-color: #08163D;
    --el-radio-button-checked-text-color: #5CF4F8;
    --el-radio-button-checked-border-color: "#5CF4F8";
}
/deep/.el-radio-button:hover {
    color:#5CF4F8;
}
#chart3-2-1 {
  width: calc(100%-20px);
  height: 580px;
}

#box{
  height: 730px;
}
.pmain {
  height: 625px;
}
</style>