<template>
  <div class="float" id="box">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>全省高速公路概况</h1>
            <div class="next">
              <el-button  @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent" class="pmain">
              <el-scrollbar>
                <p>
                  截至2022年底山东省高速公路密度（公里/百平方公里）在全国省级行政区中排名第6位。
                </p>

                <div id="chart1-2-1"></div>
                <!-- <div id="chart1-1-2"></div> -->

                <!-- <div id="chart12"></div> -->
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "panel1-1",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,

      chart2: null,
    };
  },
  props: ["showPC"],
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart1-2-1"));
      var highwayTable = require("../assets/data/1-2-1.json");

      var city = [];
      var km = [];
      var rank = [];
      highwayTable.forEach((item, index) => {
        city.push(item["name"]);
        if (item["name"] == "山东省") {
          km.push({ value: item["value"], itemStyle: { color: "#416E97" } });
        } else {
          km.push(item["value"]);
        }
        rank.push(index + 1);
      });

      var option = {
        tooltip: {
          show: true,
          confine: true,
        },
        grid: {
          left: 90,
        },
        title: {
          text: "全国各省级行政区高速公路密度(公里/百平方公里)",
          subtext:"(不包含港澳台地区)",
          left:"center",
          textStyle: { fontSize: 15, color:"azure" },
          subtextStyle: { fontSize: 12, color:"azure" },

        },
        yAxis: {
          type: "category",
          data: city,
          inverse: true,
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 15,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        xAxis: [
          {
            type: "value",
            name: "高速公路密度(公里/百平方公里)",
            position: "bottom",
            alignTicks: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              //   showMaxLabel: true,
              //   interval: 0,
            },
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [5, 0],
            },
            axisTick: {
              show: true,
            },
            splitLine:{
              show:true,
              lineStyle:{
                color:"grey",
                type:"dashed",
              }
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "高速公路密度",
            data: km,
            itemStyle: {
              color: "#CCE0F2",
            },
            label:{
                show:true,
                position:"right",
                color:"azure",

            },
            markLine: {
              lineStyle: {
                color: "grey",
              },
              label: {
                position: "start",
                formatter: "平均值",
                textStyle: {
                  color: "grey",
                },
              },
              symbol:['arrow','circle'],
              data: [{ type: "average", name: "平均值" }],
            },
          },
        ],
      };
      this.chart1.setOption(option);
    },

    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[max - height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[max - height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path+"?tn="+"1-4");
        this.$emit("subTitle", "1-4");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path+"?tn="+"1-2");
        this.$emit("subTitle", '1-2');
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      //   this.initChartgk2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>
div.float {
  max-height: 730px;
}

.pmain {
  height: 650px;
}


#chart1-2-1 {
  width: calc(100%-20px);
  height: 600px;
}
#chart1-1-2 {
  width: calc(100%-20px);
  height: 280px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>