<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>烟台市出行量分布</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  2019年烟台市公路交通出行总量为84.2万pcu/d（不含城市内部出行），其中，<b>芝罘区</b>出行总量最高，占烟台市的15.7%，其次是福山区和莱山区，分别占烟台市的13.8%和12.7%。
                </p>
                <div id="chartyt21"></div>

                <div id="chartyt22"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  props: ["showPC"],
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
    };
  },

  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chartyt21"));
      const travel = [
        132160, 116003, 106768, 88371, 70300, 69613, 68214, 56421, 48327, 43454,
        41261,
      ];
      const districts = [
        "芝罘区",
        "福山区",
        "莱山区",
        "龙口市",
        "莱州市",
        "莱阳市",
        "蓬莱区",
        "栖霞市",
        "牟平区",
        "招远市",
        "海阳市",
      ];
      var dataSet = [];
      for (var i = 0; i < 11; i++) {
        dataSet.push({ value: travel[i], name: districts[i] });
      }
      this.chart1.setOption({
        title: {
          text: "烟台市各地区出行量排名",
          left: "center",
          textStyle: {
            color: "#fff",
          },
        },
        tooltip: {
          confine:true,
          trigger: "item",
          formatter: "{a} <br/>{b} : <b>{c} Pcu/d</b> ({d}%)",
        },
        visualMap: {
          show: false,
          min: 10000,
          max: 200000,
          inRange: {
            colorLightness: [1, 0],
          },
        },
        series: {
          name: "出行量占比",
          type: "pie",
          radius: [20, 90],
          label: {
            show: false,
          },
          // center: ["75%", "50%"],
          roseType: "area",
          itemStyle: {
            color: "#33D3EB",
            shadowBlur: 200,
            shadowColor: "rgba(0, 0, 0, 0.5)",
            borderRadius: 5,
          },
          data: dataSet,
        },
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chartyt22"));
      this.chart2.setOption({
        tooltip: {
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter:function(params){
            console.log(params)
            return params.seriesName+"</br>"+params.marker+params.name+"： "+(params.value*100).toFixed(1)+" %"
          }
        },
        emphasis: {
          focus: "series",
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "25px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 45,
          y: 55,
          x2: 20,
          y2: 25,
        },
        title: {
          text: "各区出行量GDP人口比重对比图",
          textAlign: "left",
          x: "center",
          y: "top",
          textStyle: {
            color: "azure",
          },
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#C9DD23",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            data: [
              "芝罘区",
              "福山区",
              "牟平区",
              "莱山区",
              "龙口市",
              "莱阳市",
              "莱州市",
              "招远市",
              "栖霞市",
              "海阳市",
              "蓬莱区",
            ],
          },
        ],
        yAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#C9DD23",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "出行量比重",
            type: "line",
            smooth: true,
            lineStyle: {
              normal: {
                width: 2,
              },
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#1aa3c563",
                    },
                    {
                      offset: 0.8,
                      color: "#1aa3c500",
                    },
                  ],
                  false
                ),
                shadowColor: "rgb(31,83,128)",
                shadowBlur: 10,
              },
            },
            itemStyle: {
              normal: {
                color: "#1AA3C5",
              },
            },
            data: [
              0.157166438, 0.137952317, 0.057471114, 0.126969932, 0.105091974,
              0.08278471, 0.083601699, 0.051676077, 0.067096607, 0.049068132,
              0.081121,
            ],
          },
          {
            name: "GDP比重",
            type: "line",
            smooth: true,
            lineStyle: {
              normal: {
                width: 2,
              },
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#F5B75163",
                    },
                    {
                      offset: 0.8,
                      color: "#F5B75100",
                    },
                  ],
                  false
                ),
                shadowColor: "rgb(31,83,128)",
                shadowBlur: 10,
              },
            },
            itemStyle: {
              normal: {
                color: "#F5B751",
              },
            },
            data: [
              0.124696061, 0.250066671, 0.043497927, 0.055885567, 0.140506121,
              0.057311793, 0.086759238, 0.089176371, 0.038871517, 0.056843792,
              0.056384942,
            ],
          },
          {
            name: "人口比重",
            type: "line",
            smooth: true,
            lineStyle: {
              normal: {
                width: 2,
              },
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#00B05063",
                    },
                    {
                      offset: 0.8,
                      color: "#00B05000",
                    },
                  ],
                  false
                ),
                shadowColor: "rgb(31,83,128)",
                shadowBlur: 10,
              },
            },
            itemStyle: {
              normal: {
                color: "#00B050",
              },
            },
            data: [
              0.099484114, 0.09882309, 0.065527598, 0.041529552, 0.0988662,
              0.129330785, 0.129618187, 0.081464024, 0.088663438, 0.09398037,
              0.072712641,
            ],
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "3");
        this.$emit("subTitle", 3);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "1");
        this.$emit("subTitle", 1);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}
.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}


#chartyt21 {
  width: calc(100%-20px);
  height: 250px;
}
#chartyt22 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>