  },
  methods: {
    changeshowcontent(val) {
      this.globalShowPanelContent = val;
    },

    changeActivateSelectMode(val) {
      this.activateSelectMode = val;
    },
    changeActivateRectSelectMode(val) {
      console.log("activateRectSelect", val);
      this.activateRectSelect = val;
    },
    replayAnimation() {
      var [arcs, points] = this.duiwaichuxingGenerator();
      this.map.getSource("point").setData(points);
      this.counter = 0;
      this.animate(arcs, points);
    },
    animate(arcs, points) {
      var routes = arcs.features;
      var ps = points.features;
      var steps = routes[0].geometry.coordinates.length - 1;

      routes.forEach((element, index) => {
        // console.log(element,index)
        const start =
          element.geometry.coordinates[
          this.counter >= steps ? this.counter - 1 : this.counter
          ];
        const end =
          element.geometry.coordinates[
          this.counter >= steps ? this.counter : this.counter + 1
          ];
        // console.log(start,end)
        if (!start || !end) return;
        ps[index].geometry.coordinates =
          routes[index].geometry.coordinates[this.counter];

        // Calculate the bearing to ensure the icon is rotated to match the route arc
        // The bearing is calculated between the current point and the next point, except
        // at the end of the arc, which uses the previous point and the current point
        ps[index].properties.bearing = turf.bearing(
          turf.point(start),
          turf.point(end)
        );
      });
      points.features = ps;

      this.map.getSource("point").setData(points);
      this.counter = this.counter + 1;
      // Request the next frame of animation as long as the end has not been reached
      if (this.counter < steps) {
        this.animation = requestAnimationFrame(() => {
          this.animate(arcs, points);
        });
      } else {
        this.counter = 0;
        this.animation = requestAnimationFrame(() => {
          this.animate(arcs, points);
        });
      }
    },
    initChart() {
      window.mapboxgl = mapboxgl;
      window.mapboxgl.accessToken =
        // "pk.eyJ1IjoienBlbmcyMDE4IiwiYSI6ImNreDE4OHB5MTFlOXQydmxhcW52ZGo1NDIifQ.9NllPb9w2D-oiQDftWS5bg";
        "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw";
      var mapStyle = require("../assets/mapstyle/mapstyle2.json");
      var option = {
        container: "map",
        projection: "globe",
        // accessToken:
        //   "pk.eyJ1IjoienBlbmcyMDE4IiwiYSI6ImNreDE4OHB5MTFlOXQydmxhcW52ZGo1NDIifQ.9NllPb9w2D-oiQDftWS5bg",
        accessToken:
          "pk.eyJ1IjoiamY3cGVuZyIsImEiOiJjbGZubjVqejMwNWVkM3RwaDlobnRlcWRmIn0.4QS_sYHWKd-PTQE05jSTkw",
        center: [116.87, 36.616],
        zoom: 2.5,
        // style: mapStyle, // 本地地图样式
        // 复刻样式
        style: "mapbox://styles/jf7peng/clhomjspc01m001qtd30t9jd9",
        // style: "mapbox://styles/zpeng2018/ckz27op9k000y15ousweylykr",
        // style: "mapbox://styles/zpeng2018/cl244evte003o14moq2obne81",
        // 深色大海样式!!!
        // style: "mapbox://styles/zpeng2018/cl2zgt9sf000414p5xz810c63",
        // 浅色地图样式
        // style:"mapbox://styles/zpeng2018/cl2zhrcfg003f15n5a9vsxqve",
        postEffect: {
          enable: true,
          FXAA: {
            enable: true,
          },
        },
        light: {
          main: {
            intensity: 1,
            shadow: true,
            shadowQuality: "medium",
          },
          ambient: {
            intensity: 0,
          },
        },
      };
      this.map = new mapboxgl.Map(option);
      let controls = this.map._controls;
      controls.forEach((control) => {
        // 去除logo控件
        if ("_updateLogo" in control) {
          this.map.removeControl(control);
        }

        // 去除attribute控件
        if ("_attribHTML" in control) {
          this.map.removeControl(control);
        }
      });
      this.map.on("mousemove", this.lonLat);
      this.map.getCanvas().style.cursor = "";
      this.map.on("load", () => {
        this.map.getCanvas().style.cursor = "default";
        this.map.setFog({
          color: 'rgb(186, 210, 235)', // Lower atmosphere
          'high-color': 'rgb(36, 92, 223)', // Upper atmosphere
          'horizon-blend': 0.02, // Atmosphere thickness (default 0.2 at low zooms)
          'space-color': 'rgb(11, 11, 25)', // Background color
          'star-intensity': 0.6 // Background star brightness (default 0.35 at low zoooms )
        });
        this.map.boxZoom.disable();
        this.map.addSource("mapbox-dem", {
          type: "raster-dem",
          url: "mapbox://mapbox.mapbox-terrain-dem-v1",
          tileSize: 512,
          maxzoom: 14,
        });
        this.map.setTerrain({
          source: "mapbox-dem",
          exaggeration: 1.5,
        });
        // this.map.addLayer({
        //   paint: {},
        //   id: "terrainshade",
        //   source: "mapbox-dem",
        //   type: "hillshade",
        // });

        this.map.addSource("shandong", {
          type: "geojson",
          data: require("../assets/data/shandong.json"),
        });
        this.map.addSource("chinaHighway", {
          type: "geojson",
          data: require("../assets/data/chinahighway.json"),
        });

        this.map.addLayer({
          id: "shandongContour",
          type: "line",
          source: "shandong",
          paint: {
            "line-color": "#a9e1ff",
          },
        });
        this.map.addSource("china", {
          type: "geojson",
          data: require("../assets/data/china.json"),
        });
        this.map.addLayer({
          id: "chinaContour",
          type: "line",
          source: "china",
          paint: {
            "line-color": "#a9e1ff",
            "line-opacity": ["match", ["get", "name"], "山东省", 0, 1],
          },
        });
        this.map.addLayer({
          id: "chinaFill",
          type: "fill",
          source: "china",
          paint: {
            "fill-color": "#F1F1F1",
            "fill-opacity": ["match", ["get", "name"], "山东省", 0, 0.12],
          },
        });
        this.map.setLayoutProperty("yantai-buildings", "visibility", "none");
        // this.map.setLayoutProperty("road-simple2", "visibility", "visible");
        // this.map.setLayoutProperty(
