    tollLayer() {
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [116.87, 36.616],
        zoom: 6.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
      // this.map.setLayoutProperty("sdtolls", "visibility", "visible");
      // this.map.setLayoutProperty("wms-test-layer", "visibility", "visible");
      this.map.setLayoutProperty("zhangqiu-buildings", "visibility", "none");
      this.map.setLayoutProperty("Jinan-Building", "visibility", "visible");
      this.map.setLayoutProperty("volume-layer", "visibility", "none");
    },
    fly2Sd(){
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [116.87, 36.616],
        zoom: 6.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);
    },
    computeControlPoint(ps, pe, arc = 0.2) {
      const deltaX = pe[0] - ps[0];
      const deltaY = pe[1] - ps[1];
      const theta = Math.atan(deltaY / deltaX);
      const len = (Math.sqrt(deltaX * deltaX + deltaY * deltaY) / 2) * arc;
      const newTheta = theta - Math.PI / 2;
      return [
        (ps[0] + pe[0]) / 2 + len * Math.cos(newTheta),
        (ps[1] + pe[1]) / 2 + len * Math.sin(newTheta),
      ];
    },
    chuxingfenbuGenerator() {
      var features = chuxingfenbu;
      var arcs = {
        type: "FeatureCollection",
        features: [],
      };
      var points = {
        type: "FeatureCollection",
        features: [],
      };
      // console.log(features);

      features.forEach((item) => {
        var origin = [item["From(Longitude)"], item["From(Latitude)"]];
        var from = item.From;
        var to = item.To;
        var destination = [item["To(Longitude)"], item["To(Latitude)"]];
        var volume = item.交通量;
        console.log(from, origin, to, destination)
        var interPoint = this.computeControlPoint(origin, destination, 0.2);

        var route = {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                From: from,
                To: to,
                交通量: volume,
              },
              geometry: {
                type: "LineString",
                coordinates: [origin, interPoint, destination],
              },
            },
          ],
        };

        var point = {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                From: from,
                To: to,
                交通量: volume,
              },
              geometry: {
                type: "Point",
                coordinates: origin,
              },
            },
          ],
        };
        var bezier = turf.bezier(route.features[0]);
        var lineDistance = turf.lineDistance(bezier, "meters");
        var arc = [];
        var steps = 300;

        // Draw an arc between the `origin` & `destination` of the two points
        for (var i = 0; i < lineDistance; i += lineDistance / steps) {
          var segment = turf.along(bezier, i, "meters");

          arc.push(segment.geometry.coordinates);
        }
        route.features[0].geometry.coordinates = arc;
        console.log(arc[0], arc[arc.length - 1])

        arcs.features.push(route.features[0]);
        points.features.push(point.features[0]);
      });
      console.log(arcs)
      console.log(points)
      return [arcs, points];
    },
    laneLayer() {
      this.shandongLabelLayer();
      this.map.setLayoutProperty("lane-layer", "visibility", "visible");
      this.map.setLayoutProperty("lane-blur-layer", "visibility", "visible");
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [116.87, 36.616],
        zoom: 6.5,
        bearing: 0,
        pitch: 0,
