        10000,
        0.5,
        30000,
        1,
      ]);
      this.shandongLabelLayer();
      this.map.on("mouseenter", "sfzOrigin", this.changeCursorToPointer);
      this.map.on("mousemove", "sfzOrigin", this.sfzOriginPopupInit);
      this.map.on("mouseleave", "sfzOrigin", this.removePopup);
      this.map.on("click", "sfzOrigin", this.changeVolumeByToll);
      var coordinates = [116.87, 36.616];
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
