    changeCursorToPointer() {
      this.map.getCanvas().style.cursor = "pointer";
    },
    changeCursorToDefault() {
      this.map.getCanvas().style.cursor = "default";
    },

    changeVolumeByToll(e) {
      var tollname = e.features[0].properties.name;
      var tollId = e.features[0].properties.id;
      var max = e.features[0].properties.max;
      var min = e.features[0].properties.min;
      this.changeSfzOriginLayer(tollname, tollId, max, min);
    },
    changeSfzOriginLayer(name, id, max, min) {
      var highlightFilter = ["==", "name", name];
      this.map.setPaintProperty("sfzOrigin", "circle-radius", [
        "interpolate",
        ["exponential", 0.8],
        ["get", name],
        0,
        2,
        max / 2,
        4,
        max,
        6,
      ]);
      this.map.setPaintProperty("sfzOrigin", "circle-opacity", [
        "interpolate",
        ["exponential", 0.75],
        ["get", name],
        0,
        0.5,
        max / 3,
        1,
      ]);
      this.map.setFilter("sfzOriginHighlight", highlightFilter);

      if (this.clickedSfz == null) {
        this.map.off("mousemove", "sfzOrigin", this.sfzOriginPopupInit);
      }
      this.map.on("mousemove", "sfzOrigin", this.sfzOriginPopup);
      this.removePopup();
      this.clickedSfz = name;
      this.clickedSfzId = id;
      // console.log(this.clickedSfzId,this.clickedSfz)
    },

    sfzOriginPopupInit(e) {
      // this.changeCursorToPointer();
      var coordinates = this.toolcoord;
      const sfzName = e.features[0].properties.name;

      var titleHtml =
        '<div class="popup-title">' + sfzName + " 收费站溯源情况" + "</div>";

      var contentHtml =
        '<div class="popup-content">' +
        "点击查看<b>" +
        sfzName +
        "收费站</b>溯源情况" +
        "</div>";

      var htmlPopup =
        '<div class="volumePopup">' + titleHtml + contentHtml + "</div>";

      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }

      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },

    sfzOriginPopup(e) {
      // this.changeCursorToPointer();
      var originName = this.clickedSfz;
      var coordinates = this.toolcoord;
      var vOrigin = e.features[0].properties[originName];
      const sfzName = e.features[0].properties.name;

      var titleHtml =
        '<div class="popup-title">' + originName + " 收费站溯源情况" + "</div>";

      var contentHtml =
        '<div class="popup-content">' +
        "来自<b>" +
        sfzName +
        "收费站</b>的交通量：<b>" +
        vOrigin +
        "</b> Pcu/d" +
        "</p><p>单击查看<b>" +
        sfzName +
        "收费站</b>溯源情况" +
        "</div>";

      var htmlPopup =
        '<div class="volumePopup">' + titleHtml + contentHtml + "</div>";

      while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
        coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      }

      this.tollPopup.setLngLat(coordinates).setHTML(htmlPopup).addTo(this.map);
    },

    removeSfzOriginLayer() {
      this.removeShandongLabelLayer();
      if (this.clickedSfz == null) {
        this.map.off("mousemove", "sfzOrigin", this.sfzOriginPopupInit);
      } else {
        this.map.off("mousemove", "sfzOrigin", this.sfzOriginPopup);
      }
      this.map.off("mouseenter", "sfzOrigin", this.changeCursorToPointer);
      this.clickedSfz == null;
      this.clickedSfzId == null;
      this.map.off("mouseleave", "sfzOrigin", this.removePopup);
      this.map.off("click", "sfzOrigin", this.changeVolumeByToll);
      this.map.removeLayer("sfzOrigin");
      this.map.removeLayer("sfzOriginHighlight");
      this.map.removeSource("sfzOriginSource");
    },

