    taixinClick(e) {
      var taixinFilter = ["==", "颜色", 2];
      var hwName = e.features[0].properties.高速名称;
      if (hwName == "泰新高速") {
        this.taixinClicked = true;
        this.map.setFilter("taixin-layer", taixinFilter);
        this.map.setLayoutProperty(
          "taixin-nodes-layer",
          "visibility",
          "visible"
        );
        this.map.setLayoutProperty(
          "taixin-nodes-label-layer",
          "visibility",
          "visible"
        );
        this.map.setLayoutProperty(
          "taixin-nodes-layer",
          "visibility",
          "visible"
        );
      }
