      this.map.off("mouseleave", "taixin-layer", this.removePopup);
      this.map.off("mouseleave", "taixin-nodes-layer", this.removePopup);
      this.map.off("mousemove", "taixin-layer", this.taixinLinePopup);
      this.map.off("mousemove", "taixin-nodes-layer", this.taixinNodePopup);
      this.map.removeLayer("taixin-layer");
      this.map.removeLayer("taixin-nodes-layer");
      this.map.removeLayer("taixin-nodes-label-layer");
      this.map.removeSource("taixinSource");
      this.map.removeSource("taixinNodesSource");
