    changeVehicleGroup(vehicleCheckedGroup) {
      var types = [
        ["货1", "货2", "货3", "货4", "货5", "货6"],
        ["客1", "客2", "客3", "客4"],
      ];
      var volumeData = require("../assets/data/road20220822_new.json");
      var vehicleAvailabelGroup = vehicleCheckedGroup;
      // vehicleCheckedGroup 形如 [['false','false','false','false','false','false'],['true','false','false','false']]
      var vehicleAvailable = [];
      console.log(vehicleCheckedGroup);
      for (var i = 0; i < vehicleCheckedGroup.length; i++) {
        for (var j = 0; j < vehicleCheckedGroup[i].length; j++) {
          if (vehicleCheckedGroup[i][j]) {
            vehicleAvailable.push(types[i][j]);
          }
        }
      }
      console.log(vehicleAvailable);
      var features = volumeData.features;
      for (var i = 0; i < features.length; i++) {
        var properties = features[i].properties;
        // 计算所选车型的当量数之和
        var sum = 0;
        for (var j = 0; j < vehicleAvailable.length; j++) {
          sum += properties[vehicleAvailable[j]];
        }
        features[i].properties["所选总量数"] = sum;
      }
      // 计算所选总量数的最大值和最小值
      var max = 0;
      var min = 0;
      for (var i = 0; i < features.length; i++) {
        if (features[i].properties["所选总量数"] > max) {
          max = features[i].properties["所选总量数"];
        }
        if (features[i].properties["所选总量数"] < min) {
          min = features[i].properties["所选总量数"];
        }
      }
      console.log(min, max);
      this.map.getSource("pcuSource").setData(volumeData);
      this.map.setPaintProperty("pcu-layer", "line-width", [
        "interpolate",
        ["linear"],
        ["get", "所选总量数"],
        (max * 1) / 5,
        1,
        max * 1,
        8,
      ]);
    },

