    highlightHoverSection(e) {
      if (this.activateSelectMode) {
        this.changeCursorToPointer();
        var sectionID = e.features[0].id;
        if (this.hoverSectionID !== null) {
          this.map.setFeatureState(
            { source: "cyhSource", id: this.hoverSectionID },
            { hover: false }
          );
        }
        this.hoverSectionID = sectionID;
        this.map.setFeatureState(
          { source: "cyhSource", id: this.hoverSectionID },
          { hover: true }
        );
      }
    },

    highlightSelectSfz(e) {
      const features = this.map.queryRenderedFeatures(e.point, {
        layers: ["sfzCyhHighlight"],
      });

      // Change the cursor style as a UI indicator.
      this.map.getCanvas().style.cursor = features.length ? "pointer" : "";

      if (!features.length) {
        this.mapselectpopup.remove();
        return;
      }

      this.mapselectpopup
        .setLngLat(e.lngLat)
        .setText(features[0].properties.name)
        .addTo(this.map);
    },

    selectSection(e) {
      if (this.activateSelectMode) {
        console.log("select mode is activated");
        var sectionID = e.features[0].id;
        var sectionIDinJson = e.features[0].properties["序号"];
        if (this.selectedSectionID.indexOf(sectionIDinJson) == -1) {
          this.selectedSectionID.push(sectionIDinJson);
        } else {
          this.selectedSectionID.splice(
            this.selectedSectionID.indexOf(sectionIDinJson),
            1
          );
        }
        var expression = ["match", ["get", "序号"]];
        if (this.selectedSectionID.length > 0) {
          this.selectedSectionID.forEach((item) => {
            expression.push(item);
            expression.push(1);
          });
          expression.push(0);
          // console.log(expression)
          this.map.setPaintProperty(
            "cyh-selected-layer",
            "line-opacity",
            expression
          );
        } else {
          this.map.setPaintProperty("cyh-selected-layer", "line-opacity", 0);
        }
      } else {
        console.log("select mode is not activated");
      }
    },

    leaveSection(e) {
      this.changeCursorToDefault();
      if (this.hoverSectionID !== null) {
        this.map.setFeatureState(
          { source: "cyhSource", id: this.hoverSectionID },
          { hover: false }
        );
      }
      this.hoverSectionID = null;
    },

    mousePos(e) {
      const rect = this.canvas.getBoundingClientRect();
      return new mapboxgl.Point(
        e.clientX - rect.left - this.canvas.clientLeft,
        e.clientY - rect.top - this.canvas.clientTop
      );
    },

    mouseDown(e) {
      if (this.activateRectSelect == true) {
        this.selectedSfzIds = null;
        // Continue the rest of the function if the shiftkey is pressed.
        if (!(e.shiftKey && e.button === 0)) return;

        // Disable default drag zooming when the shift key is held down.
        this.map.dragPan.disable();

        // Call functions for the following events
        document.addEventListener("mousemove", this.onMouseMove);
        document.addEventListener("mouseup", this.onMouseUp);
        document.addEventListener("keydown", this.onKeyDown);

        // Capture the first xy coordinates
        this.start = this.mousePos(e);
      } else {
        return;
      }
    },

    onMouseMove(e) {
      // Capture the ongoing xy coordinates
      this.current = this.mousePos(e);

      // Append the box element if it doesnt exist
      if (!this.box) {
        this.box = document.createElement("div");
        this.box.classList.add("boxdraw");
        this.canvas.appendChild(this.box);
      }

      const minX = Math.min(this.start.x, this.current.x),
        maxX = Math.max(this.start.x, this.current.x),
        minY = Math.min(this.start.y, this.current.y),
        maxY = Math.max(this.start.y, this.current.y);

      // Adjust width and xy position of the box element ongoing
      const pos = `translate(${minX}px, ${minY}px)`;
      this.box.style.transform = pos;
      this.box.style.width = maxX - minX + "px";
      this.box.style.height = maxY - minY + "px";
    },

    onMouseUp(e) {
      // Capture xy coordinates
      this.finish([this.start, this.mousePos(e)]);
    },

    onKeyDown(e) {
      // If the ESC key is pressed
      if (e.keyCode === 27) this.finish();
    },

    finish(bbox) {
      // Remove these events now that finish has been called.
      document.removeEventListener("mousemove", this.onMouseMove);
      document.removeEventListener("keydown", this.onKeyDown);
      document.removeEventListener("mouseup", this.onMouseUp);

      if (this.box) {
        console.log(this.box)
        this.box.parentNode.removeChild(this.box);
        this.box = null;
      }
      // If bbox exists. use this value as the argument for `queryRenderedFeatures`
      if (bbox) {
        const features = this.map.queryRenderedFeatures(bbox, {
          layers: ["sfzCyh"],
        });

        if (features.length >= 1000) {
          return window.alert("Select a smaller number of features");
        }

        const ids = features.map((feature) => feature.properties.id);
        var ids_list = []
        ids.forEach((item, index) => {
          ids_list.push(item)
        })
        this.selectedSfzIds = ids_list;
        this.map.setFilter("sfzCyhHighlight", ["in", "id", ...ids]);
      }
      this.map.dragPan.enable();
    },

