      this.map.removeLayer("cyh-selected-layer");
      this.map.removeLayer("sfzCyhHighlight");
      this.map.removeLayer("sfzCyh");
      this.canvas.removeEventListener("mousedown", this.mouseDown);
      this.canvas = null;
      this.box = null;
      // remove popup
      this.map.off("mousemove", "cyh-layer", this.highlightHoverSection);
      this.map.off("click", "cyh-layer", this.selectSection);
      this.map.off("mouseleave", "cyh-layer", this.leaveSection);
      this.selectedSectionID = [];
      this.activateSelectMode = false;
      this.map.removeSource("cyhSource");
