    },
    removePjVolumeLayer() {
      this.removePureRoadLayer();
      this.map.removeLayer("pj-volume-label-layer")
      this.map.removeLayer("pj-volume-layer")
      this.map.removeLayer("pj-arc-layer")
      this.map.removeSource("pjVolumeSource")
      this.map.removeSource("pjArcSource")
    },

    pjDirectionLayer() {
      var pjLineJson = require("../assets/data/pjline.json")
      var pjPointJson = require("../assets/data/pjpoint.json")
      this.pureRoadLayer("#5CF4F8", 0.2)
      this.map.addSource("pjLineSource", {
        type: "geojson",
        data: pjLineJson,
        generateId: true,
      });
      this.map.addLayer({
        id: "pj-line-layer",
        type: "line",
        source: "pjLineSource",
        paint: {
          "line-color": "#5CF4F8",
          "line-width": ["interpolate", ["exponential", 1], ["get", "数量"], 1000, 3, 4000, 12],
        }
      });
      this.map.addLayer({
        id: "pj-line-label-layer",
        source: "pjLineSource",
        type: "symbol",
        layout: {
          "text-size": 20,
          "symbol-placement": "line-center",
          "text-field": ["get", "数量"],
        },
        paint: {
          "text-color": "#fff",
          "text-halo-width": 2,
          "text-halo-color": "#000",
        }
      }
      )
      this.map.addSource("pjPointSource", {
        type: "geojson",
        data: pjPointJson,
        generateId: true,
      });
      this.map.addLayer({
        id: "pj-point-layer",
        type: "circle",
        source: "pjPointSource",
        paint: {
          "circle-color": "#ffff00",
          "circle-radius": 10,
        }
      });
      this.map.addLayer({
        id: "pj-point-label-layer",
        type: "symbol",
        source: "pjPointSource",
        layout: {
          "text-size": 18,
          "text-anchor": "top",
          "symbol-placement": "point",
          "text-field": ["get", "name"],
        },
        paint: {
          "text-color": "#fff",
          "text-halo-width": 1,
          "text-halo-color": "#000",
        }
      });
      const easingFn = function (t) {
        return 1 - Math.pow(1 - t, 5);
      };
      const animationOptions = {
        duration: 2500,
        easing: easingFn,
        offset: [0, 0],
        animate: true,
        essential: true, // animation will happen even if user has `prefers-reduced-motion` setting on
        center: [116.87, 36.616],
        zoom: 6.5,
        bearing: 0,
        pitch: 0,
      };
      this.map.flyTo(animationOptions);


    },
    removePjDirectionLayer() {
      this.removePureRoadLayer();
      this.map.removeLayer("pj-line-label-layer")
      this.map.removeLayer("pj-line-layer")
      this.map.removeLayer("pj-point-layer")
      this.map.removeLayer("pj-point-label-layer")
      this.map.removeSource("pjLineSource")
      this.map.removeSource("pjPointSource")
    },

    pureRoadLayer(color, opacity) {
      this.map.addSource("roadSource", {
        type: "geojson",
        data: require("../assets/data/road20220822_new.json"),
      });
      this.map.addLayer({
        id: "pure-road-layer",
        type: "line",
        source: "roadSource",
        layout: {
          "line-join": "round",
          "line-cap": "round",
          visibility: "visible",
        },
        paint: {
          "line-color": color,
          "line-width": [
            "interpolate",
            ["exponential", 1],
            ["zoom"],
            2,
            0.5,
            6,
            3,
            16,
            10
          ],
          "line-opacity": [
            "interpolate",
            ["exponential", 1],
            ["zoom"],
            2,
            0 * opacity,
            6,
            0.5 * opacity,
            7,
            1 * opacity
          ]
        },
      });

    },
    channelLayerByName(name) {
      this.channelLayer();
      this.map.setFilter("channel-layer", ['==', ['get', 'name'], name])
      this.map.setFilter("channel-label-layer", ['==', ['get', 'name'], name])


    },
    removePureRoadLayer() {
      this.map.removeLayer("pure-road-layer");
      this.map.removeSource("roadSource")

    },

    changeTitleByValue(val) {
      this.subTitle = val;
    },
  },
  mounted() {
    // this.initMap(0);
    this.initChart();
    let that = this;
    this.$nextTick(function () {
      document.onkeydown = function (e) {
        let key = window.event.keyCode;
        if (key == 27) {
          console.log("esc");
          console.log(that.tabnum);
          if (that.tabnum == "report") {
            that.$router.go(-1);
          }
        }
      };
    });
    // this.tollLayer();
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart.dispose();
    this.chart = null;
  },
};
</script>

