<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1><span id="sfztitle"></span>收费站交通量溯源</h1>
            <div class="next">
              <el-button :disabled="true" @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p id="instruction">单击右侧收费站，查看对应交通量溯源情况</p>
                <p id="description"></p>
                <div id="chart4-1-1"></div>

                <!-- <div id="chart42"></div> -->
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { markRaw } from "vue";

export default {
  name: "Panel41",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      sfzName: null,
      sfzId: null,
    };
  },
  props: ["showPC", "targetSfzName", "targetSfzId"],
  watch: {
    targetSfzName(val) {
      this.sfzName = val;
    },
    targetSfzId(val) {
      this.sfzId = val;
      this.initChart(this.sfzName, this.sfzId);
    },
  },
  methods: {
    initChart(name, id) {
      var fullTable = require("../assets/data/4-1-1.json");
      var targetSfzName = name;
      var sfzObj = document.getElementById("sfztitle");
      var instructionObj = document.getElementById("instruction");
      var descriptionObj = document.getElementById("description");

      sfzObj.innerHTML = targetSfzName + "&nbsp;";
      instructionObj.innerHTML = "";

      var targetSfzId = id;

      var targetTable = fullTable[targetSfzId - 1]["originVolume"];
      var sumVol = fullTable[targetSfzId - 1]["sum"];
      var maxVol = fullTable[targetSfzId - 1]["max"];

      // console.log(targetSfzName,fullTable[targetSfzId-1]["name"],fullTable[targetSfzId-1]["originVolume"])
      targetTable.sort(function (a, b) {
        return b["value"] - a["value"];
      });
      var top10 = targetTable.slice(0, 20);
      var top1Name = top10[0]["name"];
      descriptionObj.innerHTML =
        targetSfzName +
        '&nbsp;收费站交通总量为 <b style="color:#5CF4F8;">' +
        sumVol +
        " Pcu/d</b>，其中来源交通量最大的收费站为" +
        top1Name +
        '，交通量为<b style="color:#5CF4F8;">' +
        maxVol +
        " Pcu/d</b>，前20大交通量的来源收费站分布情况如下表所示。";

      // maxVol*1.2/100
      document.getElementById("chart4-1-1").style.height = "500px";

      this.chart1 = markRaw(
        echarts.init(document.getElementById("chart4-1-1"))
      );
      var sfz = [];
      var volume = [];
      top10.forEach((item) => {
        sfz.push(item.name);
        volume.push(item.value);
      });

      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.name +
              "</br>" +
              params.marker +
              " " +
              params.seriesName +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " Pcu/d</b>"
            );
          },
        },
        title: {
          text: targetSfzName + "收费站来源交通量TOP20",
          left: "center",
          textStyle: { fontSize: 15, color: "azure" },
          textStyle: {
            color: "azure",
          },
        },
        grid: {
          x: 80,
          y: 55,
          x2: 50,
          y2: 45,
        },
        legend: {
          icon: "rect",
          // itemWidth: 14,
          // itemHeight: 5,
          // itemGap: 13,
          // right: "10px",
          // top: "0px",
          show: false,
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        xAxis: {
          type: "value",
          name: "交通量(Pcu/d)",
          nameLocation: "middle",
          nameTextStyle: {
            padding: [10, 0],
            color: "azure",
          },
          splitLine: {
            lineStyle: {
              color: "grey",
            },
          },
          axisLabel: {
            color: "azure",
          },
        },
        yAxis: {
          type: "category",
          data: sfz,
          name: "收费站名称",
          nameLocation: "start",
          nameTextStyle: {
            padding: [-10, -20],
            color: "azure",
          },
          inverse: true,
          splitArea: {
            show: true,
            interval: 0,
            areaStyle: {
              color: ["rgba(0,0,0,0.2)", "rgba(0,0,0,0.1)"],
            },
          },
          axisLabel: {
            color: "azure",
          },
        },
        emphasis: {
          focus: "series",
        },
        color: "#5CF4F8",
        series: [
          {
            name: "来源交通量",
            type: "bar",
            data: volume,
            itemStyle: {
              barWidth: 5,
            },
            label: {
              show: true,
              color: "azure",
              position: "right",
              formatter: function (params) {
                return params.value + " Pcu/d";
              },
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[max-height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[max-height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-2");
        this.$emit("subTitle", "2-2");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-1");
        this.$emit("subTitle", "5-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  max-height: 650px;
}

#chart4-1-1 {
  width: calc(100%-20px);
  /* height: 450px; */
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>