<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>对外出行</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  山东省高速公路对外出行，<b class="green">江苏连云港</b
                  >方向占比最高，通道包括长深高速和沈海高速；其次是<b
                    class="green"
                    >河北沧州</b
                  >方向， 通道包括京台高速、京沪高速和长深高速。
                </p>
                <div id="chart231"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["沈海高速", "日照至鲁苏界", 0.9],
        ["济广高速", "孔村枢纽至东平湖枢纽", 0.83],
        ["长深高速", "于家庄枢纽至临朐立交", 0.82],
        ["青银高速", "青岛东收费站至夏庄枢纽", 0.81],
        ["长深高速", "无棣至鲁冀界", 0.8],
        ["青银高速", "崔寨枢纽至机场枢纽", 0.79],
        ["济广高速", "定陶枢纽至鲁豫界", 0.77],
        ["德上高速", "聊城西枢纽至李集枢纽", 0.75],
      ],
      toll_rank: [
        ["青岛东主线收费站", "青银高速", 79729],
        ["双埠主线收费站", "青兰高速", 54861],
        ["济南零点收费站", "济广高速", 48447],
        ["济南机场收费站", "济南绕城高速", 43986],
        ["天桥收费站", "济广高速", 42246],
        ["福山收费站", "沈海高速", 40907],
        ["济南北收费站", "济广高速", 40383],
        ["黄岛东主线收费站", "前湾港疏港1号线", 39385],
        ["临沂收费站", "京沪高速", 39022],
        ["泰安东收费站", "青兰高速", 34989],
      ],
    };
  },
  props: ["showPC"],

  methods: {
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-3");
        this.$emit("subTitle", '5-3');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-5");
        this.$emit("subTitle", '5-5');
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart231"));
      var tabledata = [
        {
          To: "江苏连云港方向",
          From: "山东省",
          value: 89071,
          比重: "23.9%",
        },
        {
          To: "河北沧州方向",
          From: "山东省",
          value: 57033,
          比重: "15.3%",
        },
        {
          To: "河北石家庄方向",
          From: "山东省",
          value: 40143,
          比重: "10.8%",
        },
        {
          To: "河南濮阳方向",
          From: "山东省",
          value: 38784,
          比重: "10.4%",
        },
        {
          To: "河南商丘方向",
          From: "山东省",
          value: 35712,
          比重: "9.6%",
        },
        {
          To: "江苏徐州方向",
          From: "山东省",
          value: 35332,
          比重: "9.5%",
        },
        {
          To: "河北邯郸方向",
          From: "山东省",
          value: 30846,
          比重: "8.3%",
        },
        {
          To: "江苏新沂方向",
          From: "山东省",
          value: 21563,
          比重: "5.8%",
        },
        {
          To: "河南开封方向",
          From: "山东省",
          value: 21260,
          比重: "5.7%",
        },
        {
          To: "安徽及以远",
          From: "山东省",
          value: 2849,
          比重: "0.8%",
        },
      ];
      var toScatterData = function (data) {
        var scatterData = [];
        data.forEach((item, index) => {
          // console.log([item.value, index, item])
          scatterData.push([item.value, index, item]);
        });
        return scatterData;
      };
      var scatterData = toScatterData(tabledata);
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.From +
              " - " +
              params.data.To +
              "</b></br>" +
              params.marker +
              "交通量：&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/d</br>" +
              params.marker +
              "比重：&nbsp;" +
              params.data.比重 
            );
          },
        },
        grid: {
          x: 90,
          y: 25,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "交通量(Pcu/d)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            max: 100000,
            axisTick: {
              show: false,
              lineStyle: {
                color: "grey",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            name: "终点",
            nameGap: 5,
            type: "category",
            data: [
              "江苏连云港方向",
              "河北沧州方向",
              "河北石家庄方向",
              "河南濮阳方向",
              "河南商丘方向",
              "江苏徐州方向",
              "河北邯郸方向",
              "江苏新沂方向",
              "河南开封方向",
              "安徽及以远",
            ],
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "交通量",
            itemStyle: {
              color: "#24FFC5",
              opacity: 0.8,
            },
            barWidth: 2.5,
            label: {
              show: true,
              position: "right",
              textStyle: {
                color: "azure",
                padding: [0, 0, 0, 10],
              },
            },

            data: tabledata,
          },
          {
            type: "scatter",
            symbolSize: function (val) {
              return val[0] / 3000;
            },
            tooltip: {
              formatter: function (params) {
                return (
                  "<b>" +
                  params.data[2].From +
                  " - " +
                  params.data[2].To +
                  "</b></br>" +
                  params.marker +
                  "交通量：&nbsp;" +
                  params.data[2].value +
                  "&nbsp; Pcu/d</br>" +
                  params.marker +
                  "比重：&nbsp;" +
                  params.data[2].比重 
                );
              },
            },
            itemStyle: {
              color: "#24FFC5",
              opacity: 1,
            },
            data: scatterData,
          },
        ],
      });
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>





#chart231 {
  width: calc(100%-20px);
  height: 250px;
}
#chart12 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
.pmain {
  max-height: 650px;
}
</style>