<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>潍坊市区路网现状饱和度分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
            <p>
              路段饱和度在0.5以下为<b
                class="green"
                >畅通状态</b
              >饱和度在0.5和0.75之间为<b class="yellow">缓行状态</b
              >，饱和度在0.75以上为<b class="red">拥挤状态</b>。
            </p>
            
            <div id="chart41"></div>

            <!-- <div id="chart42"></div> -->
              </el-scrollbar>
            </div>
            
          </el-main>
           <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
    };
  },
  props:["showPC"],
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart41"));
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        series: [
          {
            name: "路段占比",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 5,
              borderColor: "#fff",
              borderWidth: 1,
            },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: 0.818, name: "畅通路段", itemStyle:{color:"#24FFC5"}},
              { value: 0.154, name: "缓行路段", itemStyle:{color:"#5CF4F8"}},
              { value: 0.028, name: "拥挤路段", itemStyle:{color:"#F97952"}},
            ],
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart42"));
      this.traffic_rank.reverse();
      var highways = [];
      var sections = [];
      var ratios = [];
      this.traffic_rank.forEach((element) => {
        highways.push(element[0]);
        sections.push(element[1]);
        ratios.push(element[2]);
      });

      this.chart2.setOption({
        title:{
          show:true,
          text:"全省高速公路主要拥堵路段",
          x:'center',
          y:'top',
          textStyle:{
            fontSize:15,
            color:"azure"
          }

        },
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          show: false,
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 135,
          y: 25,
          x2: 20,
          y2: 25,
        },
        yAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "#C9DD23",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            data: sections,
          },
        ],
        xAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "#C9DD23",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "平均拥堵度",
            type: "bar",
            smooth: true,
            barWidth: 10,
            data: ratios,
            itemStyle:{
              normal:{
                color:"#f03958",
              }
            }
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6");
        this.$emit("subTitle", 6);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "3");
        this.$emit("subTitle", 3);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  max-height: 650px;
}


#chart41 {
  width: calc(100%-20px);
  height: 200px;
}
#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}


.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>