<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>车辆行驶轨迹分析</h1>
            <div class="next">
              <el-button @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <div class="pmain">
                <el-scrollbar style="height: 100%">
                  <p>
                    高速公路收费门架记录了车辆经过的时间和位置，将车辆经过的全部收费门架在路网中匹配，可以获得车辆完整的行驶轨迹及相关信息。
                  </p>
                  <p>
                    例如河北省唐山牌照的六型货车，自<b>鲁北省界门架</b>进入山东，经荣乌高速、滨台高速、青银高速、长深高速至<b>长深鲁苏省界门架</b>，进入江苏境内。山东境内行驶距离为429.2公里，行驶时间为6小时18分钟，平均行驶速度为68.1公里/小时。
                  </p>
                  <el-table :data="tableData" :stripe="true" border style="width: 100%">
                    <el-table-column prop="字段" label="字段" width="180" />
                    <el-table-column prop="数据" label="数据"  />
                  </el-table>
                </el-scrollbar>
              </div>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      tableData: [
        {
          字段: "车型",
          数据: "六型货车",
        },
        {
          字段: "车辆牌照",
          数据: "冀B*****",
        },
        {
          字段: "入口站",
          数据: "鲁北省界门架",
        },
        {
          字段: "入口时间",
          数据: "2021/9/15  12:10:56",
        },
        {
          字段: "出口站",
          数据: "长深鲁苏省界门架",
        },
        {
          字段: "出口时间",
          数据: "2021/9/15  18:28:07",
        },
        {
          字段: "路径",
          数据: "鲁北-无棣-古城枢纽……-临沭-长深鲁苏",
        },
        {
          字段: "轴数",
          数据: "6",
        },
        {
          字段: "车货总重",
          数据: "37.7吨",
        },
        {
          字段: "行驶里程",
          数据: "429.2公里",
        },
        {
          字段: "行驶时间",
          数据: "6小时18分钟",
        },
        {
          字段: "平均行驶速度",
          数据: "68.1公里/小时",
        },

      ],
    };
  },
  props: ["showPC"],

  methods: {
    
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-2");
        this.$emit("subTitle", "2-2");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-1");
        this.$emit("subTitle", "2-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1 = null;
    this.chart2 = null;
  },
};
</script>


<style scoped lang="less">

.el-scrollbar__wrap {
  overflow-x: hidden;
  overflow-y: hidden;
}
.pmain {
  height: 650px;
}


#chart51 {
  width: calc(100%-10px);
  height: 300px;
}
#chart52 {
  width: calc(100%-20px);
  height: 180px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.el-table {
    --el-table-border-color: var(--el-border-color-lighter);
    --el-table-border: 1px solid var(--el-table-border-color);
    --el-table-text-color: rgba(255, 255, 255, 0.9);
    --el-table-header-text-color: rgba(255, 255, 255, 1);
    --el-table-row-hover-bg-color: rgba(255, 255, 255, 0.1);
    --el-table-current-row-bg-color: var(--el-color-primary-light-9);
    --el-table-header-bg-color: rgba(0, 0, 0, 0.1);
    --el-table-fixed-box-shadow: 0 0 10px rgba(0, 0, 0, 0.12);
    --el-table-bg-color: rgba(0, 0, 0, 0.1);
    --el-table-tr-bg-color: rgba(0, 0, 0, 0.1);
    --el-table-expanded-cell-bg-color: rgba(0, 0, 0, 0.1);
}

/deep/.el-table--striped .el-table__body tr.el-table__row--striped td.el-table__cell {
    background: rgba(0, 0, 0, 0.2) !important;
}


</style>