<template>
  <div class="float" id="box">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>高速公路出行量</h1>
            <div class="next">
              <el-button @click="previousPage">
                &lt;
              </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent" class="pmain">
              <el-scrollbar>
                <p>
                  从月初至3月20日，随着全省新型冠状病毒确诊病例增加，全省高速公路出行量呈现下降趋势，3月20日后，随着现存病例下降，高速公路出行量逐渐恢复。
                </p>
                <!-- <div id="chartgk1"></div> -->
                <span class="inlinecenter">高速公路出行量日变化图</span>
                <div id="chartp-1"></div>
                <span class="inlinecenter">高速公路出行量24小时变化图</span>
                <div id="chart24-1"></div>
                <div style="height:20px"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,

      chart2: null,
    };
  },
  props: ["showPC"],
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chartp-1"));
      var table = require("../assets/data/pandemic.json");
      var date = [];
      var trip = [];
      var newCase = [];
      var newCaseN = [];
      var currentCase = [];
      var highRisk = [];
      var mediumRisk = [];

      table.forEach((item) => {
        date.push(item.日期);
        trip.push(item["出行量（车次）"]);
        newCase.push(item["本土新增"]);
        newCaseN.push(item["本土无症状新增"]);
        currentCase.push(item["现存病例"]);
        highRisk.push(item.高风险);
        mediumRisk.push(item.中风险);
      });
      this.chart1.setOption({
        visualMap: [
          {
            show: false,
            type: "continuous",
            seriesIndex: 1,
            min: 0,
            max: 1257,
          },
        ],
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 60,
          y: 45,
          x2: 40,
          y2: 35,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
            },
            axisLabel: {
              rotate: 45,
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 1,
            },
            data: date,
          },
        ],
        yAxis: [
          {
            type: "value",
            name: "出行量(车次)",
            axisTick: {
              show: true,
            },
            // max:10,
            min:600000,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
          {
            type: "value",
            name: "现存病例",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "出行量（车次）",
            type: "line",
            smooth: true,
            lineStyle: {
              normal: {
                width: 2,
              },
            },
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: trip,
          },
          {
            name: "现存病例",
            type: "line",
            yAxisIndex: 1,
            smooth: true,
            lineStyle: {
              normal: {
                width: 2,
              },
            },
            itemStyle: {
              normal: {
                color: "rgb(255,255,0)",
              },
            },
            data: currentCase,
            label: {
              show: true,
              position: "top",
              color: "azure",
              formatter: function (params) {
                var index = params.dataIndex;
                if (index % 3 == 1) {
                  return params.value + " 个";
                } else {
                  return "";
                }
              },
            },
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart24-1"));
      var table = require("../assets/data/24h.json");
      var hour = [];
      var trip = [];
      table.forEach((item) => {
        hour.push(item.时段);
        trip.push(item["汇总"]);
      });
      this.chart2.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 80,
          y: 45,
          x2: 70,
          y2: 35,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
            },
            axisLabel: {
              rotate: 45,
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 1,
            },
            data: hour,
          },
        ],
        yAxis: [
          {
            type: "value",
            name: "出行量(车次)",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
          {
            type: "value",
            name: "现存病例",
            axisTick: {
              show: true,
            },
            // max:10,
            axisLine: {
              lineStyle: {
                color: "azure",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 10,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "出行量（车次）",
            type: "bar",
            itemStyle: {
              normal: {
                color: "#24FFC5",
              },
            },
            data: trip,
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style[height] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style[height] = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-2");
        this.$emit("subTitle", "2-2");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-2");
        this.$emit("subTitle", "2-2");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChartgk2();
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
    // this.chart2.dispose();
    // this.chart2 = null;
  },
};
</script>


<style scoped>
div.float {
  position: relative;
  width: 550px;
  /* height: 730px; */
}

.pmain {
  height: 625px;
}

.pmain span.inlinecenter{
  display:block;
  margin-top:10px;
  text-align: center;
  color: #fff;
}

#chartp-1 {
  width: calc(100%-20px);
  height: 250px;
}
#chart24-1 {
  width: calc(100%-20px);
  height: 200px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>