<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>总体出行分布</h1>
            <div class="next">
              <el-button :disabled="false" @click="previousPage"> &lt; </el-button>
              <el-button :disabled="false" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  山东省位于济南至青岛运输通道内，通道内相关公路承担了一定比重的<b
                    class="red"
                    >过境出行</b
                  >、山东省与周边地区之间的<b class="green">对外出行</b
                  >以及山东省内部各地市之间的<b>内部出行</b>。
                </p>
                <p>
                  根据调查结果，山东省三大经济圈之间的<b>内部出行</b>占<b>69%</b>；山东省与周边省份之间的<b
                    class="green"
                    >对外出行</b
                  >占<b class="green">24%</b>；河北省与江苏省及以远地区之间的<b
                    class="red"
                    >过境出行</b
                  >占<b class="red">7%</b>。
                </p>
                <div id="chart211"></div>
                <div id="chart212"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
    };
  },
  props: ["showPC"],

  methods: {
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-5");
        this.$emit("subTitle", "5-5");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "channel-jingjiu");
        this.$emit("subTitle", "channel-jingjiu");
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart211"));
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.From +
              " - " +
              params.data.To +
              "</b></br>" +
              params.marker +
              "交通量：&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/d</br>" +
              params.marker +
              "比重：&nbsp;" +
              params.data.比重
            );
          },
        },
        grid: {
          x: 130,
          y: 25,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "交通量(Pcu/d)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
              lineStyle: {
                color: "grey",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            name: "终点",
            nameGap: 5,
            type: "category",
            data: [
              "胶东经济圈",
              "鲁南经济圈",
              "河北省",
              "江苏省",
              "河南省",
              "安徽省",
              "鲁南经济圈",
              "河北省",
              "江苏省",
              "河南省",
              "安徽省",
              "河北省",
              "江苏省",
              "河南省",
              "安徽省",
              "江苏省",
              "河南省",
              "安徽省",
              "河南省",
              "安徽省",
            ],
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
          },
          {
            type: "category",
            nameGap: 5,
            name: "起点",
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
            nameLocation: "start",
            position: "left",
            offset: 60,
            data: [
              "省会经济圈",
              "省会经济圈",
              "省会经济圈",
              "省会经济圈",
              "省会经济圈",
              "省会经济圈",
              "胶东经济圈",
              "胶东经济圈",
              "胶东经济圈",
              "胶东经济圈",
              "胶东经济圈",
              "鲁南经济圈",
              "鲁南经济圈",
              "鲁南经济圈",
              "鲁南经济圈",
              "河北省",
              "河北省",
              "河北省",
              "江苏省",
              "河南省",
            ],
            splitArea: {
              show: true,
              areaStyle: {
                color: [
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.1)",
                  "rgba(0,0,0,0.2)",
                  "rgba(0,0,0,0.1)",
                ],
              },
            },
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //不显示最后一个坐标标签
              showMaxLabel: true,
              interval: 0,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: {
          type: "bar",
          name: "交通量",
          itemStyle: {
            color: function (params) {
              var type = params.data.类型;
              if (type == "内部出行") {
                return "#5CF4F8";
              }
              if (type == "对外出行") {
                return "#24FFC5";
              }
              if (type == "过境出行") {
                return "#F97952";
              }
            },
          },
          label: {
            show: true,
            position: "right",
            textStyle: {
              color: "azure",
            },
          },

          data: [
            {
              From: "省会经济圈",
              To: "胶东经济圈",
              value: 110978,
              比重: "13.80%",
              类型: "内部出行",
            },
            {
              From: "省会经济圈",
              To: "鲁南经济圈",
              value: 88571,
              比重: "11.00%",
              类型: "内部出行",
            },
            {
              From: "省会经济圈",
              To: "河北省",
              value: 161449,
              比重: "20.10%",
              类型: "对外出行",
            },
            {
              From: "省会经济圈",
              To: "江苏省",
              value: 30809,
              比重: "3.80%",
              类型: "对外出行",
            },
            {
              From: "省会经济圈",
              To: "河南省",
              value: 22206,
              比重: "2.80%",
              类型: "对外出行",
            },
            {
              From: "省会经济圈",
              To: "安徽省",
              value: 679,
              比重: "0.10%",
              类型: "对外出行",
            },
            {
              From: "胶东经济圈",
              To: "鲁南经济圈",
              value: 49050,
              比重: "6.10%",
              类型: "内部出行",
            },
            {
              From: "胶东经济圈",
              To: "河北省",
              value: 25740,
              比重: "3.20%",
              类型: "对外出行",
            },
            {
              From: "胶东经济圈",
              To: "江苏省",
              value: 96219,
              比重: "12.00%",
              类型: "对外出行",
            },
            {
              From: "胶东经济圈",
              To: "河南省",
              value: 9722,
              比重: "1.20%",
              类型: "对外出行",
            },
            {
              From: "胶东经济圈",
              To: "安徽省",
              value: 27,
              比重: "0.00%",
              类型: "对外出行",
            },
            {
              From: "鲁南经济圈",
              To: "河北省",
              value: 24064,
              比重: "3.00%",
              类型: "对外出行",
            },
            {
              From: "鲁南经济圈",
              To: "江苏省",
              value: 76107,
              比重: "9.50%",
              类型: "对外出行",
            },
            {
              From: "鲁南经济圈",
              To: "河南省",
              value: 44874,
              比重: "5.60%",
              类型: "对外出行",
            },
            {
              From: "鲁南经济圈",
              To: "安徽省",
              value: 2768,
              比重: "0.30%",
              类型: "对外出行",
            },
            {
              From: "河北省",
              To: "江苏省",
              value: 57252,
              比重: "7.10%",
              类型: "过境出行",
            },
            {
              From: "河北省",
              To: "河南省",
              value: 1827,
              比重: "0.20%",
              类型: "过境出行",
            },
            {
              From: "河北省",
              To: "安徽省",
              value: 750,
              比重: "0.10%",
              类型: "过境出行",
            },
            {
              From: "江苏省",
              To: "河南省",
              value: 977,
              比重: "0.10%",
              类型: "过境出行",
            },
            {
              From: "河南省",
              To: "安徽省",
              value: 63,
              比重: "0.00%",
              类型: "过境出行",
            },
          ],
        },
      });
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>



#chart211 {
  width: calc(100%-20px);
  height: 400px;
}
#chart12 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
.pmain {
  height: 650px;
}
</style>