<template>
  <div class="float-large">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>高速公路出行总量分布情况</h1>
            <div class="next">
              <el-button :disabled="false" @click="previousPage"> &lt; </el-button>
              <el-button :disabled="false" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  通过对比可以看出，山东各地市高速公路<b>出行量</b>、<b class="green">GDP</b>、<b class="red">人口</b>比重分布趋势基本一致。
                </p>

                <el-row>
                  <el-col :span="8">
                    <div id="chartmap1"></div>
                  </el-col>
                  <el-col :span="8">
                    <div id="chartmap2"></div>
                  </el-col>
                  <el-col :span="8">
                    <div id="chartmap3"></div>
                  </el-col>
                </el-row>
                <div id="chart5-1-1"></div>

                <!-- <div id="chart42"></div> -->
              </el-scrollbar>

            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img @click="hideContentFunction" src="../img/up.png" alt="" style="height: 20px" />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img @click="showContentFunction" src="../img/down.png" alt="" style="height: 20px" />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { markRaw } from "vue";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      chart3: null,
      chart4: null,
      chartEvent: {
        legendselectchanged: (item) => {
          const currSelectName = item.name;
          // 获取extend配置中的legend项
          const legend = this.chartExtend.legend;
          // 所有图例的选中状态表
          const selectedObj = item.selected;
          // 获取选中的值
          const val = selectedObj[currSelectName];
          const name = String(currSelectName);
          // 获取产品的名称
          const filterName = name.substring(0, name.length - 2);
          console.log(currSelectName, legend, selectedObj, val, name);
          // 从所有图例中过滤出包含这个产品名字的图例
          // const filterNames = Object.keys(selectedObj).filter(element => {
          //   return element.includes(filterName)
          // })
          // 更改同产品的值
          // filterNames.forEach(e => {
          //   selectedObj[e] = val
          // })
          // // 设置到legend中
          // legend.selected = selectedObj
        },
      },
    };
  },
  props: ["showPC"],
  methods: {
    initChart1() {
      this.chart4 = markRaw(
        echarts.init(document.getElementById("chart5-1-1"))
      );
      var table = require("../assets/data/5-1-1.json");
      var city = [];
      var travel = [];
      var gdp = [];
      var population = [];

      table.forEach((item) => {
        city.push(item.name);
        travel.push(item.travel);
        gdp.push(item.gdp);
        population.push(item.population);
      });
      var setLayerOption = ""

      this.chart4.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
        },
        legend: {
          icon: "rect",
          show: true,
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          name: "比重",
          nameLocation: "middle",
          nameTextStyle: {
            padding: [10, 0],
            color: "azure",
          },
          max: 0.2,
          axisLabel: {
            color: "azure",
            formatter: function (value, index) {
              return value * 100 + "%";
            },
          },
        },
        xAxis: {
          type: "category",
          data: city,
          name: "城市",
          nameLocation: "start",
          nameTextStyle: {
            padding: [-10, -20],
            color: "azure",
          },
          inverse: false,
          splitArea: {
            show: true,
            interval: 0,
            areaStyle: {
              color: ["rgba(0,0,0,0.2)", "rgba(0,0,0,0.1)"]
            }
          },
          axisLabel: {
            color: "azure",
          },
        },
        emphasis: {
          focus: "series",
        },
        color: ["#5CF4F8", "#24FFC5", "#F97952"],
        series: [
          {
            name: "各地市出行量比重",
            type: "bar",
            data: travel,
          },
          {
            name: "各地市GDP比重",
            type: "bar",
            data: gdp,
          },
          {
            name: "各地市人口比重",
            type: "bar",
            data: population,
          },
        ],
      });
      this.chart4.on("legendselectchanged", (params) => {
        this.chart4.setOption({
          legend: { selected: { [params.name]: true } },
        });
        var name = params.name;
        var optionName = "";
        if (name == "各地市出行量比重") {
          optionName = "travel";
        } else if (name == "各地市GDP比重") {
          optionName = "gdp";
        } else if (name == "各地市人口比重") {
          optionName = "population";
        }
        setLayerOption = optionName
        table.sort(function (a, b) {
          return b[optionName] - a[optionName];
        });
        var city = [];
        var travel = [];
        var gdp = [];
        var population = [];
        table.forEach((item) => {
          city.push(item.name);
          travel.push(item.travel);
          gdp.push(item.gdp);
          population.push(item.population);
        });
        this.chart4.setOption({
          xAxis: {
            data: city,
          },
          series: [
            {
              name: "各地市出行量比重",
              type: "bar",
              data: travel,
            },
            {
              name: "各地市GDP比重",
              type: "bar",
              data: gdp,
            },
            {
              name: "各地市人口比重",
              type: "bar",
              data: population,
            },
          ],

        });
        // this.$emit("changeTravelLayerOption", setLayerOption)
        // do something
      });
    },
    initChartMap1() {
      this.chart1 = markRaw(
        echarts.init(document.getElementById("chartmap1"))
      );
      var table = require("../assets/data/5-1-1.json");
      var newTable = []
      table.forEach((item) => {
        newTable.push({ "name": item.name, "value": item.travel })
      });

      var shandongJson = require("../assets/data/shandong.json")
      echarts.registerMap('shandong', shandongJson);
      var option = {
        tooltip: {
          trigger: 'item',
          showDelay: 0,
          transitionDuration: 0.2
        },
        visualMap: {
          left: 'right',
          min: 0,
          max: 0.2,
          inRange: {
            color: [
              '#DEEBF7',
              "#6E97BD",
              '#5CF4F8'
            ]
          },
          text: ['高', '低'],
          textStyle: {
            color: "#fff"
          },
          formatter: function (value) {
            return value.toFixed(1); // 范围标签显示内容。
          },
          calculable: true
        },
        series: [
          {
            name: '出行量比重',
            type: 'map',
            roam: false,
            map: 'shandong',
            label: {
              show: true
            },
            emphasis: {
              label: {
                show: true
              }
            },
            data: newTable
          }
        ]
      }
      this.chart1.setOption(option)

    },
    initChartMap2() {
      this.chart2 = markRaw(
        echarts.init(document.getElementById("chartmap2"))
      );
      var table = require("../assets/data/5-1-1.json");
      var newTable = []
      table.forEach((item) => {
        newTable.push({ "name": item.name, "value": item.gdp })
      });

      var shandongJson = require("../assets/data/shandong.json")
      echarts.registerMap('shandong', shandongJson);
      var option = {
        tooltip: {
          trigger: 'item',
          showDelay: 0,
          transitionDuration: 0.2
        },
        visualMap: {
          left: 'right',
          min: 0,
          max: 0.2,
          inRange: {
            color: [
              '#F5FFE8',
              // "#82D900",
              '#24FFC5'
            ]
          },
          text: ['高', '低'],
          textStyle: {
            color: "#fff"
          },
          formatter: function (value) {
            return value.toFixed(1); // 范围标签显示内容。
          },
          calculable: true
        },
        series: [
          {
            name: 'GDP比重',
            type: 'map',
            roam: false,
            map: 'shandong',
            label: {
              show: true
            },
            emphasis: {
              label: {
                show: true
              }
            },
            data: newTable
          }
        ]
      }
      this.chart2.setOption(option)

    },
    initChartMap3() {
      this.chart3 = markRaw(
        echarts.init(document.getElementById("chartmap3"))
      );
      var table = require("../assets/data/5-1-1.json");
      var newTable = []
      table.forEach((item) => {
        newTable.push({ "name": item.name, "value": item.population })
      });

      var shandongJson = require("../assets/data/shandong.json")
      echarts.registerMap('shandong', shandongJson);
      var option = {
        tooltip: {
          trigger: 'item',
          showDelay: 0,
          transitionDuration: 0.2
        },
        visualMap: {
          left: 'right',
          min: 0,
          max: 0.2,
          inRange: {
            color: [
              '#FFF2CA',
              '#F97952'
            ]
          },
          text: ['高', '低'],
          textStyle: {
            color: "#fff"
          },
          formatter: function (value) {
            return value.toFixed(1); // 范围标签显示内容。
          },
          calculable: true
        },
        series: [
          {
            name: '人口比重',
            type: 'map',
            roam: false,
            map: 'shandong',
            label: {
              show: true
            },
            emphasis: {
              label: {
                show: true
              }
            },
            data: newTable
          }
        ]
      }
      this.chart3.setOption(option)

    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-1");
        this.$emit("subTitle", "2-1");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "1-5");
        this.$emit("subTitle", "1-5");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      this.initChartMap1();
      this.initChartMap2();
      this.initChartMap3();
      this.initChart1();

      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2 && !this.chart3) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
    this.chart3.dispose();
    this.chart3 = null;
    this.chart4.dispose();
    this.chart4 = null;
  },
};
</script>


<style scoped>
.pmain {
  height: 650px;
}

#chart5-1-1 {
  width: calc(100%-20px);
  height: 250px;
}

#chartmap1 {
  width: calc(100%-20px);
  height: 350px;
}

#chartmap2 {
  width: calc(100%-20px);
  height: 350px;
}

#chartmap3 {
  width: calc(100%-20px);
  height: 350px;
}

#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}

.fade-leave-active {
  transition: all 0.3s;
}
</style>