<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>山东省高速公路出行分布情况——对外出行</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  山东省高速公路对外出行，<b>江苏连云港</b>方向占比最高，通道包括长深高速和沈海高速；其次是<b>河北沧州</b>方向，
                  通道包括京台高速、京沪高速和长深高速。
                </p>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["沈海高速", "日照至鲁苏界", 0.9],
        ["济广高速", "孔村枢纽至东平湖枢纽", 0.83],
        ["长深高速", "于家庄枢纽至临朐立交", 0.82],
        ["青银高速", "青岛东收费站至夏庄枢纽", 0.81],
        ["长深高速", "无棣至鲁冀界", 0.8],
        ["青银高速", "崔寨枢纽至机场枢纽", 0.79],
        ["济广高速", "定陶枢纽至鲁豫界", 0.77],
        ["德上高速", "聊城西枢纽至李集枢纽", 0.75],
      ],
      toll_rank: [
        ["青岛东主线收费站", "青银高速", 79729],
        ["双埠主线收费站", "青兰高速", 54861],
        ["济南零点收费站", "济广高速", 48447],
        ["济南机场收费站", "济南绕城高速", 43986],
        ["天桥收费站", "济广高速", 42246],
        ["福山收费站", "沈海高速", 40907],
        ["济南北收费站", "济广高速", 40383],
        ["黄岛东主线收费站", "前湾港疏港1号线", 39385],
        ["临沂收费站", "京沪高速", 39022],
        ["泰安东收费站", "青兰高速", 34989],
      ],
    };
  },
  props:["showPC"],

  methods: {
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$emit("subTitle", 3);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$emit("subTitle", 1);
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;

    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    // this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>
div.float {
  position: relative;
  width: 450px;
  /* max-height: 730px; */
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 20px;
  backdrop-filter: blur(10px);
}

.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}
.bl b {
  color: #1dbace;
}
b.green {
  color: #4d9b30;
}
b.yellow {
  color: #f6a512;
}
b.red {
  color: #f03958;
}

#chart11 {
  width: calc(100%-20px);
  height: 150px;
}
#chart12 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
.pmain {
  max-height: 650px;
}
</style>