<template>
  <div style="height: 950px; width: 100%; text-align: center" class="black">
    <div style="height:125px"></div>
    <el-button :disabled="is1" @click="previousSlide" id="pbutton"
      >上一页</el-button
    >
    <el-button :disabled="isMax" @click="nextSlide" id="nbutton"
      >下一页</el-button
    >
    <div style="margin-top: 10px">
      <img
        src="../img/highway2022/slide1.png"
        alt=""
        style="height: 740px; object-fit: cover"
        id="slide"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imgsrc: "../img/highway2022/slide1.png",
      current: 1,
      maxSlide: 17,
      is1: true,
      isMax: false,
    };
  },
  props: ["slideNo"],
  watch: {
    slideNo(val) {
      if (val <= this.maxSlide && val >= 1) {
        this.current = val;
        this.setCurrentSlide();
        if (this.current == this.maxSlide) {
          this.isMax = true;
        }
        if (this.current == 1) {
          this.is1 = true;
        }
        if (this.current != 1 && this.current != this.maxSlide) {
          this.is1 = false;
          this.isMax = false;
        }
      }
    },
    current(val) {
      this.$emit("currentSlide", val);
    },
  },
  methods: {
    refreshSlide() {
      var obj = document.getElementById("slide");
      obj.src = this.imgsrc;
    },
    setCurrentSlide() {
      this.imgsrc = require("../img/highway2022/slide" + this.current + ".png");
      this.refreshSlide();
    },
    nextSlide() {
      if (this.current < this.maxSlide) {
        this.current = this.current + 1;
        this.setCurrentSlide();
      }
      if (this.current == this.maxSlide) {
        this.isMax = true;
      }
      if (this.current != 1 && this.current != this.maxSlide) {
        this.is1 = false;
        this.isMax = false;
      }
    },
    previousSlide() {
      if (this.current > 1) {
        this.current = this.current - 1;
        this.setCurrentSlide();
      }
      if (this.current == 1) {
        this.is1 = true;
      }
      if (this.current != 1 && this.current != this.maxSlide) {
        this.is1 = false;
        this.isMax = false;
      }
    },
    importSlides() {},
  },
  mounted() {
    if (this.slideNo <= this.maxSlide && this.slideNo >= 1) {
      this.current = this.slideNo;
      this.setCurrentSlide();
      if (this.current == this.maxSlide) {
        this.isMax = true;
      }
      if (this.current == 1) {
        this.is1 = true;
      }
      if (this.current != 1 && this.current != this.maxSlide) {
        this.is1 = false;
        this.isMax = false;
      }
    }
    this.setCurrentSlide();
  },
};
</script>

<style scoped>
div.black {
  background-color: #08163d;
}
</style>