<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>高速公路网交通K值情况</h1>
            <div class="next">
              <el-button :disabled="false" @click="previousPage">
                &lt;
              </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <div class="pmain">
                <el-scrollbar style="height: 100%">
                  <div id="chart621"></div>
                </el-scrollbar>
              </div>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
    };
  },
  props: ["showPC"],

  methods: {
    initChart1() {
      var table = require("../assets/data/k_value.json");
      var names = [];
      var data = [];
      table.forEach(function (item) {
        names.push(item.name);
        data.push({ name: item.name, value: item.k_value });
      });
      this.chart1 = echarts.init(document.getElementById("chart621"));
      this.chart1.setOption({
        title: {
          text: "高速公路网交通K值情况",
          left: "center",
          top: "5%",
          textStyle: {
            fontSize: 20,
            color:"azure",
            fontWeight: "bold",
          },
        },
        tooltip: {
          show: true,
          formatter: function (params) {
            return (
              "<b>"+params.name + "</b></br>" + (params.value * 100).toFixed(1) + " %"
            );
          },
        },
        textStyle: {
          fontSize: 10,
        },
        grid: {
          left: "20%",
          right: "10%",
          top: "10%",
          bottom: "20%",
        },
        yAxis: {
          type: "category",
          data: names,
          inverse: true,
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            show: true,
            textStyle: {
              fontSize: 15,
              color:"azure",
            },
          },

        },
        xAxis: {
          type: "value",
          min: 0,
          axisLine: {
            show: true,
            color:"azure",
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            show: true,
            textStyle: {
              fontSize: 15,
              color:"azure",
            },
            formatter: function (value) {
              return (value * 100).toFixed(0) + " %";
            },
          },
          splitLine:{
            show:false,
          },

        },
        series: {
          type: "bar",
          name: "高速公路网交通K值情况",
          barWidth: "35%",
          data: data,
          label: {
            normal: {
              show: true,
              position: "right",
              formatter: function (params) {
                return (params.value * 100).toFixed(0) + " %";
              },
              textStyle: {
                fontSize: 15,
                color:"azure",
              },
            },
          },
          itemStyle:{
            color:"#5CF4F8"
          }
        },
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6-3");
        this.$emit("subTitle", "6-3");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6-1");
        this.$emit("subTitle", "6-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
  },
};
</script>


<style scoped>
.el-scrollbar__wrap {
  overflow-x: hidden;
  overflow-y: hidden;
}
.pmain {
  height: 650px;
}

#chart621 {
  width: calc(100%-10px);
  height: 750px;
}
/* #chart52 {
  width: calc(100%-20px);
  height: 180px;
} */

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>