<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>潍坊市区路网现状交通量分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain"> </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
    };
  },
  props:["showPC"],

  methods: {
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "4");
        this.$emit("subTitle", 4);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2");
        this.$emit("subTitle", 2);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;

    }, 0);
  },
  beforeUnmount() {
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style lang="less" scoped>

.pmain {
  max-height: 650px;
}

#chart1 {
  width: calc(100%-20px);
  height: 200px;
}
#chart2 {
  width: calc(100%-20px);
  height: 200px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.2s;
}
</style>