<template>
  <div class="float" id="box">
    <transition name="fade">
      <div>
        <el-container>
          <el-header class="bl">
            <h1>项目现状交通量-济南收费站</h1>
            <div class="next"></div>
          </el-header>
          <el-main class="bl">
            <div>
              <el-scrollbar v-show="showContent" class="pmain">
                <p style="text-align: center">
                  <img
                    style="width: 95%"
                    src="../../img/济南收费站.jpg"
                    alt=""
                  />
                </p>
                <p>
                  根据2021年高速公路联网收费数据，<span id="sfzName">济南</span
                  >收费站交通量为48603
                  pcu/d，其中客车交通量占78.9%，货车交通量占17.9%。
                </p>
                <div id="chart2-2-1"></div>
                <p style="text-align: center">
                  <img style="width: 95%" src="../../img/jinan.png" alt="" />
                </p>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data: function () {
    return {
      chart1: null,
      showContent: true,
    };
  },
  // props: ["toll"],
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart2-2-1"));
      var option = {
        legend: {
          textStyle: {
            color: "#fff",
          },
          top: "bottom",
        },
        title: {
          text: "济南收费站车型占比",
          textStyle: {
            color:"#fff",
            fontSize: 16,
          },
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: function (params) {
            return (
              params.marker +
              "  " +
              params.name +
              "  " +
              (params.value * 100).toFixed(1) +
              "%"
            );
          },
        },
        series: [
          {
            name: "车型占比",
            type: "pie",
            radius: ["20%", "60%"],
            avoidLabelOverlap: true,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: true,
              textStyle:{
                color:"#fff",
                fontSize:14,
              },
              formatter:function(params){
                return params.name+" "+(params.value*100).toFixed(1)+"%"
              }
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "18",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: true,
            },
            data: [
              { value: 0.779, name: "小客车" },
              { value: 0.01, name: "大客车" },
              { value: 0.115, name: "小货车" },
              { value: 0.039, name: "中货车" },
              { value: 0.025, name: "大货车" },
              { value: 0.033, name: "汽车列车" },
            ],
          },
        ],
      };
      this.chart1.setOption(option);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style["max-height"] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style["max-height"] = "90px";
    },
  },
  mounted() {
    this.initChart1();
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    // this.chart2 = null;
  },
};
</script>

<style scoped>
#chart2-2-1 {
  width: calc(100%-20px);
  height: 400px;
}

#box {
  max-height: 730px;
}
.pmain {
  height: 625px;
}
</style>