<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>烟台港公路集疏运——龙口港区</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  <b>龙口港区</b
                  >是烟台港的重要港区和西进战略的重要桥头堡，位于龙口市西北部，是距离鲁中、鲁西北地区最近的港口。主营非金属矿石、石油、煤炭等大宗散货。2019年港区吞吐量7870万吨，占烟台港的24.6%，居烟台港第二位。
                </p>

                <div id="chart531"></div>

                <div id="chart532"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      huowufenlei: [
        {
          种类: "煤炭及制品",
          吞吐量: 952,
          比重: 0.120939,
        },
        {
          种类: "石油、天然气制品",
          吞吐量: "1,067",
          比重: 0.135615,
        },
        {
          种类: "金属矿石",
          吞吐量: 105,
          比重: 0.013374,
        },
        {
          种类: "钢铁",
          吞吐量: 20,
          比重: 0.002505,
        },
        {
          种类: "矿建材料",
          吞吐量: 174,
          比重: 0.022093,
        },
        {
          种类: "水泥",
          吞吐量: 53,
          比重: 0.006723,
        },
        {
          种类: "木材",
          吞吐量: 260,
          比重: 0.033036,
        },
        {
          种类: "非金属矿石",
          吞吐量: "3,246",
          比重: 0.412392,
        },
        {
          种类: "化学肥料及农药",
          吞吐量: 107,
          比重: 0.01359,
        },
        {
          种类: "盐",
          吞吐量: 1,
          比重: 0.000181,
        },
        {
          种类: "粮食",
          吞吐量: 320,
          比重: 0.040616,
        },
        {
          种类: "化工原料及制品",
          吞吐量: 46,
          比重: 0.005835,
        },
        {
          种类: "有色金属",
          吞吐量: 1,
          比重: 0.000101,
        },
        {
          种类: "农林牧渔业产品",
          吞吐量: 2,
          比重: 0.000315,
        },
        {
          种类: "其它",
          吞吐量: "1,516",
          比重: 0.192685,
        },
      ],
      gangquzhanbi: [
        {
          地区: "芝罘区",
          品类: "地区出行量",
          吞吐量: 174,
          比重: 0.018182,
        },
        {
          地区: "福山区",
          品类: "地区出行量",
          吞吐量: 377,
          比重: 0.039394,
        },
        {
          地区: "牟平区",
          品类: "地区出行量",
          吞吐量: 182,
          比重: 0.019018,
        },
        {
          地区: "莱山区",
          品类: "地区出行量",
          吞吐量: 169,
          比重: 0.017659,
        },
        {
          地区: "龙口市",
          品类: "地区出行量",
          吞吐量: "6,734",
          比重: 0.703657,
        },
        {
          地区: "莱阳市",
          品类: "地区出行量",
          吞吐量: 138,
          比重: 0.01442,
        },
        {
          地区: "莱州市",
          品类: "地区出行量",
          吞吐量: 938,
          比重: 0.098015,
        },
        {
          地区: "蓬莱区",
          品类: "地区出行量",
          吞吐量: 418,
          比重: 0.043678,
        },
        {
          地区: "招远市",
          品类: "地区出行量",
          吞吐量: 300,
          比重: 0.031348,
        },
        {
          地区: "栖霞市",
          品类: "地区出行量",
          吞吐量: 80,
          比重: 0.008359,
        },
        {
          地区: "海阳市",
          品类: "地区出行量",
          吞吐量: 60,
          比重: 0.00627,
        },
      ],
    };
  },
  props: ["showPC"],
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart531"));
      var table = this.huowufenlei;
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        xdata.push(table[i].种类);
        ydata.push(table[i].吞吐量);
      }
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " 万吨"
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 15,
          itemHeight: 10,
          itemGap: 20,
          top: "0px",
          textStyle: {
            fontSize: 12,
            color: "#fff",
          },
        },
        series: [
          {
            name: "吞吐量",
            type: "pie",
            radius: ["30%", "50%"],
            center: ["50%", "60%"],
            avoidLabelOverlap: true,
            label: {
              normal: {
                show: true,
                textStyle: {
                  fontSize: "15",
                  color: "#fff",
                },
              },
            },
            data: (function (xdata, ydata) {
              var arr = [];
              for (var i = 0; i < xdata.length; i++) {
                arr.push({
                  name: xdata[i],
                  value: ydata[i],
                });
              }
              return arr;
            })(xdata, ydata),
          },
        ],
      });
    },
    initChart2() {
      var table = this.gangquzhanbi;
      // 按比重排序
      table.sort(function (a, b) {
        return b.比重 - a.比重;
      });
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        ydata.push(table[i].地区);
        xdata.push(table[i].比重);
      }
      this.chart2 = echarts.init(document.getElementById("chart532"));
      this.chart2.setOption({
        tooltip: {
          show: true,
          trigger: "item",
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              (params.value * 100).toFixed(1) +
              " %"
            );
          },
        },
        grid: {
          x: 75,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        yAxis: [
          {
            type: "category",
            inverse: true,
            data: ydata,
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
          },
        ],
        xAxis: [
          {
            type: "value",
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "港区腹地占比情况",
            type: "bar",
            barWidth: "80%",
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: xdata,
            label: {
              show: true,
              position: "right",
              color: "#fff",
              formatter: function (params) {
                return (params.value * 100).toFixed(1) + "%";
              },
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-4");
        this.$emit("subTitle", "5-3");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-2");
        this.$emit("subTitle", "5-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}

.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}

#chart531 {
  width: calc(100%-20px);
  height: 350px;
}
#chart532 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>