<script>
export default {
  name: "highwaynavmenu",
  data: function () {
    return {
      activeIndex: this.$route.path + "?tn=" + this.$route.query.tn,
      handleSelect: (key, keyPath) => {
        console.log(key, keyPath);
      },
      timer: null,
    };
  },
  methods: {
    refreshIndex() {
      this.activeIndex = this.$route.path + "?tn=" + this.$route.query.tn;
    },
    handleCommand(command) {
      this.$router.push(this.$route.path + "?tn=" + command);
    },
  },
  mounted() {
    this.timer = setInterval(this.refreshIndex, 1000);
  },
  beforeUnmounted() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
};
</script>

