<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>高速路网分车型交通量分布情况</h1>
            <div class="next">
              <el-button :disabled="false" @click="previousPage">
                &lt;
              </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <div class="pmain">
                <p>勾选下列车型</p>
                <span class="inlinecenter">货车</span>
                <div style="text-align:center;">
                  <el-checkbox
                    @change="huo1"
                    :checked="true"
                    label="货1"
                    size="large"
                  />
                  <el-checkbox
                    @change="huo2"
                    :checked="true"
                    label="货2"
                    size="large"
                  />
                  <el-checkbox
                    @change="huo3"
                    :checked="true"
                    label="货3"
                    size="large"
                  />
                  <el-checkbox
                    @change="huo4"
                    :checked="true"
                    label="货4"
                    size="large"
                  />
                  <el-checkbox
                    @change="huo5"
                    :checked="true"
                    label="货5"
                    size="large"
                  />
                  <el-checkbox
                    @change="huo6"
                    :checked="true"
                    label="货6"
                    size="large"
                  />
                </div>
                <span class="inlinecenter">客车</span>
                <div style="text-align:center;">
                  <el-checkbox
                    @change="ke1"
                    :checked="true"
                    label="客1"
                    size="large"
                  />
                  <el-checkbox
                    @change="ke2"
                    :checked="true"
                    label="客2"
                    size="large"
                  />
                  <el-checkbox
                    @change="ke3"
                    :checked="true"
                    label="客3"
                    size="large"
                  />
                  <el-checkbox
                    @change="ke4"
                    :checked="true"
                    label="客4"
                    size="large"
                  />
                </div>
              </div>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      vehicleCheckedGroup: [
        [true, true, true, true, true, true],
        [true, true, true, true],
      ],
    };
  },
  props: ["showPC"],

  methods: {
    emitCheckedGroup() {
      this.$emit("vehicleCheckedGroup", this.vehicleCheckedGroup);
    },
    huo1(checked) {
      this.vehicleCheckedGroup[0][0] = checked;
      this.emitCheckedGroup();
    },
    huo2(checked) {
      this.vehicleCheckedGroup[0][1] = checked;
      this.emitCheckedGroup();
    },
    huo3(checked) {
      this.vehicleCheckedGroup[0][2] = checked;
      this.emitCheckedGroup();
    },
    huo4(checked) {
      this.vehicleCheckedGroup[0][3] = checked;
      this.emitCheckedGroup();
    },
    huo5(checked) {
      this.vehicleCheckedGroup[0][4] = checked;
      this.emitCheckedGroup();
    },
    huo6(checked) {
      this.vehicleCheckedGroup[0][5] = checked;
      this.emitCheckedGroup();
    },
    ke1(checked) {
      this.vehicleCheckedGroup[1][0] = checked;
      this.emitCheckedGroup();
    },
    ke2(checked) {
      this.vehicleCheckedGroup[1][1] = checked;
      this.emitCheckedGroup();
    },
    ke3(checked) {
      this.vehicleCheckedGroup[1][2] = checked;
      this.emitCheckedGroup();
    },
    ke4(checked) {
      this.vehicleCheckedGroup[1][3] = checked;
      this.emitCheckedGroup();
    },

    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$emit("subTitle", 9);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$emit("subTitle", "6-2");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    // this.chart1.dispose();
    this.chart1 = null;
  },
};
</script>


<style scoped>
.el-scrollbar__wrap {
  overflow-x: hidden;
  overflow-y: hidden;
}
.pmain {
  height: 600px;
}

.el-checkbox {
  color: #fff;
  position: relative;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  -webkit-user-select: none;
  user-select: none;
  margin-right: 30px;
  height: 32px;
  --el-checkbox-checked-text-color: #5cf4f8;
  --el-checkbox-checked-bg-color: #5cf4f8;
}
.el-checkbox__input.is-checked + .el-checkbox__label {
  color: #5cf4f8 !important;
}

/* #chart52 {
  width: calc(100%-20px);
  height: 180px;
} */

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}

.pmain span.inlinecenter {
  display: block;
  margin-top: 10px;
  text-align: center;
  color: #fff;
}
</style>