<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>潍坊市出行热度分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
            <p>
              根据数据处理结果，潍坊市区交通出行热点主要集中在<b>青银高速</b>以南，<b
                >宝通街以北</b
              >
            </p>
            <p>
              从出行热点分布情况可以看出，潍城区东西向出行热度明显高于南北向。
            </p>
            
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
    };
  },
  props:["showPC"],
  methods: {
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "3");
        this.$emit("subTitle", '3');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "project");
        this.$emit("subTitle", 'project');
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  max-height: 650px;
}


#chartyt21 {
  width: calc(100%-20px);
  height: 150px;
}
#chartyt22 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>