<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>烟台市公路网交通量分布情况</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  2019年烟台市公路网平均交通量为16065 pcu/d，全省排名第15位。
                </p>
                <div id="chart1"></div>
                <p>烟台市高速公路、国道、省道平均交通量均低于全省平均水平</p>
                <div id="chart2"></div>

                <p>
                  大流量路段（汽车交通量超过2万pcu/d）里程为610公里，占烟台市干线公路网的25.1%，远低于全省平均水平（46.3%）。
                </p>
                <div id="chart3"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  name: "leftPanel",
  props: ["showPC"],
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      chart3: null,
    };
  },
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart1"));
      this.chart1.setOption({
        tooltip: {
          show: true,
          trigger: "item",
        },
        yAxis: {
          type: "value",
          name: "平均交通量(Pcu/d)",
          axisTick: {
            show: true,
          },
          axisLabel:{
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
          },
          nameTextStyle:{
            color: "#fff",
              fontSize: 10,

          }
        },
        grid: {
          x: 55,
          y: 35,
          x2: 65,
          y2: 25,
        },
        xAxis: {
          type: "category",
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
          textStyle: {
            fontSize: 6,
          },
          data: [
            "威海",
            "烟台",
            "菏泽",
            "聊城",
            "德州",
            "青岛",
            "淄博",
            "枣庄",
            "济宁",
            "潍坊",
            "东营",
            "日照",
            "滨州",
            "泰安",
            "临沂",
            "济南",
          ],
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        series: [
          {
            name: "平均交通量",
            type: "bar",
            data: [
              10507,
              { value: 16037, itemStyle: { color: "#F97952" } },
              17124,
              19244,
              19454,
              19565,
              20132,
              22400,
              24821,
              25760,
              26037,
              27668,
              29596,
              30180,
              31473,
              38798,
            ],
            itemStyle: {
              color: "#5CF4F8",
            },
            markLine: {
              itemStyle: {
                color: "#F97952",
              },
              data: [
                {
                  type: "average",
                  name: "平均值",
                },
              ],
            },
          },
        ],
      });
    },
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart2"));
      this.chart2.setOption({
        tooltip: {
          show: true,
          trigger: "item",
        },
        yAxis: {
          type: "value",
          name: "平均交通量(Pcu/d)",
          axisTick: {
            show: true,
          },
          axisLabel:{
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
          },
          nameTextStyle:{
            color: "#fff",
              fontSize: 10,

          }
        },
        grid: {
          x: 55,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        xAxis: {
          type: "category",
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
          textStyle: {
            fontSize: 6,
          },
          data: ["高速公路", "国道", "省道"],
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        label:{
          show:true
        },
        series: [
          {
            name: "烟台市",
            type: "bar",
            itemStyle: {
              color: "#5CF4F8",
            },
            data: [17865, 18153, 13652],
          },
          {
            name: "全省平均值",
            type: "bar",
            itemStyle: {
              color: "#F97952",
            },
            data: [38393, 23662, 15455],
          },
        ],
      });
    },
    initChart3() {
      this.chart3 = echarts.init(document.getElementById("chart3"));
      this.chart3.setOption({
        tooltip: {
          show: true,
          trigger: "item",
        },
        xAxis: {
          type: "value",
          axisTick: {
            show: true,
          },
          axisLabel:{
            formatter:function (value, index) {
              return value * 100 + "%";
            },
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
          },
          max:0.5
        },
        grid: {
          x: 45,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
          data:["高速公路","普通公路"]
        },
        yAxis: {
          type: "category",
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
          textStyle: {
            fontSize: 6,
          },
          data: ["烟台市", "全省"],
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 10,
            },
            //不显示最后一个坐标标签
            showMaxLabel: true,
            interval: 0,
          },
        },
        label: {
          show: true,
          formatter: function (params) {
            return (params.value * 100).toFixed(1) + "%";
          },
        },
        emphasis: {
          focus: "series",
        },
        series: [
          {
            name: "高速公路",
            type: "bar",
            stack: "total",
            data: [0.083, 0.157],
            itemStyle:{
              color:"#5CF4F8"
            }
          },
          {
            name: "普通公路",
            stack: "total",
            type: "bar",
            data: [0.168, 0.306],
            itemStyle:{
              color:"#F97952"
            }
          },
          {
            name: "总值",
            stack: "total",
            type: "bar",
            data: [0.251, 0.463],
            label: {
                show: true,
                position: "insideLeft",
                textStyle: { color: "#fff" },
            },

            itemStyle: {
                color: "rgba(128, 128, 128, 0)",
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "8");
        this.$emit("subTitle", 8);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6");
        this.$emit("subTitle", 6);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.initChart3();

      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    if (!this.chart1 && !this.chart2 && !this.chart3) {
      return;
    }
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;

    this.chart3.dispose();
    this.chart3 = null;
  },
};
</script>


<style lang="less" scoped>


.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}
#chart1 {
  width: calc(100%-20px);
  height: 150px;
}
#chart2 {
  width: calc(100%-20px);
  height: 150px;
}
#chart3 {
  width: calc(100%-20px);
  height: 150px;
}
div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.pmain {
  height: 650px;
}
.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.2s;
}
</style>