<template>
  <div class="float" id="box">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>收费站交通量</h1>
            <div class="next">
              <el-button :disabled="true" @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  截止2021年，山东省高速公路网共计556处收费站，全部收费站平均交通量为<b
                    class="lightblue"
                    >7856pcu/d</b
                  >。
                </p>
                <div id="chart11"></div>

                <div id="chart12"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["沈海高速", "日照至鲁苏界", 0.9],
        ["济广高速", "孔村枢纽至东平湖枢纽", 0.83],
        ["长深高速", "于家庄枢纽至临朐立交", 0.82],
        ["青银高速", "青岛东收费站至夏庄枢纽", 0.81],
        ["长深高速", "无棣至鲁冀界", 0.8],
        ["青银高速", "崔寨枢纽至机场枢纽", 0.79],
        ["济广高速", "定陶枢纽至鲁豫界", 0.77],
        ["德上高速", "聊城西枢纽至李集枢纽", 0.75],
      ],
      toll_rank: [
        ["青岛东主线收费站", "青银高速", 79729],
        ["双埠主线收费站", "青兰高速", 54861],
        ["济南零点收费站", "济广高速", 48447],
        ["济南机场收费站", "济南绕城高速", 43986],
        ["天桥收费站", "济广高速", 42246],
        ["福山收费站", "沈海高速", 40907],
        ["济南北收费站", "济广高速", 40383],
        ["黄岛东主线收费站", "前湾港疏港1号线", 39385],
        ["临沂收费站", "京沪高速", 39022],
        ["泰安东收费站", "青兰高速", 34989],
      ],
    };
  },
  props: ["showPC"],
  methods: {
    initChart1() {
      var toll = "image://" + process.env.BASE_URL + "toll.png";
      console.log(process.env.BASE_URL);
      var maxData = 556;
      this.chart1 = echarts.init(document.getElementById("chart11"));
      this.chart1.setOption({
        tooltip: {
          //鼠标指上时的标线
          trigger: "item",
          formatter: function (params) {
            return (
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " 个</b>"
            );
          },
        },
        xAxis: {
          show: false,
          max: maxData,
          splitLine: { show: false },
          offset: 10,
          axisLine: {
            lineStyle: {
              color: "#999",
            },
          },
          axisLabel: {
            margin: 10,
          },
        },
        yAxis: {
          data: ["交通量<3000", "3000<交通量<5000", "5000<交通量<7000","7000<交通量<10000","交通量>10000",],
          inverse: true,
          axisTick: { show: false },
          axisLine: { show: false },
          axisLabel: {
            margin: 10,
            color: "#999",
            fontSize: 12,
          },
        },
        grid: {
          top: "center",
          height: 150,
          top:20,
          left: 120,
          right: 70,
        },
        series: [
          {
            // current data
            type: "pictorialBar",
            symbol: toll,
            symbolRepeat: "fixed",
            symbolMargin: "5%",
            symbolClip: true,
            symbolSize: 20,
            symbolBoundingData: maxData,
            data: [276,118,51,50,62],
            markLine: {
              symbol: "none",
              label: {
                formatter: "max: {c}",
                position: "start",
              },
              lineStyle: {
                color: "green",
                type: "dotted",
                opacity: 0.2,
                width: 2,
              },
              data: [
                {
                  type: "max",
                },
              ],
            },
            z: 10,
          },
          {
            // full data
            type: "pictorialBar",
            itemStyle: {
              opacity: 0.2,
            },
            label: {
              show: true,
              formatter: function (params) {
                return ((params.value / maxData) * 100).toFixed(1) + " %";
              },
              position: "right",
              offset: [10, 0],
              color: "#fff",
              fontSize: 12,
            },
            animationDuration: 0,
            symbolRepeat: "fixed",
            symbolMargin: "5%",
            symbol: toll,
            symbolSize: 20,
            symbolBoundingData: maxData,
            data: [276,118,51,50,62],
            z: 5,
          },
        ],
      });
    },
    async initChart2() {
      const timeLineData = [1];

      var table = require("../assets/data/sfzquantity.json");
      var city = [];
      var quantity = [];
      var density = [];
      table.forEach((element) => {
        city.push(element.城市);
        quantity.push(element.收费站数量);
        density.push(element.收费站密度);
      });
      let textColor = "#fff";
      let lineColor = "rgba(255,255,255,0.2)";
      let colors = [
        {
          borderColor: "rgba(2, 191, 232,1)",
          start: "rgba(2, 191, 232,0.8)",
          end: "rgba(2, 191, 232,0.1)",
        },
        {
          borderColor: "rgba(1, 231, 152,1)",
          start: "rgba(1, 231, 152,0.1)",
          end: "rgba(1, 231, 152,0.8)",
        },
      ];
      // 基于准备好的dom，初始化echarts实例
      this.chart2 = echarts.init(document.getElementById("chart12"));
      // 指定图表的配置项和数据
      const option = {
        baseOption: {
          // backgroundColor: background,
          timeline: {
            show: false,
            top: 0,
            data: [],
          },
          grid: [
            {
              show: false,
              left: "12%",
              top: "0%",
              bottom: "5%",
              containLabel: true,
              width: "28%",
            },
            {
              show: false,
              left: "52.5%",
              top: "0%",
              bottom: "10%",
              width: "0%",
            },
            {
              show: false,
              right: "12%",
              top: "0%",
              bottom: "5%",
              containLabel: true,
              width: "28%",
            },
          ],
          xAxis: [
            {
              name: "收费站数量（处）",
              nameLocation: "center",
              nameGap: "25",
              nameTextStyle: {
                color: "azure",
              },
              type: "value",
              inverse: true, // 强制设置坐标轴分割间隔
              axisLine: {
                // 坐标轴轴线相关设置
                show: true, // 是否显示坐标轴轴线
                onZero: true, // X 轴或者 Y 轴的轴线是否在另一个轴的 0 刻度上
                lineStyle: {
                  color: "#00A5CA45",
                },
              },
              axisTick: {
                show: true,
              },
              position: "bottom",
              axisLabel: {
                show: true,
                color: "#c6d3d7",
                fontSize: 12,
              },
              splitLine: {
                show: false,
              },
            },
            {
              gridIndex: 1,
              show: false,
            },
            {
              gridIndex: 2,
              name: "收费站密度（处/百公里）",
              nameLocation: "center",
              nameGap: "25",
              nameTextStyle: {
                color: "azure",
              },
              axisLine: {
                show: true, // 是否显示坐标轴轴线
                onZero: true, // X 轴或者 Y 轴的轴线是否在另一个轴的 0 刻度上
                lineStyle: {
                  color: "rgba(1, 231, 152, 0.3)",
                },
              },
              axisTick: {
                show: true,
              },
              position: "bottom",
              axisLabel: {
                show: true,
                color: "#c6d3d7",
                fontSize: 12,
              },
              splitLine: {
                show: false,
              },
            },
          ],
          yAxis: [
            {
              type: "category",
              inverse: true,
              position: "right",
              axisLine: {
                show: true,
                lineStyle: {
                  color: "#00A5CA45",
                },
              },
              axisTick: {
                show: false,
              },
              axisLabel: {
                show: false,
              },
              data: city,
            },
            {
              gridIndex: 1,
              type: "category",
              inverse: true,
              position: "left",
              axisLine: {
                show: false,
              },
              axisTick: {
                show: false,
              },
              axisLabel: {
                show: true,
                // padding:[-5,0,20,0],
                textStyle: {
                  color: "#c1cbcc",
                  fontSize: 12,
                  fontWeight: "bold",
                },
                align: "center",
              },
              data: city.map(function (value) {
                return {
                  value: value,
                  textStyle: {
                    align: "center",
                  },
                };
              }),
            },
            {
              gridIndex: 2,
              type: "category",
              inverse: true,
              position: "left",
              axisLine: {
                show: false,
                lineStyle: {
                  color: lineColor,
                },
              },
              axisTick: {
                show: true,
              },
              axisLabel: {
                show: false,
              },
              data: city,
            },
          ],
          series: [],
        },
        options: [],
      };
      option.baseOption.timeline.data.push(timeLineData[0]);
      option.options.push({
        series: [
          {
            name: "收费站数量（处）",
            type: "bar",
            itemStyle: {
              normal: {
                label: {
                  show: true, //开启显示
                  position: "left", //在上方显示
                  textStyle: {
                    //数值样式
                    color: "#5CF4F8",
                    fontSize: 12,
                    fontWeight: "bold",
                    textBorderWidth: 0.1,
                    textBorderColor: "azure",
                  },
                },
                color: "#5CF4F8",
                borderWidth: 1,
              },
            },
            data: quantity,
            animationEasing: "elasticOut",
          },
          {
            name: "收费站密度（处/百公里）",
            type: "bar",
            // stack: "2",
            // barWidth: 25,
            xAxisIndex: 2,
            yAxisIndex: 2,
            itemStyle: {
              normal: {
                label: {
                  show: true, //开启显示
                  position: "right", //在上方显示
                  formatter: function (params) {
                    return params.value.toFixed(1);
                  },
                  textStyle: {
                    //数值样式
                    color: "#D06641",
                    fontSize: 12,
                    fontWeight: "bold",
                    textBorderWidth: 0.1,
                    textBorderColor: "azure",
                  },
                },
                color: "#D06641",
              },
            },
            data: density,
            animationEasing: "elasticOut",
          },
        ],
      });
      // 使用刚指定的配置项和数据显示图表。
      this.chart2.setOption(option);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path+"?tn="+"2-2");
        this.$emit("subTitle", '2-2');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path+"?tn="+"2-1");
        this.$emit("subTitle", '2-1');
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>
div.float {
  max-height: 730px;
}

.pmain {
  height: 650px;
}


#chart11 {
  width: calc(100%-20px);
  height: 180px;
}
#chart12 {
  width: calc(100%-20px);
  height: 360px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>