<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1>项目实施前后远期交通量对比</h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button :disabled="true" @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  根据交通量预测结果，本项目实施后，2045年周边道路高峰时段平均拥挤度由<b
                    class="lightblue"
                    >0.52</b
                  >降至<b class="lightblue">0.43</b>，降低幅度为<b class="lightblue"
                    >17.3%</b
                  >，可有效缓解周边道路的拥挤程度。
                </p>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
    };
  },
  props: ["showPC"],
  methods: {
   
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5");
        this.$emit("subTitle", 5);
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "6");
        this.$emit("subTitle", 3);
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>

.pmain {
  max-height: 650px;
}

#chart61 {
  width: calc(100%-20px);
  height: 450px;
}
#chart42 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>