<template>
  <div class="float" id="box">
    <transition name="fade">
      <div>
        <el-container>
          <el-header class="bl">
            <h1>概况</h1>
            <div class="next"></div>
          </el-header>
          <el-main class="bl">
            <div>
              <el-scrollbar v-show="showContent" class="pmain">
                
                <p>
                  项目起自<b>零点立交</b>，向西经<b>济南北立交</b>、<b>天桥立交</b>至<b>槐荫枢纽</b>，与京台高速相接，路线全长<b>16.378公里</b>。全线双向四车道高速公路标准，设计速度120km/h，路基宽度24.5~26.0m。

                </p>
                
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center"> 
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      chart1: null,
      showContent: true,
    };
  },
  props: ["showPC"],
  watch: {},
  methods:{
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style["max-height"] = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style["max-height"] = "90px";
    },
  }
};
</script>

<style scoped>

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
</style>